import React from 'react';
import PropTypes from 'prop-types';
import { inject, observer } from 'mobx-react';

import EntityEditor from 'common/components/EntityEditor';

import { primaryActionsFactory, secondaryActionsFactory } from './actions';

class Container extends React.Component {
  static propTypes = {
    orgId: PropTypes.string,
    spec: PropTypes.object,
    specId: PropTypes.string,
  };

  render() {
    const {
      orgId,
      spec,
      specQuery,
      specService,
      specEditorStore,
      routerStore,
      fileSystemStore,
      error,
    } = this.props;

    return (
      <EntityEditor
        orgId={orgId}
        entity={spec}
        entityType="spec"
        entityQuery={specQuery}
        entityService={specService}
        editorStore={specEditorStore}
        routerStore={routerStore}
        fileSystemStore={fileSystemStore}
        primaryActionsFactory={primaryActionsFactory}
        secondaryActionsFactory={secondaryActionsFactory}
        error={error}
      />
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, specService, specEditorStore, fileSystemStore } = stores;
  const { orgId, specId } = props;
  return {
    routerStore,
    specService,
    specEditorStore,
    fileSystemStore,
    specQuery: specService.getQuery(specId, {
      query: {
        orgId,
      },
    }),
  };
})(observer(Container));



