import React from 'react';
import _ from 'lodash';
import { Popup, Button, Dropdown, Message, Icon } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import { safeParse } from 'common/utils/json';
import CodeEditor from 'common/components/CodeEditor';

let EnvironmentSelector = ({ environmentStore }) => {
  const environments = _.get(environmentStore, 'currentEnvironmentData.environments');

  const envOptions = [];
  for (const k in environments) {
    const env = environments[k];
    envOptions.push({
      text: env.name,
      value: env.name,
    });
  }

  return (
    <Dropdown
      options={envOptions}
      value={environmentStore.currentEnvironment.name}
      onChange={(e, option) => {
        environmentStore.currentEnvironmentData.changeCurrentName(option.value);
      }}
      placeholder="choose an environment"
      selection
      button
      basic
    />
  );
};
EnvironmentSelector = inject('environmentStore')(observer(EnvironmentSelector));

const defaultVars = `{
  "apiKey": "123"
}`;

let EnvironmentEditor = ({ environmentStore }) => {
  const environmentData = environmentStore.currentEnvironmentData;
  const environment = environmentStore.currentEnvironment;

  return (
    <div className="EnvironmentEditor">
      <div className="mb-1 c-muted">
        <Message compact info size="small">
          <div>
            Environment variables make it easy to swap out common values.
            They are private and stored on your computer.
          </div>

          <a
            className="mt-05 d-b"
            href="https://help.stoplight.io/scenarios/overview/using-environments-and-variables"
            target="_blank"
            rel="noopener noreferrer"
          >
            <Icon name="book" /> Learn More About Environments
          </a>
        </Message>
      </div>

      <CodeEditor
        name="env-obj"
        mode="json"
        autogrow
        maxLines={20}
        width={500}
        value={
          _.isNil(environment.variables) || _.isEmpty(environment.variables)
            ? defaultVars
            : environment.variables
        }
        onChange={vars => {
          environmentData.updateVariables(vars);
        }}
        noFill
      />
    </div>
  );
};
EnvironmentEditor = inject('environmentStore')(observer(EnvironmentEditor));

class EnvironmentDropdown extends React.Component {
  componentWillMount() {
    this.props.environmentStore.initEnvironments({
      id: this.props.id,
    });
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.id && this.props.id !== nextProps.id) {
      nextProps.environmentStore.initEnvironments({
        id: nextProps.id,
      });
    }
  }

  render() {
    const { environmentStore, inverted } = this.props;

    _.get(environmentStore, 'currentEnvironmentData.lastUpdated'); // make sure this component updates
    const environment = environmentStore.currentEnvironment;
    const currentName = environment.name || 'No Environment';
    let variables = safeParse(environment.variables, environment.variables);

    let countElem;
    if (typeof variables === 'object') {
      variables = _.omitBy(variables, _.isUndefined);
      if (Object.keys(variables).length > 0) {
        countElem = <span className="c-info ml-05">[{Object.keys(variables).length}]</span>;
      }
    } else {
      countElem = (
        <span className="c-negative ml-1">
          <Icon name="cancel" fitted />
        </span>
      );
    }

    const triggerButton = (
      <Button size="tiny" inverted={inverted}>
        {currentName} {countElem}
      </Button>
    );

    return (
      <div id="tutorial-environments-selector">
        <Popup trigger={triggerButton} position="bottom right" wide="very" size="small" hoverable>
          <div className="mb-1">
            <EnvironmentSelector />
          </div>

          <Popup.Content>
            <EnvironmentEditor />
          </Popup.Content>
        </Popup>
      </div>
    );
  }
}

export default inject('environmentStore')(observer(EnvironmentDropdown));



