import React from 'react';
import _ from 'lodash';
import { inject, observer } from 'mobx-react';
import { Table, Dropdown, Message, Label } from 'semantic-ui-react';

import MainLayout from 'app/layouts/MainLayout';
import TableList from 'common/components/TableList';
import NewEntity from 'common/components/NewEntity';
import OrgCollabPopup from 'common/components/OrgCollabPopup';

import { checkPermissions, getRole } from 'common/utils/acl';

import { Link } from 'common/router';

class Component extends React.Component {
  componentWillMount() {
    this.props.orgEntityQuery.load();
  }

  render() {
    const {
      routerStore,
      header,

      orgId,
      org,
      entityData,

      entityService,
      orgEntityQuery,
      fileSystemStore,
    } = this.props;

    const entities = _.get(orgEntityQuery, 'result.data', []).slice();
    const canCreateEntity = checkPermissions(org.acl, 'create', 'org', 'entity');
    const canDeleteEntity = checkPermissions(org.acl, 'delete', 'entity');

    let listElem;
    if (!orgEntityQuery.isLoading && _.isEmpty(entities)) {
      if (canCreateEntity) {
        listElem = (
          <div>
            <NewEntity
              currentType={entityData.type}
              currentOrg={orgId}
              hideOrg
              hideType
              title={`Create Your First ${entityData.name}`}
            />
          </div>
        );
      } else {
        listElem = (
          <Message
            content={`You are a guest of this org, and have not been granted access to any ${entityData.name} yet.`}
            info
          />
        );
      }
    }

    if (!listElem) {
      listElem = (
        <TableList
          canAdd={canCreateEntity}
          scope={`${orgId}-${entityData.type}`}
          items={entities}
          loading={orgEntityQuery.isLoading}
          searchProps={['name', 'slug']}
          actionButton={
            canCreateEntity
              ? {
                  content: `New ${entityData.name}`,
                  onClick: () => {
                    routerStore.history.push({
                      pathname: '/new',
                      search: `?org=${orgId}&type=${entityData.type}`,
                    });
                  },
                }
              : null
          }
          cellFactories={[
            (entity, key) => {
              const activeFile = fileSystemStore.findActiveMapping(entityData.key, entity._id);

              return (
                <Table.Cell key={key}>
                  <Link to={`/${entity.id}`} className="d-b fw-b">
                    <div className="f">
                      <div className="mr-1">{entity.name}</div>
                      {activeFile
                        ? <Label
                            content="Local File"
                            title={`This ${entityData.name} is reading/writing to a file on your computer`}
                            size="mini"
                            basic
                          />
                        : null}
                    </div>
                    <small className="c-muted">{entity.id}</small>
                  </Link>
                </Table.Cell>
              );
            },
            (entity, key) => {
              const activeFile = fileSystemStore.findActiveMapping(entityData.key, entity._id);

              return (
                <Table.Cell key={key} textAlign="right">
                  <div className="f ai-c jc-fe">
                    <div className="pr-1">
                      <OrgCollabPopup
                        className="h-muted cur-p"
                        orgName={org.name}
                        role={_.get(entity, 'acl.org.roles.0')}
                        typeName={entityData.key}
                      />
                    </div>

                    <div className="pr-2">
                      <small className="c-muted">{getRole(entity.acl)}</small>
                    </div>

                    <div>
                      <Dropdown floating button icon="setting" className="icon">
                        <Dropdown.Menu>
                          <Dropdown.Item
                            content="View"
                            onClick={() => {
                              routerStore.history.push({
                                pathname: `/${entity.id}`,
                              });
                            }}
                          />
                          {activeFile
                            ? <Dropdown.Item
                                content="Open local file"
                                onClick={() => {
                                  const opened = window.Electron.shell.openItem(
                                    activeFile.filePath
                                  );
                                  if (!opened) {
                                    alert(`Whoops, we could not open ${activeFile.filePath}.`);
                                  }
                                }}
                              />
                            : null}
                          {canDeleteEntity || checkPermissions(entity.acl, 'delete', 'entity')
                            ? <Dropdown.Item
                                content="Delete"
                                onClick={() => {
                                  let confirmText = `Are you sure you want to remove ${entity.name}?`;
                                  confirmText = `${confirmText}\n\nYou will not be able to recover this ${entityData.name} after it is removed.`;
                                  const r = confirm(confirmText);

                                  if (r) {
                                    entityService.remove(entity.slug, {
                                      query: {
                                        orgId,
                                      },
                                    });
                                  }
                                }}
                              />
                            : null}
                        </Dropdown.Menu>
                      </Dropdown>
                    </div>
                  </div>
                </Table.Cell>
              );
            },
          ]}
        />
      );
    }

    return (
      <MainLayout centered header={header} showDownloads>
        {listElem}
      </MainLayout>
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, fileSystemStore } = stores;

  const orgId = _.get(props, 'route.params.orgId');
  const entityData = _.get(props, 'entityData');
  const entityService = stores[entityData.serviceKey];

  return {
    routerStore,
    fileSystemStore,

    orgId,
    entityData,

    entityService,
    orgEntityQuery: entityService.findQuery({
      query: {
        orgId,
        $sort: { name: 1 },
      },
    }),
  };
})(observer(Component));



