import React from 'react';
import _ from 'lodash';
import { Popup, Icon } from 'semantic-ui-react';

import SortableTreeList from 'common/components/SortableTreeList';

const SplitSidebarSection = props => {
  const { icon, name, actions = [], content, treeData, htmlId, className = '' } = props;

  return (
    <div className={`SplitSidebarSection ${className}`} id={htmlId}>
      {!_.isEmpty(name)
        ? <div className="SplitSidebarSection-header f ai-c">
            {icon
              ? <div className="SplitSidebarSection-icon f-0">
                  <Icon name={icon} />
                </div>
              : null}

            <div className="SplitSidebarSection-name f-0">
              {name}
            </div>

            <div className="SplitSidebarSection-actions f-0 f">
              {actions.map((a, i) => (
                <Popup
                  key={i}
                  trigger={
                    <div className="SplitSidebarSection-action f-0" onClick={a.onClick}>
                      <Icon name={a.icon} />
                    </div>
                  }
                  size="tiny"
                  offset={11}
                  {...a.tip}
                />
              ))}
            </div>
          </div>
        : null}

      {content
        ? <div className="SplitSidebarSection-content">
            {content}
          </div>
        : null}

      {treeData && !_.isEmpty(treeData)
        ? <div className="SplitSidebarSection-content">
            <SortableTreeList id={name} data={treeData} />
          </div>
        : null}

      {typeof treeData !== 'undefined' && _.isEmpty(treeData)
        ? <div className="SplitSidebarSection-none">
            None.
          </div>
        : null}
    </div>
  );
};

const SplitSidebar = props => {
  const { htmlId, className = '', actionElems = [], sections = [] } = props;

  return (
    <div id={htmlId} className={`SplitSidebar ${className}`}>
      {!_.isEmpty(actionElems)
        ? <div className="SplitSidebar-actions f ai-c">
            <div className="f-0">
              {actionElems}
            </div>
          </div>
        : null}

      {sections.map((s, i) => <SplitSidebarSection key={i} {...s} />)}
    </div>
  );
};

export default SplitSidebar;



