import React from 'react';
import _ from 'lodash';
import { Table, Image, Button } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import TableList from 'common/components/TableList';
import InviteMembersModal from 'common/components/InviteMembersModal';
import InviteTeamsModal from 'common/components/InviteTeamsModal';
import OrgCollabSwitcher from 'common/components/OrgCollabSwitcher';
import DropdownMenu from 'common/components/DropdownMenu';

import { identicon } from 'common/utils/images';
import { checkPermissions, hasRole } from 'common/utils/acl';

import { Link } from 'common/router';

class CollaborationPage extends React.Component {
  componentWillMount() {
    this.props.collaborationQuery.load();
  }

  getCollaborators = () => {
    const { collaborationQuery, aclType } = this.props;

    const collaborators = _.get(collaborationQuery, 'result.data', []).slice();

    return _.sortBy(collaborators, collaborator => {
      switch (_.get(collaborator, `acl.${aclType}.roles.0`, 'guest')) {
        case 'owner':
          return 1;
        case 'admin':
          return 2;
        case 'member':
          return 3;
        default:
          return 4;
      }
    });
  };

  handleInviteTeam = selectedIds => {
    const { collaborationService, query } = this.props;

    return collaborationService.create({ teamId: selectedIds }, { query });
  };

  handleInviteMembers = emails => {
    const { collaborationService, query } = this.props;

    return collaborationService.create({ userId: emails }, { query });
  };

  handleChangeOrgCollab = orgRole => {
    const { entityService, entity, orgId } = this.props;

    let orgRoles = [orgRole];

    if (!orgRole || orgRole === 'no') {
      orgRoles = null;
    }

    return entityService.update(
      entity.slug,
      {
        orgRoles,
      },
      {
        query: {
          orgId,
        },
      }
    );
  };

  handleChangeRole = (id, role) => {
    const { collaborationService, query } = this.props;

    collaborationService.update(id, {
      ...query,
      role,
    });
  };

  handleLeaveEntity = () => {
    const r = confirm('Are you sure you want to remove yourself as a collaborator?');

    if (r) {
      const { currentUser, query, orgId, collaborationService, routerStore } = this.props;
      collaborationService
        .remove(currentUser._id, {
          query,
        })
        .then(() => {
          routerStore.history.push({ pathname: `/${orgId}` });
        });
    }
  };

  render() {
    const {
      query,
      orgId,
      entity,
      entityData,
      collaborationKey,
      collaborationType,
      currentUser = {},
      aclType,

      collaborationService,
      collaborationQuery,
      tabs,
    } = this.props;

    const collaborators = this.getCollaborators();
    const isEntityOwner = hasRole(entity.acl, aclType, 'owner');
    const canEditCollaboration = checkPermissions(entity.acl, 'update', 'entity', 'collaboration');

    return (
      <div className="CollaborationPage">
        <TableList
          scope={`${entity.id}-${collaborationType}`}
          items={collaborators}
          loading={collaborationQuery.isLoading}
          noItemsMessage={() => {
            let message = `There are no ${collaborationType} for ${entity.name}`;

            if (canEditCollaboration) {
              message = `${message} To invite a ${_.capitalize(collaborationKey)}, click the invite button to the top right.`;
            }

            return message;
          }}
          topSegment={
            <OrgCollabSwitcher
              orgName={_.capitalize(orgId)}
              role={_.get(entity, 'acl.org.roles.0')}
              typeName={entityData.key}
              canEdit={canEditCollaboration}
              onChange={this.handleChangeOrgCollab}
            />
          }
          tabs={tabs}
          actionButton={() => {
            if (canEditCollaboration) {
              if (collaborationType === 'teams') {
                return (
                  <InviteTeamsModal
                    orgId={orgId}
                    entityTeams={collaborators}
                    error={collaborationService.error}
                    name={_.get(entity, 'name')}
                    handleSave={this.handleInviteTeam}
                  />
                );
              }
              return (
                <InviteMembersModal
                  id={`entity-${entity.id}`}
                  orgId={orgId}
                  name={entity.name}
                  currentMembers={collaborators}
                  error={collaborationService.error}
                  handleSave={this.handleInviteMembers}
                />
              );
            }
          }}
          cellFactories={[
            (collaborator, key) => {
              let elem;

              if (collaborationType === 'teams') {
                elem = (
                  <Link to={`/${collaborator.id}`} className="d-b fw-b">
                    <div className="f">
                      <div className="mr-1">{collaborator.name}</div>
                    </div>
                    <small className="c-muted">{collaborator.id}</small>
                  </Link>
                );
              } else {
                elem = (
                  <div className="f">
                    <div className="f ai-c">
                      <Image
                        src={identicon(collaborator.email || collaborator.name)}
                        size="mini"
                        inline
                      />
                      {collaborator.status === 'pending'
                        ? <div className="ml-1">
                            <div>{collaborator.email}</div>
                            <div>Pending</div>
                          </div>
                        : <div className="ml-1">
                            <div>{collaborator.name}</div>
                            <div>{collaborator.username} - {collaborator.email}</div>
                          </div>}
                    </div>
                  </div>
                );
              }

              return (
                <Table.Cell key={key} width="14">
                  {elem}
                </Table.Cell>
              );
            },
            (collaborator, key) => {
              let roleElem;
              const role = _.get(collaborator, `acl.${aclType}.roles.0`, 'guest');
              const hasOwnerRole = hasRole(collaborator.acl, aclType, 'owner');

              if (canEditCollaboration) {
                let options = [];

                const disabled = currentUser._id === collaborator._id || hasOwnerRole;

                if (collaborationType !== 'teams' && (isEntityOwner || hasOwnerRole)) {
                  options.push({
                    title: 'Owner',
                    value: 'owner',
                    disabled: disabled,
                    onClick: value => {
                      const r = confirm(
                        `Are you sure you want to transfer ownership of the ${entity.name} to ${collaborator.name || collaborator.username}?`
                      );

                      if (r) {
                        this.handleChangeRole(collaborator._id, value);
                      }
                    },
                    description: (
                      <div>
                        <div>
                          Owners can
                          {' '}
                          <span className="fd-u">update</span>
                          {' '}
                          the
                          {' '}
                          {entityData.key}
                          , its connections, and its collaborators.
                        </div>
                        <div>
                          There can only be
                          {' '}
                          <span className="fd-u">one</span>
                          {' '}
                          owner of a {entityData.key}.
                        </div>
                      </div>
                    ),
                  });
                }

                options = options.concat([
                  {
                    title: 'Admin',
                    value: 'admin',
                    disabled: disabled,
                    description: (
                      <span>
                        {collaborationType === 'teams' ? 'Anyone in this team' : 'Admins'} can
                        {' '}
                        <span className="fd-u">update</span>
                        {' '}
                        the
                        {' '}
                        {entityData.key}
                        , its connections, and its collaborators.
                      </span>
                    ),
                  },
                  {
                    title: 'Member',
                    value: 'member',
                    disabled: disabled,
                    description: (
                      <span>
                        {collaborationType === 'teams' ? 'Anyone in this team' : 'Members'} can
                        {' '}
                        <span className="fd-u">update</span>
                        {' '}
                        the
                        {' '}
                        {entityData.key}
                        {' '}
                        and its connections. They can
                        {' '}
                        <span className="fd-u">view</span>
                        {' '}
                        its collaborators.
                      </span>
                    ),
                  },
                  {
                    title: 'Guest',
                    value: 'guest',
                    disabled: disabled,
                    description: (
                      <span>
                        {collaborationType === 'teams' ? 'Anyone in this team' : 'Guests'} can
                        {' '}
                        <span className="fd-u">view</span>
                        {' '}
                        the
                        {' '}
                        {entityData.key}
                        {' '}
                        and its connections. They
                        {' '}
                        <span className="fd-u">cannot view</span>
                        {' '}
                        its collaborators.
                      </span>
                    ),
                  },
                ]);

                if (hasOwnerRole && isEntityOwner) {
                  options.push({
                    divider: true,
                  });

                  const description = `Before leaving an ${entityData.key}, you must give another collaborator the owner role.`;

                  options.push({
                    title: `Leave ${entityData.key}`,
                    description,
                    icon: 'remove',
                    onClick: () => {
                      alert(description);
                    },
                  });
                } else if (!hasOwnerRole) {
                  options.push({
                    divider: true,
                  });

                  let confirmText = 'Are you sure you want to';

                  if (collaborator._id === currentUser._id) {
                    confirmText = `${confirmText} leave this ${entityData.key}?`;
                  } else {
                    confirmText = `${confirmText} remove ${collaborator.name || collaborator.username} from this ${entityData.key}?`;
                  }

                  options.push({
                    title: collaborator._id === currentUser._id
                      ? `Leave ${entityData.key}`
                      : `Remove ${collaborationType === 'teams' ? 'team' : 'collaborator'}`,
                    description: confirmText,
                    icon: 'remove',
                    onClick: () => {
                      const r = confirm(confirmText);

                      if (r) {
                        collaborationService.remove(collaborator._id, { query });
                      }
                    },
                  });
                }

                roleElem = (
                  <DropdownMenu
                    className="icon w-100"
                    floatLeft={true}
                    value={role}
                    onChange={value => this.handleChangeRole(collaborator._id, value)}
                    options={options}
                    dropdownOptions={{
                      button: true,
                    }}
                  />
                );
              } else {
                roleElem = (
                  <div className="f ai-c">
                    <small className="c-muted mr-1">{_.capitalize(role)}</small>
                    {collaborator._id === currentUser._id
                      ? <Button
                          basic
                          negative
                          title={`Leave ${entityData.key}`}
                          icon="sign out"
                          onClick={this.handleLeaveEntity}
                        />
                      : <Button basic className="v-h" />}
                  </div>
                );
              }

              return <Table.Cell key={key} textAlign="center">{roleElem}</Table.Cell>;
            },
          ]}
        />
      </div>
    );
  }
}

export default inject((stores, props) => {
  const { orgId, entityId, entity, entityData, collaborationType, collaborationServiceKey } = props;

  const currentUser = _.get(stores, 'userService.user', {});
  const entityService = stores[entityData.serviceKey];
  const collaborationService = stores[collaborationServiceKey];
  const collaborationKey = _.trimEnd(collaborationType, 's');

  const query = {
    orgId,
    [entityData.idProp]: entityId,
  };

  return {
    query,
    entity,
    entityData,
    collaborationKey,
    collaborationType,
    currentUser,
    aclType: collaborationType === 'teams' ? 'team' : 'user',

    routerStore: stores.routerStore,
    entityService,
    collaborationService,
    collaborationQuery: collaborationService.findQuery({
      query,
    }),
  };
})(observer(CollaborationPage));



