import _ from 'lodash';

/**
 * Supported roles set ordered by access level.
 * @readonly
 * @enum {string}
 */
const ROLE_LIST = ['owner', 'admin', 'member', 'guest'];

export const checkPermissions = (acl = {}, action, resource, modifier) => {
  let permission = `${action}:${resource}`;

  if (modifier) {
    permission = `${permission}:${modifier}`;
  }

  return _.includes(acl.permissions, permission);
};

export const mappedRoles = (acl = {}, type = 'user') => {
  let roles = [];

  if (type == 'all') {
    roles = _.get(acl, ['user', 'roles'], []);

    if (_.isEmpty(roles)) {
      roles = _.map(_.get(acl, ['team', 'roles'], []), role => `team:${role}`);
    }

    if (_.isEmpty(roles)) {
      roles = _.map(_.get(acl, ['org', 'roles'], []), role => `org:${role}`);
    }
  } else {
    roles = _.get(acl, [type, 'roles'], []);
  }

  return _.map(roles, _.capitalize).join(' - ');
};

export const hasRole = (acl = {}, type = 'user', role) => {
  const roles = _.get(acl, [type, 'roles'], []);

  return _.includes(roles, role);
};

/**
 * Sort roles in a ROLE_LIST array order.
 * @param {array} roles - roles to sort.
 * @return {array}
 */
export const sortRoles = roles => {
  return roles.sort((a, b) => (ROLE_LIST.indexOf(a) < ROLE_LIST.indexOf(b) ? -1 : 1));
};

/**
 * Returns highest role from passed roles set.
 * @param {string[]} roles - Roles set.
 * @return {string|undefined} - Highest role name.
 */
export const getHighestRole = roles => {
  if (!roles || !roles.length) {
    return;
  }

  if (roles.length === 1) {
    return roles[0];
  }

  return sortRoles(roles)[0];
};

export const getRole = (acl = {}, type = 'user') => {
  const roles = _.get(acl, [type, 'roles'], []);

  const [explicitRoles, teamRoles, orgRoles] = roles.reduce(
    ([explicit, team, org], role) => {
      if (role.includes('team:')) {
        team.push(role.replace(/team\:/, ''));
      } else if (role.includes('org:')) {
        org.push(role.replace(/org\:/, ''));
      } else {
        explicit.push(role);
      }

      return [explicit, team, org];
    },
    [[], [], []]
  );

  let highestRole = '';
  let level = '';
  if (explicitRoles.length) {
    highestRole = getHighestRole(explicitRoles);
  } else if (teamRoles.length) {
    highestRole = getHighestRole(teamRoles);
    level = 'team';
  } else {
    highestRole = getHighestRole(orgRoles);
    level = 'org';
  }

  return `${level ? _.capitalize(level + ' ') : ''}${_.capitalize(highestRole)}`;
};



