import React from 'react';
import PropTypes from 'prop-types';
import { Icon } from 'semantic-ui-react';
import _ from 'lodash';
import { inject, observer } from 'mobx-react';

class SplitPane extends React.Component {
  static propTypes = {
    id: PropTypes.string.isRequired,
    className: PropTypes.string,
    vertical: PropTypes.bool, // sections will be stacked on top of each other, instead of side by side
    children: PropTypes.node,
    sections: PropTypes.arrayOf(
      PropTypes.shape({
        className: PropTypes.string,
        size: PropTypes.number, // optional initial size of the section
        repaint: PropTypes.bool, // will force a repaint after dragging (useful for CodeEditor)
        render: PropTypes.func.isRequired,
      })
    ),
  };

  state = {
    initialized: false,
    dragging: false,
    repaint: false,
    lastSize: _.get(this.props, 'pane.lastSize'),
    pos: _.get(this.props, 'pane.pos', {}),
  };

  componentDidUpdate(props, state) {
    if (this.state.dragging && !state.dragging) {
      document.addEventListener('mousemove', this.onMouseMove);
      document.addEventListener('mouseup', this.onMouseUp);
    } else if (!this.state.dragging && state.dragging) {
      document.removeEventListener('mousemove', this.onMouseMove);
      document.removeEventListener('mouseup', this.onMouseUp);
    }

    if (this.state.repaint) {
      this.setState({ repaint: false });
    }
  }

  componentWillReceiveProps(nextProps) {
    if (!this.state.initialized) {
      this.setState({
        initialized: true,
        lastSize: _.get(nextProps, 'pane.lastSize'),
        pos: _.get(nextProps, 'pane.pos', {}),
        repaint: true,
      });
    }
  }

  // calculate relative position to the mouse and set dragging=true
  onMouseDown = (e, index) => {
    // only left mouse button
    if (e.button !== 0) return;

    this.setState({
      dragging: true,
      clickTime: new Date().getTime(),
      index,
    });

    e.stopPropagation();
    e.preventDefault();
  };

  onMouseUp = e => {
    // if they are clicking
    const now = new Date().getTime();
    const { pos = {} } = this.state;
    let { lastSize } = this.state;
    if (now - this.state.clickTime < 200) {
      pos[this.state.index] = pos[this.state.index] || {};

      if (pos[this.state.index].size <= 0) {
        pos[this.state.index].size = lastSize < 25 ? 200 : lastSize;
      } else {
        lastSize = pos[this.state.index].size;
        pos[this.state.index].size = 0;
      }
    }

    this.setState({
      dragging: false,
      repaint: true,
      lastSize,
      pos,
    });

    if (this.props.updateSplitPane) {
      this.props.updateSplitPane({
        lastSize,
        pos,
      });
    }

    e.stopPropagation();
    e.preventDefault();
  };

  onMouseMove = e => {
    if (!this.state.dragging) return;

    const pos = {};

    if (this.props.vertical) {
      const docHeight = window.innerHeight;
      pos[this.state.index] = {
        size: docHeight - e.pageY - 7 - (this.props.dragAdjust || 0),
      };
    } else {
      const fixedWidth =
        _.get(document.getElementsByClassName('MasterLayout-sidebar'), '[0].clientWidth', 0) +
        7 -
        (this.props.dragAdjust || 0);

      pos[this.state.index] = {
        size: e.pageX - fixedWidth,
      };
    }

    this.setState({ pos });

    e.stopPropagation();
    e.preventDefault();
  };

  render() {
    const {
      className,
      sections = [],
      vertical = false,
      disableDrag = false,
      children,
    } = this.props;

    const { pos = {}, repaint } = this.state;

    const elems = [];
    const classNames = [];

    if (className) {
      classNames.push(className);
    }

    if (vertical) {
      classNames.push('fd-c is-vertical');
    }

    // first pass build styles
    for (const i in sections) {
      if (!Object.prototype.hasOwnProperty.call(sections, i)) {
        continue;
      }

      const item = sections[i];

      const data = {
        size: undefined,
        style: {},
      };

      if (vertical && pos[i - 1]) {
        data.style[vertical ? 'height' : 'width'] = data.size = item.forceSize || pos[i - 1].size;
      } else if (!vertical && pos[i]) {
        data.style[vertical ? 'height' : 'width'] = data.size = item.forceSize || pos[i].size;
      } else if (item.size) {
        data.style[vertical ? 'height' : 'width'] = data.size = item.forceSize || item.size;
      } else if (item.forceSize) {
        data.style[vertical ? 'height' : 'width'] = data.size = item.forceSize;
      }

      // only render the section if it's visible
      if (typeof data.size === 'undefined' || data.size > 5 || disableDrag) {
        const itemClassNames = [item.className || ''];
        if (item.compact) {
          itemClassNames.push('f-0 is-compact');
        } else if (item.fluid && !data.size) {
          itemClassNames.push('f-1');

          if (!disableDrag) {
            itemClassNames.push('h-100 w-100');
          }
        } else if (disableDrag) {
          itemClassNames.push('f-1');
        } else {
          itemClassNames.push('f-0');
        }

        elems.push(
          <div
            key={i}
            id={item.htmlId}
            className={`SplitPane-section ${itemClassNames.join(' ')}`}
            style={data.style}
          >
            {repaint && item.repaint ? '' : item.render()}
          </div>
        );
      }

      if (sections[parseInt(i) + 1]) {
        elems.push(
          <div
            key={`${i}-expander`}
            className={`SplitPane-expander f f-0 ai-c jc-c ${disableDrag ? 'is-disabled' : ''}`}
            onMouseDown={e => {
              if (!disableDrag) {
                this.onMouseDown(e, i);
              }
            }}
          >
            <Icon name={vertical ? 'ellipsis horizontal' : 'ellipsis vertical'} />
          </div>
        );
      }
    }

    return (
      <div className={`SplitPane f w-100 h-100 ${classNames.join(' ')}`}>
        {elems}
        {children}
      </div>
    );
  }
}

export default inject(({ appStore }, { id, noPersist }) => ({
  ...(!noPersist ? appStore.injectSplitPane(id) : null),
}))(observer(SplitPane));



