import React from 'react';
import PropTypes from 'prop-types';
import { inject, observer } from 'mobx-react';

import EntityEditor from 'common/components/EntityEditor';

import { primaryActionsFactory, secondaryActionsFactory } from './actions';

class Container extends React.Component {
  static propTypes = {
    orgId: PropTypes.string,
    collection: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    collectionId: PropTypes.string,
  };

  render() {
    const {
      orgId,
      collection,
      collectionQuery,
      collectionService,
      collectionEditorStore,
      routerStore,
      fileSystemStore,
      entityData,
      error,
    } = this.props;

    return (
      <EntityEditor
        orgId={orgId}
        entity={collection}
        entityType="collection"
        entityQuery={collectionQuery}
        entityService={collectionService}
        editorStore={collectionEditorStore}
        exportUrl={entityData.buildExportUrl(collection)}
        routerStore={routerStore}
        fileSystemStore={fileSystemStore}
        primaryActionsFactory={primaryActionsFactory}
        secondaryActionsFactory={secondaryActionsFactory}
        error={error}
      />
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, collectionService, collectionEditorStore, fileSystemStore } = stores;
  const { orgId, collectionId } = props;
  return {
    routerStore,
    collectionService,
    collectionEditorStore,
    fileSystemStore,
    collectionQuery: collectionService.getQuery(collectionId, {
      query: {
        orgId,
      },
    }),
  };
})(observer(Container));



