import React from 'react';
import _ from 'lodash';
import { Button, Input, Table, Message, Checkbox, Dropdown, Loader, Menu } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

let SearchBar = ({ currentTableList, ...inputOptions }) => {
  return (
    <Input
      icon="search"
      placeholder="Search..."
      value={currentTableList.searchInput}
      onChange={(e, { value }) => {
        currentTableList.updateSearchInput(value);
      }}
      {...inputOptions}
    />
  );
};

SearchBar = inject(({ tableListStore }) => ({
  currentTableList: tableListStore.currentTableList,
}))(observer(SearchBar));

class Container extends React.Component {
  componentWillMount() {
    this.initTableList(this.props);
    this.updateItems(this.props.items);
  }

  componentWillReceiveProps(nextProps) {
    if (!_.isEqual(this.props.items, nextProps.items)) {
      this.updateItems(nextProps.items);
    }
  }

  initTableList = (props, { force } = {}) => {
    const initProps = {
      scope: props.scope,
      items: props.items,
    };

    if (props.searchProps) {
      initProps.searchProps = props.searchProps;
    }

    props.tableListStore.initTableList(initProps, {
      force,
    });
  };

  updateItems = items => {
    this.props.tableListStore.currentTableList.updateItems(items);
  };

  addSelected = value => {
    this.props.tableListStore.currentTableList.addSelected(value);
  };

  removeSelected = value => {
    this.props.tableListStore.currentTableList.removeSelected(value);
  };

  resetSelected = () => {
    this.props.tableListStore.currentTableList.resetSelected();
  };

  render() {
    const {
      loading,
      items,
      cellFactories,
      actionButton,
      topSegment,
      dropdownItems,
      tableListStore,
      noItemsMessage,
      canAdd,
      tabs = [],
      routerStore,
      children,
    } = this.props;

    const {
      filteredItems,
      debouncedSearchInput,
      selected,
      hasSearchInput,
    } = tableListStore.currentTableList;

    let actionItem = null;
    if (actionButton) {
      if (typeof actionButton === 'function') {
        actionItem = actionButton();
      } else {
        actionItem = <Button primary basic {...actionButton} />;
      }
    }

    let topBar;
    if (_.isEmpty(tabs)) {
      topBar = (
        <div className="f">
          <div className="f-0 mr-1">
            {actionItem}
          </div>
          <div className="f-1 ai-fe ta-r">
            <SearchBar />
          </div>
        </div>
      );
    } else {
      topBar = (
        <Menu>
          {actionItem ? <Menu.Item>{actionItem}</Menu.Item> : null}

          {tabs.map((tab, index) => (
            <Menu.Item
              key={index}
              active={tab.active}
              content={tab.name}
              onClick={() => {
                routerStore.history.push({
                  pathname: tab.href,
                });
              }}
            />
          ))}
          <Menu.Menu position="right">
            <Menu.Item>
              <SearchBar iconPosition="left" transparent />
            </Menu.Item>
          </Menu.Menu>
        </Menu>
      );
    }

    if (loading && _.isEmpty(items)) {
      return (
        <div className="TableList">
          {topSegment}

          {topBar}

          <Loader inline="centered" style={{ marginTop: 50, width: '100%' }} active />
        </div>
      );
    }

    if (_.isEmpty(items)) {
      return (
        <div className="TableList">
          {topSegment}

          {topBar}

          <Message info>
            <Message.Header>
              {noItemsMessage
                ? noItemsMessage()
                : `No items found.${canAdd ? ' Add one using the button to the top right.' : ''}`}
            </Message.Header>
          </Message>
        </div>
      );
    }

    return (
      <div className="TableList">
        {topSegment}

        {topBar}

        {children}

        {_.isEmpty(filteredItems)
          ? <Message warning>
              <Message.Header>
                {hasSearchInput ? `'${debouncedSearchInput}' not found.` : 'Not found.'}
              </Message.Header>
            </Message>
          : <Table striped>
              {dropdownItems
                ? <Table.Header>
                    <Table.Row>
                      <Table.HeaderCell>
                        <Checkbox
                          checked={items.length === selected.length}
                          onChange={(e, { checked }) => {
                            if (checked) {
                              return this.addSelected(_.map(items, '_id'));
                            }

                            return this.resetSelected();
                          }}
                        />
                      </Table.HeaderCell>
                      <Table.HeaderCell colSpan={cellFactories.length}>
                        {selected.length
                          ? <Dropdown
                              floating
                              button
                              labeled
                              text={`${selected.length} selected`}
                              className="icon"
                            >
                              <Dropdown.Menu>
                                {_.map(dropdownItems, ({ onClick, ...extraProps }, i) => (
                                  <Dropdown.Item
                                    key={i}
                                    {...extraProps}
                                    onClick={() => {
                                      onClick ? onClick(selected.slice()) : null;
                                      this.resetSelected();
                                    }}
                                  />
                                ))}
                              </Dropdown.Menu>
                            </Dropdown>
                          : 'Select All'}
                      </Table.HeaderCell>
                    </Table.Row>
                  </Table.Header>
                : null}
              <Table.Body>
                {_.map(filteredItems, (item, i) => (
                  <Table.Row key={i}>
                    {dropdownItems
                      ? <Table.Cell collapsing>
                          <Checkbox
                            checked={_.includes(selected, item._id)}
                            onChange={(e, { checked }) => {
                              if (checked) {
                                return this.addSelected(item._id);
                              }

                              this.removeSelected(item._id);
                            }}
                          />
                        </Table.Cell>
                      : null}
                    {cellFactories
                      ? _.map(cellFactories, (cellFactory, key) => cellFactory(item, key))
                      : null}
                  </Table.Row>
                ))}
              </Table.Body>
            </Table>}
      </div>
    );
  }
}

export default inject(({ tableListStore, routerStore }, { scope }) => ({
  tableListStore,
  routerStore,
}))(observer(Container));



