import React from 'react';
import _ from 'lodash';
import { inject, observer } from 'mobx-react';
import { Button } from 'semantic-ui-react';

import PageHeader from 'common/components/PageHeader';
import EnvironmentDropdown from 'common/components/EnvironmentDropdown';
import TutorialButton from 'common/components/TutorialButton';

import { buildExportUrl } from 'common/utils/entities';
import { getConfigVar } from 'common/utils/config';
import { checkPermissions } from 'common/utils/acl';
import { getAllowedConnections } from 'common/utils/entities';

import { Switch, Route } from 'common/router';

import Collaboration from './collaboration';
import Connections from './connections';
import Discussion from './discussion';
import Settings from './settings';
import Single from './single';

class Container extends React.Component {
  componentWillMount() {
    this.handleLoad(this.props);

    const srn = _.get(this.props, 'entityQuery.data.id');
    if (srn) {
      const name = _.get(this.props, 'entityQuery.data.name');
      this.props.appStore.addSrn({ srn, name });
    }
  }

  componentWillReceiveProps(nextProps) {
    const currentEntityId = _.get(this.props, 'route.params.entityId');
    const entityId = _.get(nextProps, 'route.params.entityId');
    if (entityId && currentEntityId !== entityId) {
      this.handleLoad(nextProps);
    }

    const currentSrn = _.get(this.props, 'entityQuery.data.id');
    const srn = _.get(nextProps, 'entityQuery.data.id');
    if (srn && currentSrn !== srn) {
      const name = _.get(nextProps, 'entityQuery.data.name');
      nextProps.appStore.addSrn({ srn, name });
    }
  }

  handleLoad = ({ entityQuery, routerStore }) => {
    entityQuery.load().catch(err => {
      if (err.code === 404) {
        routerStore.history.replace({
          pathname: '/not-found',
        });
      }
    });
  };

  render() {
    const { route, crumbs = [], entityService, entityQuery, entityData } = this.props;

    const { isLoading } = entityService;
    const entity = _.get(entityQuery, 'data', {});
    const outboundEntityTypes = getAllowedConnections('outbound', entityData.type);

    const newCrumbs = crumbs.concat({
      href: entity.id ? route.url : null,
      children: entity.name || 'New',
    });

    const header = ({ activeTab, extraCrumbs, subTitle }) => (
      <PageHeader
        subTitle={subTitle || entity.summary}
        crumbs={extraCrumbs ? newCrumbs.concat(extraCrumbs) : newCrumbs}
        tabs={[
          {
            name: entityData.name,
            active: activeTab === 'editor',
            href: route.url,
            icon: 'compose',
          },
          {
            name: 'Connections',
            active: activeTab === 'connections',
            href: _.isEmpty(outboundEntityTypes)
              ? `${route.url}/connections/inbound`
              : `${route.url}/connections/outbound`,
            icon: 'fork',
            hide: !checkPermissions(entity.acl, 'read', 'entity', 'connections'),
          },
          {
            name: 'Discussion',
            active: activeTab === 'discussion',
            href: `${route.url}/discussion`,
            icon: 'chat',
            hide: getConfigVar('ENV') !== 'development',
          },
          {
            name: 'Collaboration',
            active: activeTab === 'collaboration',
            href: `${route.url}/collaboration/members`,
            icon: 'users',
            hide: !checkPermissions(entity.acl, 'read', 'entity', 'collaboration'),
          },
          {
            name: 'Settings',
            active: activeTab === 'settings',
            href: `${route.url}/settings`,
            icon: 'cogs',
            hide: !checkPermissions(entity.acl, 'update', 'entity'),
          },
        ]}
        right={
          <div className="f ai-c">
            <div className="f-0">
              <TutorialButton
                id={`${entityData.type}-overview`}
                editorId={entity.id}
                popupProps={{
                  content: `Start ${entityData.name} Tutorial`,
                }}
                inverted
              />
            </div>

            {entityData.type === 'specs' && getConfigVar('ENV') === 'development'
              ? <div className="f-0 ml-05">
                  <Button content="Code Gen" icon="code" size="tiny" inverted />
                </div>
              : null}

            <div className="f-0 ml-05">
              <a href={buildExportUrl({ entityType: entityData.type, entity })} target="_blank">
                <Button content="Export" icon="share" size="tiny" inverted />
              </a>
            </div>

            <div className="f-0 ml-05">
              {entity.id
                ? <EnvironmentDropdown id={`${entityData.type}-${entity.id}`} inverted />
                : null}
            </div>
          </div>
        }
      />
    );

    return (
      <Switch>
        <Route
          path={`${route.path}/collaboration`}
          render={matchProps => (
            <Collaboration
              route={matchProps}
              header={header}
              entity={entity}
              entityData={entityData}
              loading={isLoading}
            />
          )}
        />

        <Route
          path={`${route.path}/connections`}
          render={matchProps => (
            <Connections
              route={matchProps}
              header={header}
              entity={entity}
              entityData={entityData}
              loading={isLoading}
            />
          )}
        />

        <Route
          path={`${route.path}/discussion`}
          render={matchProps => (
            <Discussion
              route={matchProps}
              header={header}
              entity={entity}
              entityData={entityData}
              loading={isLoading}
            />
          )}
        />

        <Route
          path={`${route.path}/settings`}
          render={matchProps => (
            <Settings
              route={matchProps}
              header={header}
              entity={entity}
              entityData={entityData}
              loading={isLoading}
            />
          )}
        />

        <Route
          path={route.path}
          render={matchProps => (
            <Single
              route={matchProps}
              header={header}
              entity={entity}
              entityData={entityData}
              error={entityQuery.error}
              loading={isLoading}
            />
          )}
        />
      </Switch>
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, appStore } = stores;

  const orgId = _.get(props, 'route.params.orgId');
  const entityId = _.get(props, 'route.params.entityId');
  const entityData = _.get(props, 'entityData');

  const entityService = stores[entityData.serviceKey];

  return {
    routerStore,
    appStore,
    entityData,

    entityService,
    entityQuery: entityService.getQuery(entityId, {
      query: {
        orgId,
      },
    }),
  };
})(observer(Container));



