import React from 'react';
import _ from 'lodash';
import { toJS } from 'mobx';
import { inject, observer } from 'mobx-react';
import {
  Button,
  Segment,
  Header,
  Grid,
  Message,
  Form,
  Icon,
  Input,
  Label,
} from 'semantic-ui-react';

import { createURL } from 'common/utils/url';
import { simpleSlugify } from 'common/utils/general';
import { getConfigVar } from 'common/utils/config';

class SettingsPage extends React.Component {
  componentWillMount() {
    this.updateUiEntity(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.updateUiEntity(nextProps);
  }

  updateUiEntity({ ui, entity, updateUi }) {
    if (!ui.entity && !_.isEmpty(entity)) {
      updateUi('set', 'entity', _.clone(toJS(entity)));
    }
  }

  renderPublic() {
    const { orgId, entityId, entityService, entityData, ui, updateUi } = this.props;
    const entity = ui.entity || {};
    const entityName = entityData.name;

    if (!_.isEmpty(entity.acl.public)) {
      return (
        <Grid.Row>
          <Grid.Column width={12}>
            <Header as="h4">Make this {entityName} private</Header>
            <p>
              This {entityName} is public. This means that <strong>anyone </strong> will
              be able to <strong>read</strong> this {entityName}.
            </p>
            <p>
              If public {entityName} is made private only Collaborators of this {entityName} will
              be able to read this {entityName}.
            </p>
          </Grid.Column>
          <Grid.Column textAlign="right" width={4}>
            <Button
              basic
              icon="hide"
              content="Make private"
              disabled={entityService.isSaving}
              onClick={() => {
                const r = confirm(`Are you sure you want to make this ${entityName} private?`);

                if (r) {
                  entityService
                    .update(entityId, { publicRoles: null }, { query: { orgId } })
                    .then(res => updateUi('set', 'entity.acl.public', res.acl.public));
                }
              }}
            />
          </Grid.Column>
        </Grid.Row>
      );
    }

    return (
      <Grid.Row>
        <Grid.Column width={12}>
          <Header as="h4">Make this {entityName} public to the world</Header>
          <p>
            By default, all {entityName}s are private. This means that only
            Collaborators of this {entityName} will be able to read this {entityName}.
          </p>
          <p>
            Making a {entityName} public means that <strong>anyone</strong> will
            be able to <strong>read</strong> this {entityName}.
          </p>
          <p>
            Once you make a {entityName} public, there is no going back.
            Please be certain.
          </p>
        </Grid.Column>
        <Grid.Column textAlign="right" width={4}>
          <Button
            basic
            icon="unhide"
            content="Go public"
            disabled={entityService.isSaving}
            onClick={() => {
              const r = confirm(`Are you sure you want to make this ${entityName} public?`);

              if (r) {
                entityService
                  .update(entityId, { publicRoles: ['guest'] }, { query: { orgId } })
                  .then(res => updateUi('set', 'entity.acl.public', res.acl.public));
              }
            }}
          />
        </Grid.Column>
      </Grid.Row>
    );
  }

  render() {
    const {
      orgId,
      entityId,
      entityService,
      routerStore,
      entityData,
      ui,
      updateUi,
      currentUser,
    } = this.props;

    const entityName = entityData.name;
    const entity = ui.entity || {};
    const original = this.props.entity || {};
    const entityChanged = !_.isEqual(entity, original);

    let subdomainEditor;
    if (entityData.type === 'instances') {
      const u = createURL(getConfigVar('PRISM_DEV_HOST'));

      subdomainEditor = (
        <Form.Field>
          <label>Subdomain</label>
          <Input
            labelPosition="right"
            value={entity.subdomain}
            onChange={(e, { value }) => updateUi('set', 'entity.subdomain', value)}
          >
            <input />
            <Label>.{u.host}</Label>
          </Input>
        </Form.Field>
      );
    }

    return (
      <div className="SettingsPage">
        <Segment.Group>
          <Segment secondary attached="top">
            <Header as="h3">General</Header>
          </Segment>
          <Segment attached="bottom" loading={entityService.isSaving}>
            <Form error={entityService.error} warning={entity.slug !== original.slug}>
              <Message content={(entityService.error || '').toString()} error />

              <Form.Input
                label={`${entityName} Name`}
                value={entity.name}
                onChange={(e, { value }) => updateUi('set', 'entity.name', value)}
              />

              {subdomainEditor}

              <Form.Input
                label="Slug"
                placeholder="Slug"
                value={entity.slug}
                onChange={(e, { value }) => updateUi('set', 'entity.slug', value)}
              />

              <Message warning size="tiny">
                <Icon name="exclamation circle" />
                Note, URL identifier will change to "
                <strong>{orgId}/{entityData.type}/{simpleSlugify(entity.slug)}</strong>
                ". Please be careful when changing this value. Changing this value WILL BREAK any other entities that depend on this
                {' '}
                {entityData.type}
                's export URL.
              </Message>
              <Button
                content="Save"
                disabled={entityService.isSaving || !entityChanged}
                onClick={e => {
                  e.preventDefault();

                  if (!entityChanged) {
                    return;
                  }

                  entityService
                    .save(
                      {
                        _id: original.slug,
                        ..._.pick(entity, ['name', 'slug', 'subdomain']),
                      },
                      {
                        query: {
                          orgId,
                        },
                      }
                    )
                    .then(updatedEntity => {
                      updateUi('set', 'entity', _.clone(updatedEntity));
                      routerStore.history.push({
                        pathname: `/${orgId}/${entityData.type}/${updatedEntity.slug}/settings`,
                      });
                    });
                }}
              />
              <Button
                basic
                content="Reset"
                disabled={!entityChanged}
                onClick={e => {
                  e.preventDefault();
                  updateUi('set', 'entity', _.clone(original));
                }}
              />
            </Form>
          </Segment>
        </Segment.Group>

        <Segment.Group>
          <Segment attached="top" inverted color="red">
            <Header as="h3">Danger Zone</Header>
          </Segment>
          <Segment attached="bottom">
            <Grid divided="vertically">
              {currentUser.slAdmin ? this.renderPublic() : null}

              <Grid.Row columns={2}>
                <Grid.Column>
                  <Header as="h4">Delete this {entityName}</Header>
                  <div>
                    Once you delete a {entityName}, there is no going back.
                    Please be certain.
                  </div>
                </Grid.Column>
                <Grid.Column textAlign="right">
                  <Button
                    basic
                    color="red"
                    icon="trash"
                    content={`Delete this ${entityName}`}
                    onClick={() => {
                      const r = confirm(`Are you sure you want to delete this ${entityName}?`);

                      if (r) {
                        entityService.remove(entityId, { query: { orgId } }).then(() => {
                          routerStore.history.push({
                            pathname: `/${orgId}/${entityData.type}`,
                          });
                        });
                      }
                    }}
                  />
                </Grid.Column>
              </Grid.Row>
            </Grid>
          </Segment>
        </Segment.Group>
      </div>
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, appStore, userService } = stores;

  const { orgId, entityId, entity, entityData } = props;
  const entityService = stores[entityData.serviceKey];

  const query = {
    orgId,
    [entityData.idProp]: entityId,
  };

  return {
    routerStore,
    query,
    entity,
    entityData,
    entityService,
    currentUser: userService.user,
    ...appStore.injectUi(`entity-settings-${entityId}`),
  };
})(observer(SettingsPage));



