import React from 'react';
import PropTypes from 'prop-types';
import { inject, observer } from 'mobx-react';
import _ from 'lodash';
import { Message, Popup, Icon, Button, Modal } from 'semantic-ui-react';

import { pathToHash } from 'common/utils/history';
import Highlight from 'common/components/Highlight';

const ValidationResult = inject(
  'routerStore'
)(({ routerStore, type = '', messages = [], activeMessage, setActiveMessage }) => {
  return (
    <div
      className={`SpecValidationResult f-0 f ai-c ${messages.length ? 'is-active' : ''} is-${type}`}
    >
      <Popup
        hoverable
        content={
          <div className="SpecValidationResult-popup">
            {messages.length === 0
              ? <div className="c-muted">
                  none
                </div>
              : null}

            {messages.map((m, i) => {
              const hashPath = pathToHash({ path: m.path });

              return (
                <div key={i} className="SpecValidationResult-item">
                  <Message error={type === 'error'} warning={type === 'warning'}>
                    <Message.Header>
                      {m.code}
                    </Message.Header>

                    {m.message
                      ? <div>
                          - {m.message}
                        </div>
                      : null}

                    {m.description
                      ? <div>
                          - {m.description}
                        </div>
                      : null}

                    {m.path
                      ? <div
                          className="cur-p is-link"
                          onClick={() => {
                            routerStore.setHash(hashPath);
                          }}
                        >
                          go to {hashPath}
                        </div>
                      : null}

                    {m.inner
                      ? <div className="mt-1">
                          <Button
                            compact
                            size="mini"
                            onClick={() => {
                              setActiveMessage(i);
                            }}
                          >
                            more info
                          </Button>
                        </div>
                      : null}
                  </Message>
                </div>
              );
            })}
          </div>
        }
        size="tiny"
        wide="very"
        position="top right"
        trigger={
          <div>
            <Icon name={type === 'error' ? 'remove' : 'exclamation circle'} />
            {messages.length} {messages.length === 1 ? type : `${type}s`}
          </div>
        }
      />

      {activeMessage
        ? <Modal
            open
            onClose={() => {
              setActiveMessage(null);
            }}
          >
            <Modal.Header>
              {activeMessage.code} at path {JSON.stringify(activeMessage.path)}
            </Modal.Header>
            <Modal.Content>
              {activeMessage.message
                ? <Modal.Description>
                    - {activeMessage.message}
                  </Modal.Description>
                : null}

              {activeMessage.description
                ? <Modal.Description>
                    - {activeMessage.description}
                  </Modal.Description>
                : null}

              <Modal.Description>
                <Highlight mode="json" immediate>
                  {activeMessage.inner}
                </Highlight>
              </Modal.Description>
            </Modal.Content>
          </Modal>
        : null}
    </div>
  );
});

const ValidationResults = ({ results = {}, ui, updateUi }) => {
  if (_.isEmpty(results.errors) && _.isEmpty(results.warnings)) {
    return (
      <div className="SpecValidationResults f h-100">
        <div className="SpecValidationResult f-0 f ai-c">
          <span className="c-positive">
            <Icon name="check" />
          </span>

          Valid
        </div>
      </div>
    );
  }

  return (
    <div className="SpecValidationResults f h-100">
      <ValidationResult
        type="error"
        messages={results.errors}
        activeMessage={_.get(results.errors, ui.activeError)}
        setActiveMessage={i => {
          updateUi('set', ['activeError'], i);
        }}
      />

      <ValidationResult
        type="warning"
        messages={results.warnings}
        activeMessage={_.get(results.errors, ui.activeWarning)}
        setActiveMessage={i => {
          updateUi('set', ['activeWarning'], i);
        }}
      />
    </div>
  );
};

const validationType = PropTypes.arrayOf(
  PropTypes.shape({
    code: PropTypes.string,
    message: PropTypes.string,
    path: PropTypes.array,
  })
);
ValidationResults.propTypes = {
  errors: validationType,
  warnings: validationType,
};

export default inject(({ appStore }, { editor }) => ({
  results: editor.validationResults,
  ...appStore.injectUi('specValidationResults'),
}))(observer(ValidationResults));



