import React from 'react';
import _ from 'lodash';
import { Table, Image, Button, Message } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import ErrorMessage from 'common/components/ErrorMessage';
import InviteMembersModal from 'common/components/InviteMembersModal';
import TableList from 'common/components/TableList';
import DropdownMenu from 'common/components/DropdownMenu';

import { identicon } from 'common/utils/images';
import { checkPermissions, mappedRoles, hasRole } from 'common/utils/acl';

import MainLayout from 'app/layouts/MainLayout';

class Component extends React.Component {
  componentWillMount() {
    this.props.orgMemberQuery.load();
  }

  getMembers = () => {
    const { orgMemberQuery } = this.props;
    const members = _.get(orgMemberQuery, 'result.data', []).slice();

    return _.sortBy(members, m => {
      switch (_.get(m, 'acl.user.roles.0')) {
        case 'owner':
          return 1;
        case 'admin':
          return 2;
        case 'member':
          return 3;
        default:
          return 4;
      }
    });
  };

  handleLeaveOrg = () => {
    const r = confirm('Are you sure you want to leave this organization?');

    if (r) {
      const { currentUser, query, orgMemberService, routerStore } = this.props;
      orgMemberService
        .remove(currentUser._id, {
          query,
        })
        .then(() => {
          routerStore.history.push({ pathname: '/' });
        });
    }
  };

  handleChangeRole = (id, role) => {
    const { orgMemberService, orgMemberQuery, query } = this.props;

    return orgMemberService
      .update(
        id,
        {
          role,
        },
        {
          query,
        }
      )
      .then(() => {
        if (role === 'owner') {
          // We need to reload members data to update controls
          orgMemberQuery.load();
        }
      });
  };

  handleInvites = emails => {
    const { orgMemberService, query } = this.props;
    return orgMemberService.create(
      {
        userId: emails,
      },
      { query }
    );
  };

  render() {
    const {
      org = {},
      query,
      orgId,
      orgMemberService,
      orgMemberQuery,
      header,
      tabs,
      currentUser,
    } = this.props;

    const members = this.getMembers();
    const owner = _.find(members, m => {
      return hasRole(m.acl, 'user', 'owner');
    });
    const isOrgOwner = _.get(owner, '_id') === currentUser._id;
    const canUpdateMembers = checkPermissions(org.acl, 'update', 'org', 'collaboration');

    let listElem;

    if (!orgMemberQuery.isLoading && _.isEmpty(members)) {
      let message = 'There are no Members in this Organization.';

      if (canUpdateMembers) {
        message = `${message} To invite a Member, click the invite button to the top right.`;
      }

      listElem = <Message content={message} info />;
    }

    if (!listElem) {
      listElem = (
        <TableList
          tabs={tabs}
          scope={`${orgId}-members`}
          items={members}
          loading={orgMemberQuery.isLoading}
          searchProps={['name', 'email', 'username', 'roles']}
          actionButton={() => {
            if (canUpdateMembers) {
              return (
                <InviteMembersModal
                  id={`org-${orgId}`}
                  name={org.name}
                  error={orgMemberService.error}
                  clearError={orgMemberService.clearError}
                  handleSave={this.handleInvites}
                />
              );
            }
          }}
          cellFactories={[
            (member, key) => (
              <Table.Cell key={key} width="14">
                <div className="f">
                  <div className="f ai-c">
                    <Image src={identicon(member.email)} size="mini" inline />
                    {member.status === 'pending'
                      ? <div className="ml-1">
                          <div>{member.email}</div>
                          <div>Pending</div>
                        </div>
                      : <div className="ml-1">
                          <div>{member.name}</div>
                          <div>{member.username} - {member.email}</div>
                        </div>}
                  </div>
                </div>
              </Table.Cell>
            ),

            (member, key) => {
              let roleElem;

              const roles = mappedRoles(member.acl);

              if (canUpdateMembers) {
                let options = [];

                const hasOwnerRole = hasRole(member.acl, 'user', 'owner');

                const disabled = currentUser._id === member._id || hasOwnerRole;

                if (isOrgOwner || hasOwnerRole) {
                  options.push({
                    title: 'Owner',
                    value: 'owner',
                    disabled: disabled,
                    onClick: value => {
                      const r = confirm(
                        `Are you sure you want to transfer ownership of the ${org.name} org to ${member.name || member.username}?`
                      );

                      if (r) {
                        this.handleChangeRole(member._id, value);
                      }
                    },
                    description: (
                      <div>
                        <div>
                          Owners can
                          {' '}
                          <span className="fd-u">update</span>
                          {' '}
                          the org and
                          {' '}
                          <span className="fd-u">manage</span>
                          {' '}
                          its members, teams and entities.
                        </div>
                        <div>
                          There can only be
                          {' '}
                          <span className="fd-u">one</span>
                          {' '}
                          owner in an org.
                        </div>
                      </div>
                    ),
                  });
                }

                options = options.concat([
                  {
                    title: 'Admin',
                    value: 'admin',
                    disabled: disabled,
                    description: (
                      <span>
                        Admins can
                        {' '}
                        <span className="fd-u">update</span>
                        {' '}
                        the org and
                        {' '}
                        <span className="fd-u">manage</span>
                        {' '}
                        its members, teams and entities.
                      </span>
                    ),
                  },
                  {
                    title: 'Member',
                    value: 'member',
                    disabled: disabled,
                    description: (
                      <span>
                        Members can
                        {' '}
                        <span className="fd-u">create</span>
                        {' '}
                        entities and teams and
                        {' '}
                        <span className="fd-u">view</span>
                        {' '}
                        other members.
                      </span>
                    ),
                  },
                  {
                    title: 'Guest',
                    value: 'guest',
                    disabled: disabled,
                    description: (
                      <span>
                        Guests
                        {' '}
                        <span className="fd-u">cannot create</span>
                        {' '}
                        entities, members or teams.
                      </span>
                    ),
                  },
                ]);

                if (hasOwnerRole && isOrgOwner) {
                  options.push({
                    divider: true,
                  });

                  const description =
                    'Before leaving an org, you must give another member the owner role.';

                  options.push({
                    title: 'Leave Organization',
                    description,
                    icon: 'remove',
                    onClick: () => {
                      alert(description);
                    },
                  });
                } else if (!hasOwnerRole) {
                  options.push({
                    divider: true,
                  });

                  let confirmText = 'Are you sure you want to';

                  if (member._id === currentUser._id) {
                    confirmText = `${confirmText} leave this organization?`;
                  } else {
                    confirmText = `${confirmText} remove ${member.name || member.username} from this organization?`;
                  }

                  options.push({
                    title: member._id === currentUser._id
                      ? 'Leave organization'
                      : `Remove from organization`,
                    description: confirmText,
                    icon: 'remove',
                    onClick: () => {
                      if (member._id === currentUser._id) {
                        this.handleLeaveOrg();
                      } else {
                        const r = confirm(confirmText);

                        if (r) {
                          orgMemberService.remove(member._id, {
                            query,
                          });
                        }
                      }
                    },
                  });
                }

                roleElem = (
                  <DropdownMenu
                    className="icon w-100"
                    floatLeft={true}
                    value={_.get(member, 'acl.user.roles.0')}
                    onChange={value => this.handleChangeRole(member._id, value)}
                    options={options}
                    dropdownOptions={{
                      button: true,
                    }}
                  />
                );
              } else {
                roleElem = (
                  <div className="f ai-c">
                    <small className="c-muted mr-1">{roles}</small>
                    {member._id === currentUser._id
                      ? <Button
                          basic
                          negative
                          title="Leave Organization"
                          icon="sign out"
                          onClick={this.handleLeaveOrg}
                        />
                      : <Button basic className="v-h" />}
                  </div>
                );
              }

              return <Table.Cell key={key} textAlign="right">{roleElem}</Table.Cell>;
            },
          ]}
        />
      );
    }

    return (
      <MainLayout centered header={header} showDownloads>
        <ErrorMessage
          error={orgMemberService.error}
          onDismiss={orgMemberService.clearError}
          className="mb-2"
        />
        {listElem}
      </MainLayout>
    );
  }
}

export default inject(({ routerStore, userService, orgMemberService }, { route }) => {
  const orgId = _.get(route, 'params.orgId');

  const query = {
    orgId,
  };

  return {
    orgId,
    query,

    routerStore,
    orgMemberService,
    currentUser: userService.user,
    orgMemberQuery: orgMemberService.findQuery({
      query: {
        ...query,
        $sort: { name: 1 },
      },
    }),
  };
})(observer(Component));



