import React from 'react';
import { Message } from 'semantic-ui-react';

const LocalFileModeMessage = () => {
  return (
    <div style={{ maxWidth: 800 }}>
      <Message
        info
        compact
        icon="check"
        header="Enabling Local File Mode"
        content={`To get started, click "Save As" or "Open Existing File" in the left sidebar. When in local file mode, Stoplight will read/write to a file on your computer,
          instead of to Stoplight's servers. You can switch between
          these modes at any time.`}
      />

      <Message
        info
        compact
        className="mt-1"
        icon="eye"
        header="Increased Privacy"
        content={`Data is stored in a file on your computer for maximum privacy and
          security. Of course, you can also save this data back to Stoplight's secure servers if desired.`}
      />

      <Message
        info
        compact
        className="mt-1"
        icon="fork"
        header="Flexible Workflow Integration"
        content={`Because Stoplight interops with your local filesystem, you can continue to use your
          favorite tools. Check your Stoplight managed file into Git, use code annotation libraries
          to automatically generate your specifications, etc.`}
      />
    </div>
  );
};

export default LocalFileModeMessage;



