import _ from 'lodash';
import shortid from 'shortid';

import { safeStringify, safeParse } from '../../json';

export const notes = [
  'General: Flatten before and after properties, and convert them from arrays to objects.',
  'General: Convert scenarios and utilities properties from arrays to objects.',
  'General: Remove id property on all scenarios (id is the object key now).',
];

export const migrateCollection = ({ collection }) => {
  let stringifiedCollection = safeStringify(collection);
  stringifiedCollection = stringifiedCollection.replace(/\"ref\":/g, '"$ref":');

  const parsedCollection = safeParse(stringifiedCollection);

  let phase = {};
  if (parsedCollection.before) {
    phase = migratePhase({ scenarios: parsedCollection.before.scenarios });
  }
  parsedCollection.before = phase || {};

  phase = {};
  if (parsedCollection.after) {
    phase = migratePhase({ scenarios: parsedCollection.after.scenarios });
  }
  parsedCollection.after = phase;

  parsedCollection.scenarios = migratePhase({ scenarios: parsedCollection.scenarios });
  parsedCollection.utilities = migratePhase({ scenarios: parsedCollection.utilities });

  phase = {};
  if (parsedCollection.done) {
    phase = migratePhase({ scenarios: parsedCollection.done.scenarios });
  }
  parsedCollection.done = phase;

  return parsedCollection;
};

const migratePhase = ({ scenarios }) => {
  if (scenarios instanceof Array) {
    return _.keyBy(scenarios, scenario => {
      if (scenario.id) {
        return scenario.id;
      }

      return shortid.generate().substring(0, 5).toLowerCase();
    });
  } else {
    return scenarios || {};
  }
};

export const migrateScenario = ({ scenario }) => {
  delete scenario.id;
  return scenario;
};

export const migrateStep = ({ step }) => {
  return step;
};

export const migrateScript = ({ script }) => {
  return script;
};



