import React from 'react';
import _ from 'lodash';
import { DragDropContext } from 'react-dnd';
import HTML5Backend from 'react-dnd-html5-backend';

import PageHeader from 'common/components/PageHeader';
// import AnonCollectionEditor from 'common/components/AnonCollectionEditor';
// import AnonSpecEditor from 'common/components/AnonSpecEditor';
import UserWelcome from 'common/components/UserWelcome';
import { Route, Switch } from 'common/router';

import New from './new';
import Settings from './settings';
import Org from './org';
import NewOrg from './org/new';
import Orgs from './orgs';

const HeavyRoutes = props => {
  const { route } = props;

  const mainHeader = activeTab => {
    return (
      <PageHeader
        crumbs={[
          {
            children: <UserWelcome />,
          },
        ]}
        tabs={[
          {
            name: 'Organizations',
            active: activeTab === 'organizations',
            href: '/',
            icon: 'sitemap',
          },
          {
            name: 'Settings',
            active: activeTab === 'settings',
            href: '/settings',
            icon: 'cogs',
          },
        ]}
      />
    );
  };

  return (
    <Switch>
      <Route
        path={`${route.path}/new`}
        render={matchProps => (
          <New
            route={matchProps}
            org={_.get(matchProps, 'query.org')}
            type={_.get(matchProps, 'query.type')}
          />
        )}
      />

      <Route
        path={`${route.path}/settings`}
        render={matchProps => <Settings route={matchProps} header={mainHeader('settings')} />}
      />

      {/* <Route
        path={`${route.path}/scenario-editor/:scenarioId?`}
        render={matchProps => (
          <AnonCollectionEditor
            route={matchProps}
            collection={{
              _id: 'anon',
              id: 'anon',
            }}
          />
        )}
      />

      <Route
        path={`${route.path}/spec-editor/:specId?`}
        render={matchProps => (
          <AnonSpecEditor
            route={matchProps}
            spec={{
              _id: 'anon',
              id: 'anon',
            }}
          />
        )}
      /> */}

      <Route
        path={`${route.path}/new-org`}
        render={matchProps => {
          return <NewOrg route={matchProps} />;
        }}
      />

      <Route
        path={`${route.path}/:orgId`}
        render={matchProps => {
          return <Org route={matchProps} />;
        }}
      />

      <Route
        path={route.path}
        render={matchProps => {
          // const route = _.trim(matchProps.location.pathname, '/').split('/')[0];
          // if (userService.loginRequired(route)) {
          //   return (
          //     <Login />
          //   );
          // }

          return <Orgs route={matchProps} header={mainHeader('organizations')} />;
        }}
      />
    </Switch>
  );
};

export default DragDropContext(HTML5Backend)(HeavyRoutes);



