import _ from 'lodash';

import * as migrations10 from './1_0';
import * as migrations11 from './1_1';
import { scenarioPathLocations } from '../';

// import { safeStringify, safeParse } from '../../json';

export const latestVersion = '1.1';

const migrations = {
  '1.0': migrations10,
  '1.1': migrations11,
};

const runMigration = ({ collection, version, migration }) => {
  // console.log(`Migrating to ${version}.`);

  let newCollection = _.cloneDeep(collection);
  newCollection.scenarioVersion = version;

  newCollection = migration.migrateCollection({ collection: newCollection });
  for (const location of scenarioPathLocations) {
    for (const i in newCollection[location]) {
      newCollection[location][i] = migration.migrateScenario({
        scenario: newCollection[location][i],
      });

      for (const x in newCollection[location][i].steps) {
        newCollection[location][i].steps[x] = migration.migrateStep({
          step: newCollection[location][i].steps[x],
        });

        const beforeScript = _.get(newCollection[location][i].steps[x], 'before.script');
        if (beforeScript) {
          _.set(
            newCollection[location][i].steps[x],
            'before.script',
            migration.migrateScript({ script: beforeScript })
          );
        }

        const inputScript = _.get(newCollection[location][i].steps[x], 'input.script');
        if (inputScript) {
          _.set(
            newCollection[location][i].steps[x],
            'input.script',
            migration.migrateScript({ script: inputScript })
          );
        }

        const afterScript = _.get(newCollection[location][i].steps[x], 'after.script');
        if (afterScript) {
          _.set(
            newCollection[location][i].steps[x],
            'after.script',
            migration.migrateScript({ script: afterScript })
          );
        }
      }
    }
  }

  return {
    collection: newCollection,
    notes: migration.notes,
  };
};

export const migrateCollection = ({ collection, withNotes = true }) => {
  if (_.isEmpty(collection)) {
    return { collection };
  }

  const currentVersion = collection.scenarioVersion;
  let startMigrationIndex = 0;
  if (currentVersion) {
    startMigrationIndex = _.indexOf(_.keys(migrations), currentVersion);
    if (startMigrationIndex < 0) {
      startMigrationIndex = 0;
    }
  }

  let migratedCollection = collection;
  const notes = {};

  const migrationKeys = _.keys(migrations);
  for (const i in migrationKeys) {
    if (i > startMigrationIndex) {
      const migrationResults = runMigration({
        collection: migratedCollection,
        version: migrationKeys[i],
        migration: migrations[migrationKeys[i]],
      });

      migratedCollection = migrationResults.collection;
      notes[migrationKeys[i]] = migrationResults.notes;
    }
  }

  const result = { collection: migratedCollection };
  if (withNotes) {
    result.notes = notes;
  }

  return result;
};



