import React from 'react';
import PropTypes from 'prop-types';
import { inject, observer } from 'mobx-react';

import EntityEditor from 'common/components/EntityEditor';

import { primaryActionsFactory, secondaryActionsFactory } from './actions';

class Container extends React.Component {
  static propTypes = {
    orgId: PropTypes.string,
    instance: PropTypes.oneOfType([PropTypes.string, PropTypes.object]),
    instanceId: PropTypes.string,
    path: PropTypes.string,
  };

  render() {
    const {
      orgId,
      instance,
      instanceQuery,
      instanceService,
      instanceEditorStore,
      routerStore,
      fileSystemStore,
      error,
    } = this.props;

    return (
      <EntityEditor
        orgId={orgId}
        entity={instance}
        entityType="instance"
        entityQuery={instanceQuery}
        entityService={instanceService}
        editorStore={instanceEditorStore}
        routerStore={routerStore}
        fileSystemStore={fileSystemStore}
        primaryActionsFactory={primaryActionsFactory}
        secondaryActionsFactory={secondaryActionsFactory}
        error={error}
        disableLocal
      />
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, instanceService, instanceEditorStore, fileSystemStore } = stores;
  const { orgId, instanceId } = props;

  return {
    routerStore,
    instanceService,
    instanceEditorStore,
    fileSystemStore,
    instanceQuery: instanceService.getQuery(instanceId, {
      query: {
        orgId,
      },
    }),
  };
})(observer(Container));



