import React from 'react';
import _ from 'lodash';

import { Route } from 'common/router';
import MainLayout from 'app/layouts/MainLayout';
import SpecEditor from 'common/components/SpecEditor';
import CollectionEditor from 'common/components/CollectionEditor';
import InstanceEditor from 'common/components/InstanceEditor';

export default props => {
  const { route, header, loading, entity, entityData, error } = props;

  const orgId = _.get(route, 'params.orgId');
  const entityId = _.get(route, 'params.entityId');
  let Editor;

  switch (entityData.type) {
    case 'collections':
      Editor = (
        <Route
          path={`${route.path}/:scenarioId?`}
          render={matchProps =>
            <CollectionEditor
              route={matchProps}
              orgId={orgId}
              collection={entity}
              collectionId={entityId}
              entityData={entityData}
              scenarioId={_.get(matchProps, 'params.scenarioId')}
              stepId={_.get(matchProps, 'query.step')}
              error={error}
            />}
        />
      );
      break;
    case 'instances':
      Editor = (
        <InstanceEditor
          orgId={orgId}
          instance={entity}
          instanceId={entityId}
          apiId={_.get(route, 'query.api')}
          ruleId={_.get(route, 'query.rule')}
          stepId={_.get(route, 'query.step')}
          error={error}
        />
      );
      break;
    default:
      Editor = (
        <SpecEditor
          orgId={orgId}
          spec={entity}
          specId={entityId}
          path={_.get(route, 'query.path')}
          error={error}
        />
      );
      break;
  }

  return (
    <MainLayout
      className={`${entityData.type}-module`}
      tutorial={`${entityData.type}-overview`}
      header={header({ activeTab: 'editor' })}
      loading={loading}
    >
      {Editor}
    </MainLayout>
  );
};



