import React from 'react';
import {
  Select,
  Input,
  Header,
  Button,
  Label,
  Icon,
  Checkbox,
  Popup,
  Message,
  Accordion,
  Divider,
  Form,
} from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';
import _ from 'lodash';

import { Link } from 'common/router';
import ErrorMessage from 'common/components/ErrorMessage';
import FormFileButton from 'common/components/FormFileButton';
import OrgCollabSwitcher from 'common/components/OrgCollabSwitcher';
import { simpleSlugify } from 'common/utils/general';
import { getConfigVar } from 'common/utils/config';
import { getEntityTypesWithData, getEntityName, getEntityDescription } from 'common/utils/entities';

class NewEntity extends React.Component {
  componentWillMount() {
    this.props.orgQuery.load();
    this.props.newEntityStore.updateOrg(this.props.currentOrg);
    this.props.newEntityStore.updateType(this.props.currentType);
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.currentOrg !== this.props.currentOrg) {
      nextProps.newEntityStore.updateOrg(nextProps.currentOrg);
    }

    if (nextProps.currentType !== this.props.currentType) {
      nextProps.newEntityStore.updateUserEntities([]);
      nextProps.newEntityStore.updateConnections([]);
      nextProps.newEntityStore.updateType(nextProps.currentType);
    }
  }

  render() {
    const {
      routerStore,
      orgQuery,
      currentType,
      hideOrg,
      readonlyOrg,
      hideType,
      readOnlyType,
      newEntityStore,
      title,
    } = this.props;

    const {
      orgRole,
      orgMembers,
      orgTeams,
      userEntities,
      tabs,
      invites,
      teams,
      connections,
      canSubmit,
    } = newEntityStore;

    const orgs = _.get(orgQuery, 'result.data', []).slice();
    const currentOrg = this.props.currentOrg;

    const currentName = newEntityStore.name;
    const org = _.find(orgs, { id: currentOrg }) || {};
    const currentSrn = `${getConfigVar('SL_APP_HOST')}/${currentOrg}/${currentType}/${currentName
      ? simpleSlugify(currentName)
      : '***'}`;

    const entityTypesWithData = getEntityTypesWithData();
    let currentTypeName = getEntityName(currentType);
    let currentTypeDescription = getEntityDescription(currentType);

    const entityTypeOptions = _.map(entityTypesWithData, e => ({ text: e.name, value: e.type }));

    const orgMemberOptions = _.map(orgMembers, m => ({
      text: _.isEmpty(m.name) ? m.email : `${m.name} - ${m.email}`,
      value: m.email,
    }));

    return (
      <div className="NewEntity">
        <Accordion id={currentTypeName} styled fluid exclusive={false}>
          <div className="p-1">
            <div className="pt-15">
              <Header as="h2" textAlign="center">
                {title || `Create a New ${currentTypeName}`}
                <Header.Subheader>
                  {currentTypeDescription}
                </Header.Subheader>
              </Header>
            </div>

            <Divider />

            <ErrorMessage error={newEntityStore.error} onDismiss={newEntityStore.clearError} />

            <div className="f ai-c pt-1">
              {!hideOrg
                ? <div className="NewEntity-input f-0">
                    <div className="NewEntity-inputName">
                      Organization
                    </div>

                    {readonlyOrg
                      ? <Input value={currentOrg} readOnly />
                      : <Select
                          value={currentOrg}
                          options={(orgs || []).map(o => ({
                            text: o.name,
                            value: o.id,
                          }))}
                          onChange={(e, { value }) => {
                            routerStore.setQueryParams({ org: value }, { replace: true });
                          }}
                        />}
                  </div>
                : null}

              {!hideOrg
                ? <div className="NewEntity-divider f-0">
                    /
                  </div>
                : null}

              {!hideType
                ? <div className="NewEntity-input f-0">
                    <div className="NewEntity-inputName">
                      Type
                    </div>

                    {readOnlyType
                      ? <Input value={currentType} readOnly />
                      : <Select
                          value={currentType}
                          options={entityTypeOptions}
                          onChange={(e, { value }) => {
                            routerStore.setQueryParams({ type: value }, { replace: true });
                          }}
                        />}
                  </div>
                : null}

              {!hideType
                ? <div className="NewEntity-divider f-0">
                    /
                  </div>
                : null}

              <div className="NewEntity-input f-1">
                <div className="NewEntity-inputName">
                  Name
                </div>

                <Input
                  value={currentName}
                  fluid
                  onChange={e => {
                    newEntityStore.updateName(e.target.value);
                  }}
                />
              </div>
            </div>

            <div className="NewEntity-srn">
              <div className="NewEntity-srnName">
                Your {currentTypeName} will be available at:
              </div>

              {currentSrn}
            </div>
          </div>

          <Accordion.Title
            active={tabs.import}
            onClick={() => newEntityStore.toggleTab('import')}
            className={newEntityStore.canImport ? '' : 'd-n'}
          >
            <Icon name="dropdown" /> Import
          </Accordion.Title>
          <Accordion.Content active={tabs.import} className={newEntityStore.canImport ? '' : 'd-n'}>
            <div
              className={`NewEntity-importUrl pt-05 pb-1 ${newEntityStore.isUrlValid
                ? 'c-positive'
                : ''}`}
            >
              <Input
                fluid
                value={newEntityStore.url}
                disabled={newEntityStore.fileLoading}
                labelPosition={newEntityStore.url ? 'right' : 'left'}
                placeholder={
                  currentType === 'collections'
                    ? 'https://raw.githubusercontent.com/myorg/repo/master/collection.json'
                    : 'http://petstore.swagger.io/v2/swagger.json'
                }
                onChange={e => {
                  newEntityStore.updateUrl(e.target.value, currentType);
                }}
              >
                <Label basic>
                  URL to {currentType === 'collections' ? 'Collection or Postman' : 'Swagger'} File
                </Label>

                <input />

                {newEntityStore.url
                  ? <Label basic>
                      <Icon
                        name={
                          newEntityStore.urlLoading
                            ? 'spinner'
                            : newEntityStore.isUrlValid ? 'check' : 'remove'
                        }
                        loading={newEntityStore.urlLoading}
                      />
                    </Label>
                  : null}
              </Input>
            </div>

            <div className="pl-1 pb-1 fw-b">
              OR
            </div>

            <div className="pb-05">
              <FormFileButton
                icon={newEntityStore.fileData ? 'check' : 'file outline'}
                name={
                  newEntityStore.fileName ||
                  `Choose ${currentType === 'collections'
                    ? 'Collection or Postman'
                    : 'Swagger'} File`
                }
                buttonProps={{
                  basic: true,
                  positive: newEntityStore.fileData ? true : false,
                }}
                onChange={e => {
                  newEntityStore.updateFile(_.get(e, 'target.files[0]'), currentType);
                }}
                loading={newEntityStore.fileLoading}
              />

              {newEntityStore.fileData
                ? <Button icon="remove" onClick={newEntityStore.clearFile} basic />
                : null}

              {newEntityStore.fileData
                ? <div className="mt-1">
                    <Popup
                      wide="very"
                      size="small"
                      trigger={
                        <Checkbox
                          disabled={!window.Electron}
                          label={`Enable local file mode ${!window.Electron
                            ? '(desktop app required)'
                            : ''}`}
                          checked={newEntityStore.connectLocalFile}
                          onChange={() => {
                            newEntityStore.toggleConnectLocalFile();
                          }}
                        />
                      }
                    >
                      The Stoplight desktop app can read/write directly from your
                      filesystem. This is perfect if you generate spec from code, want deep git
                      integration,
                      or just want better interop with your favorite tools.
                      <br /><br />
                      You can switch between filemode and saving to Stoplight at any time!
                    </Popup>
                  </div>
                : null}
            </div>
          </Accordion.Content>

          <Accordion.Title
            active={tabs.collaboration}
            onClick={() => newEntityStore.toggleTab('collaboration')}
          >
            <Icon name="dropdown" /> Add Collaborators
          </Accordion.Title>
          <Accordion.Content active={tabs.collaboration}>
            <p>
              Collaborators will be able to access this entity with either read or read/write
              permissions. You can quickly add individual collabrators or teams of collabrators now
              or wait until later.
            </p>

            <OrgCollabSwitcher
              orgName={org.name}
              role={orgRole}
              typeName={currentTypeName}
              onChange={newRole => {
                newEntityStore.updateOrgRole(newRole);
              }}
              basic
            />

            <Form className="pt-2">
              <Form.Dropdown
                label="Individual Collaborators"
                name="invites"
                fluid
                multiple
                search
                selection
                value={_.slice(invites)}
                options={orgMemberOptions}
                onChange={(e, { value }) => {
                  newEntityStore.updateInvites(_.uniq(value));
                }}
              />

              <Form.Dropdown
                label="Teams"
                name="teams"
                fluid
                multiple
                search
                selection
                value={_.slice(teams)}
                options={_.map(orgTeams, m => ({ text: `${m.name} - ${m.id}`, value: m._id }))}
                onChange={(e, { value }) => {
                  newEntityStore.updateTeams(_.uniq(value));
                }}
              />
            </Form>

            <small>Hit <code>Enter</code> to add them to the list.</small>
          </Accordion.Content>

          <Accordion.Title
            active={tabs.connections}
            onClick={() => newEntityStore.toggleTab('connections')}
          >
            <Icon name="dropdown" /> Connect Entities
          </Accordion.Title>
          <Accordion.Content active={tabs.connections}>
            <p>
              Connecting entities grants read access between this entity and its connections and
              allows you to use them together. For example, you might want to connect a Scenario and
              an API Spec so you can quickly build Scenarios from the data in your API Spec.
            </p>

            <Form>
              <Form.Dropdown
                label="Entities to connect"
                placeholder="Enter entity name"
                fluid
                multiple
                search
                selection
                value={_.slice(connections)}
                options={_.map(userEntities, e => ({
                  text: `${e.name} - ${e.id}`,
                  value: `${e.entityType}/${e._id}`,
                  key: e._id,
                }))}
                onChange={(e, { value }) => {
                  newEntityStore.updateConnections(_.uniq(value));
                }}
                onSearchChange={(e, value) => {
                  if (!value.length) {
                    newEntityStore.updateUserEntities([]);
                    newEntityStore.updateConnections([]);
                    return;
                  }

                  setTimeout(() => {
                    newEntityStore.fetchUserEntities(currentType, value, org._id);
                  }, 300);
                }}
              />
            </Form>

            <small>Hit <code>Enter</code> to add them to the list.</small>
          </Accordion.Content>
        </Accordion>

        {newEntityStore.dataSrn
          ? <Message positive>
              <span className="mr-1">
                <Button
                  content="Go To Existing"
                  icon="edit"
                  onClick={() => {
                    newEntityStore.openSrn(newEntityStore.dataSrn);
                  }}
                  secondary
                />
              </span>

              We found a Stoplight ID in this
              file: <b>{newEntityStore.dataSrn}</b>
            </Message>
          : null}

        <div className="NewEntity-actions f ai-c">
          <div className="f-0 mr-2">
            <Button
              primary
              content={`Create ${currentTypeName}`}
              disabled={!canSubmit}
              onClick={() => {
                newEntityStore.submit(entity => {
                  routerStore.history.push({
                    pathname: `/${currentOrg}/${currentType}/${entity.slug}`,
                  });
                });
              }}
              loading={newEntityStore.isSubmitting}
            />
          </div>

          <div className="f-0 mr-2">
            <Link to={`/${currentOrg}`} className="NewEntity-cancel-button">
              Cancel
            </Link>
          </div>

          {newEntityStore.requirement
            ? <div className="c-negative f-0">
                {newEntityStore.requirement}
              </div>
            : null}
        </div>
      </div>
    );
  }
}

export default inject(({ routerStore, orgService, newEntityStore }) => ({
  routerStore,
  newEntityStore,
  orgQuery: orgService.findQuery({
    query: {
      $sort: { name: 1 },
    },
  }),
}))(observer(NewEntity));



