import React from 'react';
import _ from 'lodash';

import MainLayout from 'app/layouts/MainLayout';
import ConnectionsPage from 'common/components/ConnectionsPage';
import { getAllowedConnections } from 'common/utils/entities';

import { Switch, Route } from 'common/router';

const Component = ({ route, header, entity, entityData, loading }) => {
  const orgId = _.get(route, 'params.orgId');
  const entityId = _.get(route, 'params.entityId');

  const inboundEntityTypes = getAllowedConnections('inbound', entityData.type);
  const outboundEntityTypes = getAllowedConnections('outbound', entityData.type);

  const tabs = ({ activeTab, connectionType }) => {
    const entityTypes = getAllowedConnections(connectionType, entityData.type);
    return _.map(entityTypes, entityType => ({
      name: _.capitalize(entityType),
      href: `${route.url}/${connectionType}/${entityType}`,
      active: activeTab === entityType,
    }));
  };

  const topTabs = ({ activeTab }) => {
    const tabs = [];

    if (!_.isEmpty(outboundEntityTypes)) {
      tabs.push({
        name: `This ${entityData.name} Depends On`,
        href: `${route.url}/outbound/${outboundEntityTypes[0]}`,
        active: activeTab === 'outbound',
      });
    }

    if (!_.isEmpty(inboundEntityTypes)) {
      tabs.push({
        name: `Things That Depend On This ${entityData.name}`,
        href: `${route.url}/inbound/${inboundEntityTypes[0]}`,
        active: activeTab === 'inbound',
      });
    }

    return tabs;
  };

  const routes = [];

  for (const connectionType of ['outbound', 'inbound']) {
    const allEntityTypes = _.uniq(inboundEntityTypes.concat(outboundEntityTypes));
    for (const entityType of allEntityTypes) {
      routes.push(
        <Route
          key={entityType + connectionType}
          path={`${route.path}/${connectionType}/${entityType}`}
          render={matchProps => (
            <ConnectionsPage
              connectionServiceKey={entityData.connectionServiceKey}
              orgId={orgId}
              entityId={entityId}
              entityProp={entityData.idProp}
              entity={entity}
              connectedType={entityType}
              connectionType={connectionType}
              tabs={tabs({ activeTab: entityType, connectionType })}
              toptabs={topTabs({ activeTab: connectionType })}
            />
          )}
        />
      );
    }

    let connectedType = outboundEntityTypes[0];
    if (connectionType === 'inbound') {
      connectedType = inboundEntityTypes[0];
    }

    routes.push(
      <Route
        key={connectedType + connectionType}
        path={`${route.path}/${connectionType}`}
        render={matchProps => (
          <ConnectionsPage
            connectionServiceKey={entityData.connectionServiceKey}
            orgId={orgId}
            entityId={entityId}
            entityProp={entityData.idProp}
            entity={entity}
            connectedType={connectedType}
            connectionType={connectionType}
            tabs={tabs({ activeTab: connectedType, connectionType })}
            toptabs={topTabs({ activeTab: connectionType })}
          />
        )}
      />
    );
  }

  return (
    <MainLayout
      className={`${entityData.type}-module`}
      tutorial={`${entityData.type}-overview`}
      header={header({ activeTab: 'connections' })}
      loading={loading}
      centeredContent
    >
      <Switch>
        {routes}
      </Switch>
    </MainLayout>
  );
};

export default Component;



