import React from 'react';
import { Popup, Button } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

// This button starts the tutorial with the given id

const TutorialButton = ({
  id,
  editorId,
  title,
  trigger,
  inverted,
  popupProps = {},
  tutorialStore,
}) => {
  return (
    <span id="tutorial-start-button">
      <Popup
        trigger={
          trigger
            ? trigger
            : <Button
                size="tiny"
                className="icon"
                icon="university"
                inverted={inverted}
                onClick={() => {
                  tutorialStore.handleReset(id, editorId);
                }}
              />
        }
        content="Start Tutorial"
        position="bottom right"
        size="tiny"
        {...popupProps}
      />
    </span>
  );
};

export default inject('tutorialStore')(observer(TutorialButton));



