import React from 'react';
import { Dropdown, Button } from 'semantic-ui-react';

import { getConfigVar } from 'common/utils/config';

export const primaryActionsFactory = props => {
  const { editor, editorDisabled, isSaving, isLoading } = props;

  return (
    <div className="f-0 mr-1">
      {!editorDisabled
        ? <Dropdown
            button
            pointing
            icon="plus"
            className="icon top left mini primary"
            disabled={isSaving || isLoading}
          >
            <Dropdown.Menu>
              <Dropdown.Item icon="plus" text="Add API" onClick={() => editor.addApi()} />

              <Dropdown.Item icon="plus" text="Add Rule" onClick={() => editor.addRule()} />

              <Dropdown.Divider />

              <Dropdown.Item
                icon="undo"
                text="Undo Last Change"
                disabled={!editor.canUndo}
                onClick={editor.undo}
              />

              <Dropdown.Item
                icon={{ name: 'undo', flipped: 'horizontally' }}
                text="Redo Last Change"
                disabled={!editor.canRedo}
                onClick={editor.redo}
              />
            </Dropdown.Menu>
          </Dropdown>
        : null}
    </div>
  );
};

export const secondaryActionsFactory = props => {
  return <div className="f" />;
};



