import React from 'react';
import _ from 'lodash';

import { Switch, Route } from 'common/router';

import Entity from './entity';
import List from './list';

export default props => {
  const { route, entityData, crumbs = [], header, org } = props;

  const entityCrumbs = crumbs.concat({
    href: route.url,
    children: _.capitalize(entityData.type),
  });

  return (
    <div className="w-100 h-100">
      <Switch>
        <Route
          path={`${route.path}/:entityId`}
          render={matchProps => {
            return (
              <Entity
                route={matchProps}
                entityData={entityData}
                crumbs={entityCrumbs}
                header={header}
                org={org}
              />
            );
          }}
        />

        <Route
          path={route.path}
          render={matchProps => (
            <List
              route={matchProps}
              entityData={entityData}
              crumbs={entityCrumbs}
              header={header}
              org={org}
            />
          )}
        />
      </Switch>
    </div>
  );
};



