import React from 'react';
import _ from 'lodash';
import { Button, Form } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import ErrorMessage from 'common/components/ErrorMessage';

class OrgCreate extends React.Component {
  render() {
    const {
      routerStore,
      orgService,
      orgMemberService,
      ui,
      updateUi,
      clearUi,
      userService,
    } = this.props;

    return (
      <Form
        loading={orgService.isSaving || orgMemberService.isSaving}
        onSubmit={e => {
          e.preventDefault();

          orgService
            .create({
              name: ui.name,
            })
            .then(result => {
              if (ui.emails) {
                return orgMemberService
                  .create({
                    orgId: result._id,
                    userId: ui.emails,
                  })
                  .then(() => {
                    routerStore.history.push({ pathname: `/${result.slug}` });
                    clearUi();
                  })
                  .catch(() => {
                    routerStore.history.push({ pathname: `/${result.slug}` });
                    clearUi();
                  });
              }

              routerStore.history.push({ pathname: `/${result.slug}` });
              clearUi();
            });
        }}
      >
        <ErrorMessage className="mb-2" error={orgService.error} />

        <Form.Input
          label="Name"
          name="name"
          placeholder={`For example, '${_.capitalize(_.get(userService, 'user.username'))}' or 'Acme Corp'`}
          value={ui.name}
          onChange={(e, { value }) => {
            updateUi('set', 'name', value);
          }}
        />

        <Form.Dropdown
          label="Add members by email (can also do this later)"
          name="emails"
          placeholder="Emails"
          fluid
          multiple
          search
          selection
          allowAdditions
          value={ui.emails || []}
          options={_.map(ui.emails, e => ({ text: e, value: e }))}
          onChange={(e, { value }) => {
            updateUi('set', 'emails', _.uniq(value));
          }}
        />

        <Button primary type="submit">
          Create Organization
        </Button>
      </Form>
    );
  }
}

export default inject(({ routerStore, userService, appStore, orgService, orgMemberService }) => ({
  routerStore,
  userService,
  ...appStore.injectUi('New-Org'),
  orgService,
  orgMemberService,
}))(observer(OrgCreate));



