import _ from 'lodash';
import recast from 'recast';

import { safeStringify, safeParse } from '../../json';

const builders = recast.types.builders;

export const notes = [
  'General: Scripting is built around getters and setters now.',
  'General: Variable replacement requires brackets now, ie {$.ctx.foo}, not $.ctx.foo',
  'General: Change $.env to $$.env',
  "Scripting: Change $.ctx.foo = 'bar' to $.ctx.set('foo', 'bar')",
  "Scripting: Change $.env.foo = 'bar' to $$.env.set('foo', 'bar')",
  "Scripting: Change input.body.nested.prop = 'bar' to input.body.set('nested.prop', 'bar')",
  "Scripting: Change output.body.foo = 'bar' to output.body.set('foo', 'bar')",
];

export const migrateCollection = ({ collection }) => {
  return collection;
};

export const migrateScenario = ({ scenario }) => {
  return scenario;
};

export const migrateStep = ({ step }) => {
  // replace "$.ctx\env.foo" with "{$.ctx.foo}" in input
  const nonEnclosedDollarRegex = /\"(\$\.(?:env|ctx)+[a-zA-Z\[\].']+)(\\|\")/g;
  let stringifiedStepInput = safeStringify(step.input);
  stringifiedStepInput = _.replace(stringifiedStepInput, nonEnclosedDollarRegex, '"{$1}$2');
  step.input = safeParse(stringifiedStepInput);

  // replace "$.ctx\env.foo" with "{$.ctx.foo}" in assertion expected property
  const nonEnclosedDollarRegexValue = /(\$\.(?:env|ctx)+[a-zA-Z\[\].']+)/g;
  const assertions = _.get(step, 'after.assertions');
  if (!_.isEmpty(assertions)) {
    for (const i in assertions) {
      if (assertions[i].hasOwnProperty('expected') && typeof assertions[i].expected === 'string') {
        // if it's not already enclosed in brackets
        if (!assertions[i].expected.match('{')) {
          _.set(
            step,
            ['after', 'assertions', i, 'expected'],
            _.replace(assertions[i].expected, nonEnclosedDollarRegexValue, '{$1}')
          );
        }
      }
    }
  }

  // replace $.env with $$.env. do this after the "$.ctx\env.foo" to "{$.ctx.foo}" change above!
  const singleDollarEnvRegex = /\$\.env/g;
  let stringifiedStep = JSON.stringify(step);

  // for some reason need three $ here.. weird. It only replaces with 2.
  stringifiedStep = stringifiedStep.replace(singleDollarEnvRegex, '$$$.env');

  // so that it's idempotent, replace $$$ with $$ (if this process is run more than once)
  const tripleDollarEnvRegex = /\$\$\$\.env/g;
  stringifiedStep = stringifiedStep.replace(tripleDollarEnvRegex, '$$$.env');

  step = JSON.parse(stringifiedStep);

  return step;
};

export const migrateScript = ({ script }) => {
  let newScript = script;

  const ast = recast.parse(script);

  recast.visit(ast, {
    visitAssignmentExpression: function(path) {
      const operator = _.get(path, 'value.operator');
      const leftObject = _.get(path, 'value.left.object');
      const leftObjectIdentifier = leftObject.object || {};
      const leftObjectProperty = leftObject.property || {};

      if (
        operator === '=' &&
        _.includes(['$', '$$', 'input', 'output'], leftObjectIdentifier.name) &&
        _.includes(
          ['ctx', 'env', 'body', 'headers', 'method', 'url', 'status'],
          leftObjectProperty.name
        )
      ) {
        const setIdentifier = builders.identifier('set');
        const callee = builders.memberExpression(
          builders.memberExpression(leftObjectIdentifier, leftObjectProperty),
          setIdentifier
        );

        path = builders.callExpression(callee, [
          _.get(path, 'value.left.property.name')
            ? builders.literal(_.get(path, 'value.left.property.name'))
            : _.get(path, 'value.left.property'),
          _.get(path, 'value.right.value')
            ? builders.literal(_.get(path, 'value.right.value'))
            : _.get(path, 'value.right'),
        ]);

        return path;
      } else if (operator === '=' && _.includes(['$', '$$', 'input', 'output'], leftObject.name)) {
        const setIdentifier = builders.identifier('set');
        const callee = builders.memberExpression(
          builders.memberExpression(leftObject, _.get(path, 'value.left.property')),
          setIdentifier
        );

        if (_.get(path, 'value.right.value')) {
          path = builders.callExpression(callee, [
            builders.literal(_.get(path, 'value.right.value')),
          ]);
          return path;
        } else {
          path = builders.callExpression(callee, [_.get(path, 'value.right')]);
        }

        return path;
      } else {
        console.log('NOT HANDLED', leftObject);
        console.log('********************************');
      }

      this.traverse(path);
    },
  });

  newScript = recast.print(ast).code;

  // replace var x = $.ctx.foo with $.ctx.get('foo')
  newScript = _.replace(
    newScript,
    /\= (\$\.ctx|\$\$\.env|input|output)\.([a-zA-Z0-9_\-\.]+)/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `= ${p1}.get('${p2}')`;
    }
  );

  // replace var x = $.ctx['foo'] with $.ctx.get('foo')
  newScript = _.replace(
    newScript,
    /\= (\$\.ctx|\$\$\.env|input|output)\[(['"a-zA-Z0-9_\-\.]+)\]/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `= ${p1}.get(${p2})`;
    }
  );

  // replace $.ctx.foo + 1 with $.ctx.get('foo') + 1
  newScript = _.replace(
    newScript,
    /(\$\.ctx|\$\$\.env|input|output)\.([a-zA-Z0-9_\-\.]+) \+/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `${p1}.get('${p2}') +`;
    }
  );

  // replace $.ctx['foo'] + 1 with $.ctx.get('foo') + 1
  newScript = _.replace(
    newScript,
    /(\$\.ctx|\$\$\.env|input|output)\[(['"a-zA-Z0-9_\-\.]+)\] \+/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `${p1}.get(${p2}) +`;
    }
  );

  // replace 1 + $.ctx.foo with 1 + $.ctx.get('foo')
  newScript = _.replace(
    newScript,
    /\+ (\$\.ctx|\$\$\.env|input|output)\.([a-zA-Z0-9_\-\.]+)/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `+ ${p1}.get('${p2}')`;
    }
  );

  // replace 1 + $.ctx['foo'] with 1 + $.ctx.get('foo')
  newScript = _.replace(
    newScript,
    /\+ (\$\.ctx|\$\$\.env|input|output)\[(['"a-zA-Z0-9_\-\.]+)\]/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `+ ${p1}.get(${p2})`;
    }
  );

  // replace , $.ctx.foo with , $.ctx.get('foo')
  newScript = _.replace(
    newScript,
    /, (\$\.ctx|\$\$\.env|input|output)\.([a-zA-Z0-9_\-\.]+)/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `, ${p1}.get('${p2}')`;
    }
  );

  // replace , $.ctx['foo'] with , $.ctx.get('foo')
  newScript = _.replace(
    newScript,
    /, (\$\.ctx|\$\$\.env|input|output)\[(['"a-zA-Z0-9_\-\.]+)\]/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `, ${p1}.get('${p2}')`;
    }
  );

  // replace $.ctx.foo, with $.ctx.get('foo'),
  newScript = _.replace(
    newScript,
    /(\$\.ctx|\$\$\.env|input|output)\.([a-zA-Z0-9_\-\.]+),/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `${p1}.get('${p2}'),`;
    }
  );

  // replace $.ctx['foo'], with $.ctx.get('foo'),
  newScript = _.replace(
    newScript,
    /(\$\.ctx|\$\$\.env|input|output)\[(['"a-zA-Z0-9_\-\.]+)\],/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `${p1}.get('${p2}'),`;
    }
  );

  // replace $.ctx.foo\n with $.ctx.get('foo')\n
  newScript = _.replace(
    newScript,
    /(\$\.ctx|\$\$\.env|input|output)\.([a-zA-Z0-9_\-\.]+)\n/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `${p1}.get('${p2}')\n`;
    }
  );

  // replace $.ctx['foo']\n with $.ctx.get('foo')\n
  newScript = _.replace(
    newScript,
    /(\$\.ctx|\$\$\.env|input|output)\[(['"a-zA-Z0-9_\-\.]+)\]\n/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `${p1}.get('${p2}')\n`;
    }
  );

  // replace ($.ctx.foo) with ($.ctx.get('foo'))
  newScript = _.replace(
    newScript,
    /\((\$\.ctx|\$\$\.env|input|output)\.([a-zA-Z0-9_\-\.]+)\)/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `(${p1}.get('${p2}'))`;
    }
  );

  // replace ($.ctx['foo']) with ($.ctx.get('foo'))
  newScript = _.replace(
    newScript,
    /\((\$\.ctx|\$\$\.env|input|output)\[(['"a-zA-Z0-9_\-\.]+)\]\)/g,
    (match, p1, p2, offset, string) => {
      if (match.match('.get')) {
        return match;
      }

      return `(${p1}.get('${p2}'))`;
    }
  );

  return newScript;
};



