import React from 'react';
import _ from 'lodash';
import { Table, Dropdown } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import MainLayout from 'app/layouts/MainLayout';

import { checkPermissions, getRole } from 'common/utils/acl';

import TableList from 'common/components/TableList';

import { Link } from 'common/router';

class Component extends React.Component {
  componentWillMount() {
    this.props.teamQuery.load();
  }

  handleSelect = value => {
    this.props.updateUi('set', 'selected', _.uniq(value));
  };

  render() {
    const { orgId, org, route, routerStore, teamService, teamQuery, header, tabs } = this.props;

    const teams = _.sortBy(_.get(teamQuery, 'result.data', []).slice(), 'name');

    return (
      <MainLayout centered header={header} showDownloads>
        <TableList
          tabs={tabs}
          scope={`${orgId}-teams`}
          items={teams}
          loading={teamQuery.isLoading}
          noItemsMessage={() => (
            <div>
              You do not have access to any teams in this organization.
            </div>
          )}
          actionButton={
            checkPermissions(org.acl, 'create', 'team')
              ? {
                  content: 'Create a team',
                  onClick: () => {
                    routerStore.history.push({ pathname: `${route.url}/new/settings` });
                  },
                }
              : null
          }
          cellFactories={[
            (team, key) => (
              <Table.Cell key={key}>
                <Link to={`/${team.id}`} className="d-b fw-b">
                  <div>{team.name}</div>
                  <small className="c-muted">{team.id}</small>
                </Link>
              </Table.Cell>
            ),
            (team, key) => {
              return (
                <Table.Cell key={key} textAlign="right">
                  <div className="f ai-c jc-fe">
                    <div className="mr-1">
                      <small className="c-muted">{getRole(team.acl)}</small>
                    </div>
                    <div>
                      <Dropdown floating button icon="setting" className="icon">
                        <Dropdown.Menu>
                          <Dropdown.Item
                            content="View"
                            onClick={() => {
                              routerStore.history.push({ pathname: `${route.url}/${team.slug}` });
                            }}
                          />
                          {checkPermissions(team.acl, 'update', 'team')
                            ? <Dropdown.Item
                                content="Edit"
                                onClick={() => {
                                  routerStore.history.push({
                                    pathname: `${route.url}/${team.slug}/settings`,
                                  });
                                }}
                              />
                            : null}
                          {checkPermissions(team.acl, 'delete', 'team')
                            ? <Dropdown.Item
                                content="Delete"
                                onClick={() => {
                                  const r = confirm(
                                    `Are you sure you want to remove ${team.name}?`
                                  );

                                  if (r) {
                                    teamService.remove(team._id, {
                                      query: {
                                        orgId,
                                      },
                                    });
                                  }
                                }}
                              />
                            : null}
                        </Dropdown.Menu>
                      </Dropdown>
                    </div>
                  </div>
                </Table.Cell>
              );
            },
          ]}
        />
      </MainLayout>
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, teamService, teamMemberService, userService } = stores;
  const { route } = props;
  const orgId = _.get(route, 'params.orgId');

  return {
    orgId,
    routerStore,
    currentUser: userService.user,

    teamMemberService,
    teamService,
    teamQuery: teamService.findQuery({
      query: {
        orgId: orgId,
      },
    }),
  };
})(observer(Component));



