import React from 'react';
import { observer } from 'mobx-react';

import SplitSidebar from 'common/components/SplitSidebar';

const SpecSidebar = props => {
  const { editor } = props;

  return <SplitSidebar htmlId="tutorial-sidebar" sections={editor.sidebarSections} />;
};

export default observer(SpecSidebar);



