import React from 'react';

import MainLayout from 'app/layouts/MainLayout';
import NewEntity from 'common/components/NewEntity';

export default function({ org, type }) {
  return (
    <MainLayout centered>
      <NewEntity currentOrg={org} currentType={type} />
    </MainLayout>
  );
}



