import React from 'react';
import _ from 'lodash';
import { inject, observer } from 'mobx-react';

import { Route, Switch } from 'common/router';
import PageHeader from 'common/components/PageHeader';
import { getEntityTypesWithData } from 'common/utils/entities';
import { checkPermissions } from 'common/utils/acl';

import Members from './members';
import Entity from './entity';
import Settings from './settings';

class Container extends React.Component {
  componentWillMount() {
    const { routerStore, teamQuery } = this.props;

    teamQuery.load().catch(err => {
      if (err.code === 404) {
        routerStore.history.replace({
          pathname: `/not-found`,
        });
      }
    });
  }

  render() {
    const { route, teamQuery, crumbs } = this.props;

    const team = _.get(teamQuery, 'data', {});

    const canReadMembers = checkPermissions(team.acl, 'read', 'team', 'collaboration');

    const teamCrumbs = crumbs.concat({
      href: route.url,
      children: team.name || 'New',
    });

    const entityTypesWithData = getEntityTypesWithData();

    const teamHeader = ({ subTitle, activeTab, extraCrumbs, hideTabs }) => {
      const entityTabs = _.map(entityTypesWithData, entityData => {
        return {
          name: `${entityData.name}s`,
          active: activeTab === entityData.type,
          href: `${route.url}/${entityData.type}`,
          icon: entityData.icon,
        };
      });

      const tabs = [
        {
          name: 'Members',
          active: activeTab === 'Members',
          href: route.url,
          icon: 'users',
          hide: !canReadMembers,
        },
        ...entityTabs,
        {
          name: 'Settings',
          active: activeTab === 'Settings',
          href: `${route.url}/settings`,
          icon: 'setting',
          hide: !checkPermissions(team.acl, 'update', 'team'),
        },
      ];

      return (
        <PageHeader
          subTitle={subTitle}
          crumbs={extraCrumbs ? teamCrumbs.concat(extraCrumbs) : teamCrumbs}
          tabs={hideTabs ? [] : tabs}
        />
      );
    };

    const entityRoutes = _.map(entityTypesWithData, entityData => {
      return (
        <Route
          key={entityData.type}
          path={`${route.path}/${entityData.type}`}
          render={matchProps => (
            <Entity route={matchProps} header={teamHeader} team={team} entityData={entityData} />
          )}
        />
      );
    });

    return (
      <Switch>
        <Route
          path={`${route.path}/settings`}
          render={matchProps => <Settings route={matchProps} header={teamHeader} team={team} />}
        />

        {entityRoutes}

        <Route
          path={route.path}
          render={matchProps => {
            if (!canReadMembers) {
              return (
                <Entity
                  route={matchProps}
                  header={teamHeader}
                  team={team}
                  entityData={entityTypesWithData[0]}
                />
              );
            }

            return <Members route={matchProps} header={teamHeader} team={team} />;
          }}
        />
      </Switch>
    );
  }
}

export default inject(({ routerStore, teamService, userService }, { route }) => ({
  routerStore,
  currentUser: userService.user,
  teamQuery: teamService.getQuery(route.params.teamId, {
    query: {
      orgId: route.params.orgId,
    },
  }),
}))(observer(Container));



