import React from 'react';
import _ from 'lodash';
import { Button, Segment, Header, Grid, Message, Form, Icon } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';
import { simpleSlugify } from 'common/utils/general';
import MainLayout from 'app/layouts/MainLayout';

class Component extends React.Component {
  componentWillMount() {
    this.updateUiOrg(this.props);
  }

  componentWillReceiveProps(nextProps) {
    this.updateUiOrg(nextProps);
  }

  updateUiOrg = ({ ui, org, updateUi }) => {
    if (!ui.org && !_.isEmpty(org)) {
      updateUi('set', 'org', _.clone(org));
    }
  };

  render() {
    const { ui, updateUi, header, loading, orgService, routerStore } = this.props;

    const org = ui.org || {};
    const original = this.props.org || {};
    const orgChanged = !_.isEqual(org, original);

    return (
      <MainLayout centered header={header} loading={loading}>
        <div className="OrgSettingsPage">
          <Segment.Group>
            <Segment secondary attached="top">
              <Header as="h3">General</Header>
            </Segment>

            <Segment attached="bottom" loading={orgService.isSaving}>
              <Form error={orgService.error} warning={org.slug !== original.slug}>
                <Message content={(orgService.error || '').toString()} error />

                <Form.Input
                  label="Name"
                  name="name"
                  placeholder="Name"
                  value={org.name || ''}
                  onChange={(e, { value }) => updateUi('set', 'org.name', value)}
                />

                <Form.Input
                  label="Slug"
                  name="slug"
                  placeholder="Slug"
                  value={org.slug || ''}
                  onChange={(e, { value }) => updateUi('set', 'org.slug', value)}
                />

                <Message warning size="tiny">
                  <Icon name="exclamation circle" />
                  Note, URL slug will change to "<strong>{simpleSlugify(org.slug)}</strong>"
                </Message>

                <Form.Input
                  label="Summary"
                  name="summary"
                  placeholder="A quick sentence about this organization..."
                  value={org.summary || ''}
                  onChange={(e, { value }) => updateUi('set', 'org.summary', value)}
                />

                <Form.Input
                  label="Logo"
                  name="image"
                  placeholder="Absolute HTTPS url to a logo image"
                  value={org.logoUrl || ''}
                  onChange={(e, { value }) => updateUi('set', 'org.logoUrl', value)}
                />

                <Form.Input
                  label="Primary Color"
                  name="primaryColor"
                  placeholder="#E040FB"
                  value={org.primaryColor || ''}
                  onChange={(e, { value }) => updateUi('set', 'org.primaryColor', value)}
                />

                <Button
                  content="Save"
                  disabled={orgService.isSaving || !orgChanged}
                  onClick={e => {
                    e.preventDefault();

                    if (!orgChanged) {
                      return;
                    }

                    orgService
                      .update(original._id, {
                        name: org.name,
                        slug: org.slug,
                        summary: org.summary,
                        logoUrl: org.logoUrl,
                        primaryColor: org.primaryColor,
                      })
                      .then(result => {
                        updateUi('set', 'org', _.clone(result));

                        routerStore.history.push({
                          pathname: `/${result.slug}/settings`,
                        });
                      });
                  }}
                />

                <Button
                  basic
                  content="Reset"
                  disabled={!orgChanged}
                  onClick={e => {
                    e.preventDefault();
                    updateUi('set', 'org', _.clone(original));
                  }}
                />
              </Form>
            </Segment>
          </Segment.Group>

          <Segment.Group>
            <Segment attached="top" inverted color="red">
              <Header as="h3">Danger Zone</Header>
            </Segment>
            <Segment attached="bottom">
              <Grid divided="vertically">
                <Grid.Row columns={2}>
                  <Grid.Column>
                    <Header as="h4">Delete this Org</Header>
                    <div>
                      Once you delete a Org, there is no going back.
                      Please be certain.
                    </div>
                  </Grid.Column>
                  <Grid.Column textAlign="right">
                    <Button
                      basic
                      color="red"
                      icon="trash"
                      content="Delete this Org"
                      onClick={() => {
                        const r = confirm(
                          `Are you sure you want to remove ${org.name}?\nThis cannot be reverted and all information will be lost forever.`
                        );

                        if (r) {
                          orgService
                            .remove(original._id, {
                              query: {
                                orgId: original._id,
                              },
                            })
                            .then(() => {
                              window.location.href = '/';
                            });
                        }
                      }}
                    />
                  </Grid.Column>
                </Grid.Row>
              </Grid>
            </Segment>
          </Segment.Group>
        </div>
      </MainLayout>
    );
  }
}

export default inject(({ orgService, appStore, routerStore }, { org }) => ({
  ...appStore.injectUi(`Org-Settings-${_.get(org, 'id', 'new-org')}`),
  orgService,
  routerStore,
}))(observer(Component));



