import React from 'react';
import { Button, Popup } from 'semantic-ui-react';
import { observer } from 'mobx-react';

const WidgetHeader = ({ editor = {} }) => {
  const { currentContent = {}, currentContentTab = {} } = editor;
  const { component, headerActions = [] } = currentContent;

  let actionElems = [];
  let targetHeaderActions = [];

  // support the current tab clearing the normal header actions
  if (headerActions && !currentContentTab.exclusiveHeaderActions) {
    targetHeaderActions = targetHeaderActions.concat(headerActions);
  }

  // content tabs can specify their own header actions (in entityStore.contentTabs functions)
  if (currentContentTab.headerActions) {
    targetHeaderActions = targetHeaderActions.concat(currentContentTab.headerActions);
  }

  // build the header actions
  for (const i in targetHeaderActions) {
    const action = targetHeaderActions[i];
    const actionProps = action.props || {};

    if (action.type) {
      switch (action.type) {
        case 'button':
          actionElems.push(
            <div className="f-0 mr-1" key={i}>
              <Button
                size="mini"
                {...actionProps}
                onClick={() => {
                  actionProps.onClick({
                    value: editor.currentParsed,
                    handleUpdate(t, p, v) {
                      editor.updateCurrentParsed(t, p, v);
                    },
                    editor,
                  });
                }}
              />
            </div>
          );
          break;
      }
    } else if (action.factory) {
      // header actions can use a factory component
      const Component = action.factory;
      actionElems.push(
        <div className="f-0 mr-1" key={i}>
          <Component editor={editor} {...actionProps} />
        </div>
      );
    }
  }

  if (!currentContentTab.exclusiveHeaderActions && !_.isEmpty(editor.currentPath)) {
    actionElems.push(
      <div className="f-0 mr-1" key="undo-remove">
        <Button.Group size="mini">
          <Popup
            trigger={
              <Button
                icon="undo"
                onClick={() => {
                  editor.resetCurrentPath();
                }}
              />
            }
          >
            reset {editor.currentHashPath}
          </Popup>

          <Popup
            trigger={
              <Button
                icon="trash"
                onClick={() => {
                  editor.removeCurrentPath();
                }}
              />
            }
          >
            remove {editor.currentHashPath}
          </Popup>
        </Button.Group>
      </div>
    );

    actionElems.push();
  }

  if (!component) {
    actionElems.push(
      <div key="none" className="EntityEditor-widgetHeader-empty f-0">
        There are no visual widgets for this part of your spec, but you can edit it directly below.
      </div>
    );
  }

  if (_.isEmpty(actionElems)) {
    return <div />;
  }

  return (
    <div className="EntityEditor-widgetHeader f">
      <div className="f ai-c f-0">
        {actionElems}
      </div>
    </div>
  );
};

export default observer(WidgetHeader);



