import React from 'react';
import _ from 'lodash';
import { Table, Label, Button } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import MainLayout from 'app/layouts/MainLayout';

import { checkPermissions, mappedRoles } from 'common/utils/acl';

import TableList from 'common/components/TableList';
import InviteEntityModal from 'common/components/InviteEntityModal';

import { Link } from 'common/router';

class Component extends React.Component {
  componentWillMount() {
    this.props.teamEntityQuery.load();
  }

  render() {
    const {
      header,
      route,
      team,
      query,
      entityData,
      teamEntityService,
      teamEntityQuery,
      fileSystemStore,
    } = this.props;

    const teamId = _.get(route, 'params.teamId');
    const orgId = _.get(route, 'params.orgId');
    const teamEntities = _.get(teamEntityQuery, 'result.data', []).slice();
    const canAddEntity = checkPermissions(team.acl, 'create', 'team', 'entity');
    const canUpdateTeam = checkPermissions(team.acl, 'update', 'team');

    return (
      <MainLayout
        centered
        header={header({
          activeTab: entityData.type,
          subTitle: _.get(team, 'summary'),
        })}
        showDownloads
      >
        <TableList
          scope={`${orgId}-${teamId}-${entityData.type}`}
          items={teamEntities}
          loading={teamEntityQuery.isLoading}
          noItemsMessage={() => {
            return `This team does not have read or write permissions to any ${entityData.name} yet.`;
          }}
          actionButton={() => {
            if (canAddEntity) {
              return (
                <InviteEntityModal
                  orgId={orgId}
                  entityData={entityData}
                  entities={teamEntities}
                  error={teamEntityService.error}
                  name={_.get(team, 'name')}
                  handleSave={entityIds => {
                    // TODO list public specs in dropdown
                    return teamEntityService.create(
                      {
                        [entityData.idProp]: entityIds,
                      },
                      {
                        query,
                      }
                    );
                  }}
                />
              );
            }
          }}
          cellFactories={[
            (entity, key) => {
              const activeFile = fileSystemStore.findActiveMapping(entityData.key, entity._id);

              return (
                <Table.Cell key={key} width="14">
                  <Link to={`/${entity.id}`} className="d-b fw-b">
                    <div className="f">
                      <div className="mr-1">{entity.name}</div>
                      {activeFile
                        ? <Label
                            content="Local File"
                            title={`This ${entityData.name} is reading/writing to a file on your computer`}
                            size="mini"
                            basic
                          />
                        : null}
                    </div>
                    <small className="c-muted">{entity.id}</small>
                  </Link>
                </Table.Cell>
              );
            },
            (entity, key) => {
              return (
                <Table.Cell key={key} textAlign="right">
                  <small className="c-muted">{mappedRoles(entity.acl, 'org')}</small>
                </Table.Cell>
              );
            },
            (entity, key) =>
              <Table.Cell key={key} textAlign="right">
                {canUpdateTeam
                  ? <Button
                      basic
                      negative
                      icon="remove"
                      onClick={() => {
                        const r = confirm(
                          `Are you sure you want to remove the "${entity.name}" ${entityData.name}?`
                        );

                        if (r) {
                          teamEntityService
                            .remove(entity._id, { query })
                            .then(() => teamEntityQuery.load());
                        }
                      }}
                    />
                  : null}
              </Table.Cell>,
          ]}
        />
      </MainLayout>
    );
  }
}

export default inject((stores, props) => {
  const { fileSystemStore } = stores;
  const { route, entityData } = props;

  const teamEntityService = stores[entityData.teamServiceKey];

  const query = {
    orgId: _.get(route, 'params.orgId'),
    teamId: _.get(route, 'params.teamId'),
  };

  return {
    fileSystemStore,
    entityData,
    query,

    teamEntityService,
    teamEntityQuery: teamEntityService.findQuery({ query }),
  };
})(observer(Component));



