import React from 'react';
import _ from 'lodash';
import { Button, Message, Loader, Card, Image } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import MainLayout from 'app/layouts/MainLayout';

import { identicon } from 'common/utils/images';
import { mappedRoles } from 'common/utils/acl';

import ErrorMessage from 'common/components/ErrorMessage';
import OrgCreate from 'common/components/OrgCreate';

import { Link } from 'common/router';

class Component extends React.Component {
  componentWillMount() {
    this.props.orgQuery.load();
  }

  render() {
    const {
      routerStore,
      params,
      orgQuery,
      header,
      orgService,
      orgMemberService,
      currentUser,
    } = this.props;

    const orgs = _.get(orgQuery, 'result.data', []).slice();

    if (orgQuery.isLoading) {
      return (
        <MainLayout centered header={header} showDownloads>
          <Loader inline="centered" style={{ marginTop: 50, width: '100%' }} active />
        </MainLayout>
      );
    }

    let contentElem;
    if (_.isEmpty(orgs)) {
      contentElem = (
        <div>
          <Message info>
            <Message.Header>Create an organization using the form below.</Message.Header>
            <Message.Content>
              <div className="mt-1">
                If your company already has a Stoplight organization, ask an admin of that organization to add you as a member.
              </div>

              <div className="mt-05">
                You can also create an organization for personal use.
              </div>
            </Message.Content>
          </Message>

          <OrgCreate {...this.props} />
        </div>
      );
    } else {
      contentElem = (
        <div>
          <div className="mb-2">
            <Link to="/new-org">
              <Button icon="plus" content="New Organization" primary />
            </Link>
          </div>

          <Card.Group>
            {orgs.map((org, i) => {
              return (
                <Card
                  key={i}
                  color={org.color}
                  onClick={e => {
                    if (_.get(e, 'target.tagName', '').toLowerCase() !== 'button') {
                      routerStore.history.push({
                        pathname: `/${org.slug}`,
                      });
                    }
                  }}
                  style={{
                    borderBottom: `2px solid ${org.primaryColor || '#ddd'}`,
                  }}
                >
                  <Card.Content>
                    <Image floated="right" size="mini" src={org.logoUrl || identicon(org.slug)} />
                    <Card.Header>
                      {org.name}
                    </Card.Header>
                    <Card.Meta>
                      {mappedRoles(org.acl)}
                    </Card.Meta>
                    <Card.Description>
                      {org.summary}
                    </Card.Description>
                  </Card.Content>
                  <Card.Content extra>
                    <Button
                      floated="right"
                      size="small"
                      basic
                      compact
                      onClick={() => {
                        const r = confirm(
                          `Are you sure you want to leave the ${org.name} organization?`
                        );

                        if (r) {
                          orgMemberService
                            .remove(currentUser._id, {
                              query: {
                                orgId: org.id,
                              },
                            })
                            .then(() => {
                              orgService.removeLocal(org, params);
                            });
                        }
                      }}
                    >
                      Leave
                    </Button>
                  </Card.Content>
                </Card>
              );
            })}
          </Card.Group>
        </div>
      );
    }

    return (
      <MainLayout centered header={header} showDownloads>
        <ErrorMessage error={orgService.error} onDismiss={orgService.clearError} className="mb-2" />

        <ErrorMessage
          error={orgMemberService.error}
          onDismiss={orgMemberService.clearError}
          className="mb-2"
        />

        {contentElem}
      </MainLayout>
    );
  }
}

export default inject(({ routerStore, orgService, orgMemberService, userService }) => ({
  routerStore,
  currentUser: userService.user,
  orgMemberService,
  orgService,
  orgQuery: orgService.findQuery({
    query: {
      $sort: { name: 1 },
    },
  }),
}))(observer(Component));



