import React from 'react';
import { Popup, Icon } from 'semantic-ui-react';

const OrgCollabPopup = ({ orgName, role, typeName, className }) => {
  if (!role || role === 'no') {
    return null;
  }

  return (
    <Popup
      hoverable
      wide
      size="small"
      className="OrgCollabPopup-popup"
      position="top left"
      trigger={
        <span className={`OrgCollabPopup-dropdownTrigger ${className}`}>
          <Icon name="universal access" />
        </span>
      }
    >
      <Popup.Header>
        All {orgName} org collaborators have
        {' '}
        <span className="fd-u">{role}</span>
        {' '}
        access.
      </Popup.Header>
      <Popup.Content>
        {role === 'guest'
          ? <div>
              Guests can
              {' '}
              <span className="fd-u">view</span>
              {' '}
              the
              {' '}
              {typeName}
              {' '}
              and its connections. They
              {' '}
              <span className="fd-u">cannot view</span>
              {' '}
              its collaborators.
            </div>
          : <div>
              Members can
              {' '}
              <span className="fd-u">update</span>
              {' '}
              the
              {' '}
              {typeName}
              {' '}
              and its connections. They can
              {' '}
              <span className="fd-u">view</span>
              {' '}
              its collaborators.
            </div>}
      </Popup.Content>
    </Popup>
  );
};

export default OrgCollabPopup;



