import React from 'react';
import PropTypes from 'prop-types';
import _ from 'lodash';
import { observer } from 'mobx-react';

import SplitPane from 'common/components/SplitPane';
import LocalFileModeMessage from 'common/components/LocalFileModeMessage';
import ErrorMessage from 'common/components/ErrorMessage';

import SidebarHeader from './sidebarHeader';
import Sidebar from './sidebar';
import WidgetHeader from './widgetHeader';
import Footer from './footer';

class EntityEditor extends React.Component {
  static propTypes = {
    orgId: PropTypes.string,
    entity: PropTypes.object,
    entityType: PropTypes.string,
    entityQuery: PropTypes.object,
    entityService: PropTypes.object,
    editorStore: PropTypes.object,
    routerStore: PropTypes.object,
    fileSystemStore: PropTypes.object,
    primaryActionsFactory: PropTypes.func,
  };

  componentWillMount() {
    this.initEditor(this.props, {
      reload: true,
    });

    this.updateMode(this.props);
  }

  componentWillUnmount() {
    this.props.fileSystemStore.setCurrentId(null);
  }

  componentWillReceiveProps(nextProps) {
    const entityId = _.get(this.props, 'entity.id');
    const nextEntityId = _.get(nextProps, 'entity.id');

    if (entityId !== nextEntityId) {
      this.initEditor(nextProps);
    }

    this.updateMode(nextProps);
  }

  getEditor = () => {
    const { editorStore, entity = {} } = this.props;

    return editorStore.getEditor({ id: entity.id }) || {};
  };

  updateMode = props => {
    const editor = this.getEditor();
    if (_.isEmpty(editor)) {
      return;
    }
  };

  initEditor = (props, { reload, force } = {}) => {
    const editor = props.editorStore.initEditor(
      {
        orgId: _.get(props, 'orgId'),
        entity: props.entity,
        entityType: props.entityType,
        error: props.error,
      },
      {
        force,
      }
    );

    if (editor) {
      editor.connections.loadAll();
    }
  };

  handleReloadEntity = (options, cb) => {
    this.getEditor().load(options, cb);
  };

  handleSaveEntity = (options, cb) => {
    this.getEditor().save(options, cb);
  };

  handleRemoveEntity = (options, cb) => {
    this.getEditor().remove(options, cb);
  };

  handleReloadSource = () => {
    const sourceUrl = _.get(this, 'props.entity.sourceUrl');

    const r = confirm(
      `This will replace your ${this.props
        .entityType} with the one at '${sourceUrl}'. Are you sure?`
    );

    if (r) {
      this.getEditor().reloadFromUrl(sourceUrl);
    }
  };

  render() {
    const {
      orgId,
      entity = {},
      entityType,
      entityService,
      exportUrl,
      fileSystemStore,
      primaryActionsFactory,
      secondaryActionsFactory,
      disableLocal,
    } = this.props;

    const editor = this.getEditor();
    const writeFile = _.get(fileSystemStore, 'currentMapping.isActive');
    const writeFilePath = _.get(fileSystemStore, 'currentMapping.filePath');

    // don't allow user to make changes if in filemode but have not selected a file path yet
    const editorDisabled = writeFile && !writeFilePath;

    return (
      <div className="EntityEditor h-100 w-100 f fd-c">
        <ErrorMessage
          className="EntityEditor-error"
          error={editor.error}
          onDismiss={editor.clearError}
        />

        <SplitPane
          id="paneX"
          className="EntityEditor-wrapper f-1"
          sections={[
            {
              className: 'EntityEditor-sidebar',
              size: 325,
              render: () => {
                return (
                  <div className="f fd-c h-100">
                    <div className="f-0">
                      <SidebarHeader
                        orgId={orgId}
                        editor={editor}
                        entity={entity}
                        entityType={entityType}
                        entityService={entityService}
                        handleRemoveEntity={this.handleRemoveEntity}
                        handleSaveEntity={this.handleSaveEntity}
                        handleReloadEntity={this.handleReloadEntity}
                        handleReloadSource={this.handleReloadSource}
                        editorDisabled={editorDisabled}
                        primaryActionsFactory={primaryActionsFactory}
                        secondaryActionsFactory={secondaryActionsFactory}
                        disableLocal={disableLocal}
                      />
                    </div>

                    <div className="f-1 pos-r h-100">
                      <Sidebar editor={editor} entityType={entityType} />
                    </div>
                  </div>
                );
              },
            },
            {
              className: 'EntityEditor-content',
              fluid: true,
              render: () => {
                if (editorDisabled) {
                  return (
                    <div className="p-2">
                      <LocalFileModeMessage />
                    </div>
                  );
                }

                const Component = _.get(editor, 'currentContentTab.component');

                return (
                  <div className="f fd-c h-100">
                    <div className="f-0">
                      <WidgetHeader orgId={orgId} editor={editor} exportUrl={exportUrl} />
                    </div>

                    <div className="f-1 pos-r h-100">
                      <Component
                        orgId={orgId}
                        entityId={entity.id}
                        editor={editor}
                        exportUrl={exportUrl}
                      />
                    </div>

                    <div className="f-0">
                      <Footer editor={editor} />
                    </div>
                  </div>
                );
              },
            },
          ]}
        />
      </div>
    );
  }
}

export default observer(EntityEditor);



