import React from 'react';
import { Dropdown, Button } from 'semantic-ui-react';

import { getConfigVar } from 'common/utils/config';
import SpecCrudBuilder from 'common/components/SpecCrudBuilder';
import SpecPathBuilder from 'common/components/SpecPathBuilder';

export const primaryActionsFactory = props => {
  const { editor, editorDisabled, isSaving, isLoading, routerStore } = props;

  return (
    <div className="f-0 mr-1">
      {!editorDisabled
        ? <Dropdown
            button
            pointing
            icon="plus"
            className="icon top left mini primary"
            disabled={isSaving || isLoading}
          >
            <Dropdown.Menu>
              <SpecCrudBuilder
                editorId={editor.id}
                triggerFactory={({ handleOpen }) => {
                  return (
                    <Dropdown.Item
                      key="crud"
                      text="CRUD Builder"
                      icon="magic"
                      onClick={handleOpen}
                    />
                  );
                }}
              />

              <SpecPathBuilder
                editorId={editor.id}
                triggerFactory={({ handleOpen }) => {
                  return (
                    <Dropdown.Item
                      key="path"
                      text="Path / Operation"
                      icon="inbox"
                      onClick={handleOpen}
                    />
                  );
                }}
              />

              <Dropdown.Item text="Definition (Model)" icon="database" onClick={editor.addModel} />

              <Dropdown.Item
                text="Security Definition"
                icon="lock"
                onClick={editor.addSecurityDefinition}
              />

              <Dropdown.Item
                text="Shared Parameter"
                icon="filter"
                onClick={editor.addSharedParameter}
              />

              <Dropdown.Item
                text="Shared Response"
                icon="upload"
                onClick={editor.addSharedResponse}
              />

              <Dropdown.Divider />

              <Dropdown.Item
                icon="undo"
                text="Undo Last Change"
                disabled={!editor.canUndo}
                onClick={editor.undo}
              />

              <Dropdown.Item
                icon={{ name: 'undo', flipped: 'horizontally' }}
                text="Redo Last Change"
                disabled={!editor.canRedo}
                onClick={editor.redo}
              />
            </Dropdown.Menu>
          </Dropdown>
        : null}
    </div>
  );
};

export const secondaryActionsFactory = props => {
  return <div className="f ml-05 mr-05" />;
};



