import React from 'react';
import { inject, observer } from 'mobx-react';
import { Menu, Label, Icon } from 'semantic-ui-react';
import _ from 'lodash';

import { hashToPath, pathToHash, buildHash } from 'common/utils/history';
import { Link } from 'common/router';

const PageHeader = props => {
  const { className = '', crumbs, subTitle, left, center, right, tabs = [], hash } = props;

  const crumbElems = [];

  for (const i in crumbs) {
    const crumb = crumbs[i];
    const isLast = !crumbs[parseInt(i) + 1];
    const { elem, children, href, ...linkProps } = crumb;

    if (href) {
      crumbElems.push(
        <Link key={i} className="PageHeader-title-crumb" to={href} {...linkProps}>{children}</Link>
      );
    } else if (elem) {
      crumbElems.push(elem);
    } else {
      crumbElems.push(<div key={i} className="PageHeader-title-crumb">{children}</div>);
    }

    if (!isLast) {
      crumbElems.push(<div key={`${i}-d`} className="PageHeader-title-divider">/</div>);
    }
  }

  const hashParts = hashToPath({ hash });
  if (!_.isEmpty(hashParts)) {
    const usedParts = [];

    crumbElems.push(<div key="h-d" className="PageHeader-title-divider">#/</div>);

    for (const i in hashParts) {
      const isLast = !hashParts[parseInt(i) + 1];
      const part = hashParts[i];
      usedParts.push(part);

      crumbElems.push(
        <Link
          key={`h-${i}`}
          className="PageHeader-title-crumb"
          to={buildHash(pathToHash({ path: usedParts }))}
        >
          {part}
        </Link>
      );

      if (!isLast) {
        crumbElems.push(<div key={`h-${i}-d`} className="PageHeader-title-divider">/</div>);
      }
    }
  }

  const tabElem = tabs.length
    ? <Menu tabular borderless size="small" className="PageHeader-tabs">
        {tabs.map((tab, i) => {
          if (tab.hide) {
            return null;
          }

          return (
            <Menu.Item fitted key={i} name={tab.name} active={tab.active}>
              <Link to={tab.href}>
                {tab.icon ? <Icon name={tab.icon} /> : null}
                <span>{tab.name}</span>
                {!_.isNil(tab.label) ? <Label> {`${tab.label}`}</Label> : null}
              </Link>
            </Menu.Item>
          );
        })}
      </Menu>
    : null;

  return (
    <div className={`PageHeader ${className}`}>
      <div className="PageHeader-inner f ai-c">
        <div className="f-0">
          <div className="PageHeader-title" id="tutorial-breadcrumbs">
            {crumbElems}
          </div>

          {subTitle ? <div className="PageHeader-subTitle d-n">{subTitle}</div> : null}
        </div>

        <div className="PageHeader-left f-0">{left}</div>
        <div className="PageHeader-center f-1 f jc-c">{center}</div>
        <div className="PageHeader-right f-0 h-100 f ai-c">{right}</div>
      </div>

      {tabElem}
    </div>
  );
};

export default inject(({ routerStore }) => ({
  hash: _.get(routerStore, 'location.hash'),
}))(observer(PageHeader));



