import React from 'react';
import { observer } from 'mobx-react';
import { Icon } from 'semantic-ui-react';

import ValidationResults from './validationResults';

const Footer = ({ editor }) => {
  const tabElems = [];
  for (const tab of editor.contentTabs) {
    const isActive = tab.id === editor.currentContentTabId;

    tabElems.push(
      <div
        key={tab.id}
        id={tab.htmlId}
        className={`EntityEditorFooter-tab f-0 ${isActive ? 'is-active' : ''} ${tab.className || ''}`}
        onClick={() => {
          editor.setCurrentContentTab(tab.id);

          if (tab.onClick) {
            tab.onClick();
          }
        }}
      >
        {tab.icon ? <Icon name={tab.icon} className="pr-15" /> : null}
        {tab.name}

        {isActive ? <Icon name="caret up" className="EntityEditorFooter-tabCaret" /> : null}
      </div>
    );
  }

  return (
    <div className="EntityEditorFooter f" id="tutorial-entity-footer">
      <div className="EntityEditorFooter-tabs f f-1">
        {tabElems}
      </div>

      <div className="f-0" id="tutorial-entity-validation">
        <ValidationResults editor={editor} />
      </div>
    </div>
  );
};

export default observer(Footer);



