import React from 'react';
import _ from 'lodash';
import { Icon, Popup } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import { Link } from 'common/router';

const buildMetaElems = meta => {
  const metaElems = [];

  if (meta) {
    for (const m of meta) {
      if (m.icon) {
        metaElems.push(
          <div key={m.id} className={`TreeList-metaItem f-0 ${m.className || 'c-muted'}`}>
            <Icon name={m.icon} />
          </div>
        );
      } else {
        metaElems.push(
          <div key={m.id} className={`TreeList-metaItem f-0 ${m.className || 'c-muted'}`}>
            {m.name}
          </div>
        );
      }
    }
  }

  return metaElems;
};

const buildActionElems = meta => {
  const metaElems = [];

  if (meta) {
    for (const m of meta) {
      let elem;
      if (m.hasOwnProperty('href')) {
        elem = (
          <Link
            key={m.id}
            className={`TreeList-action f-0 f ai-c ${m.className || 'c-muted'} ${m.type
              ? `is-${m.type}`
              : ''}`}
            to={m.href}
          >
            <Icon name={m.icon} className="TreeList-actionIcon" />
          </Link>
        );
      } else {
        elem = (
          <div
            key={m.id}
            className={`TreeList-action f-0 f ai-c ${m.className || 'c-muted'} ${m.type
              ? `is-${m.type}`
              : ''}`}
            onClick={m.onClick}
          >
            <Icon name={m.icon} className="TreeList-actionIcon" />
          </div>
        );
      }

      if (m.tip) {
        elem = <Popup key={m.id} trigger={elem} content={m.tip} size="tiny" />;
      }

      metaElems.push(elem);
    }
  }

  return metaElems;
};

const Item = ({ item, ItemFactory }) => {
  let contentElem;
  if (ItemFactory) {
    contentElem = <ItemFactory index={0} item={item} />;
  } else {
    const metaElems = buildMetaElems(item.meta);
    const actionElems = buildActionElems(item.actions);

    contentElem = (
      <div className="f" title={item.name}>
        {item.icon
          ? <div className="f-0 TreeList-itemIcon">
              <Icon name={item.icon} />
            </div>
          : null}

        <div className="f-0 f ai-c jc-c TreeList-itemToken">
          {item.token
            ? <div className={`TreeList-itemToken-inner f ai-c ${item.token.className || ''}`}>
                {item.token.name}
              </div>
            : null}
        </div>
        <div className="f-1">{item.name}</div>
        <div className="TreeList-meta f-0 f">
          {metaElems}
        </div>

        <div className="TreeList-actions f-0 f">
          {actionElems}
        </div>
      </div>
    );
  }

  const classNames = ['TreeList-item'];
  if (item.isActive) {
    classNames.push('is-active');
  }

  if (item.isMuted) {
    classNames.push('is-muted');
  }

  if (item.hasOwnProperty('href')) {
    return (
      <Link
        to={item.href}
        className={classNames.join(' ')}
        onClick={e => {
          // if we're clicking on an action, don't navigate
          if (_.get(e, 'target.className', '').includes('TreeList-action')) {
            e.preventDefault();
            e.stopPropagation();
            return false;
          }
        }}
      >
        {contentElem}
      </Link>
    );
  }

  return (
    <div className={classNames.join(' ')}>
      {contentElem}
    </div>
  );
};

const Folder = ({ folder, path, depth, handleToggleFolder, ui, ...extra }) => {
  let isOpen = folder.isActive || folder.childActive || folder.openByDefault;

  if (!folder.noToggle) {
    if (_.has(ui, path.concat('toggled'))) {
      isOpen = _.get(ui, path.concat('toggled'));
    }
  }

  const metaElems = buildMetaElems(folder.meta);
  const actionElems = buildActionElems(folder.actions);

  const innerClassNames = ['TreeList-folderInner'];
  let folderInner = (
    <div className="f ai-c">
      {!folder.noToggle
        ? <div
            className="TreeList-folderToggler f-0"
            onClick={e => {
              e.preventDefault();
              handleToggleFolder(path, 'toggled', !isOpen);
              return false;
            }}
          >
            <Icon
              className={isOpen ? `${folder.icon || 'folder open'}` : folder.icon || 'folder'}
            />
          </div>
        : null}

      <div className="TreeList-folderName f-1">
        {folder.name}
      </div>

      <div className="TreeList-meta f-0 f">
        {metaElems}
      </div>

      <div className="TreeList-actions f-0 f">
        {actionElems}
      </div>
    </div>
  );
  if (folder.hasOwnProperty('href')) {
    folderInner = (
      <Link className={innerClassNames.join(' ')} to={folder.href}>
        {folderInner}
      </Link>
    );
  } else {
    folderInner = (
      <div className={innerClassNames.join(' ')}>
        {folderInner}
      </div>
    );
  }

  const classNames = ['TreeList-folder', `depth--${depth}`];
  if (folder.isActive) {
    classNames.push('is-active');
  }
  if (folder.isMuted) {
    classNames.push('is-muted');
  }
  if (folder.childActive) {
    classNames.push('child-active');
  }
  if (folder.noToggle) {
    classNames.push('no-toggle');
  }

  return (
    <div className={classNames.join(' ')}>
      <div
        onClick={e => {
          if (
            !_.includes(_.get(e, 'target.className'), 'TreeList-action') &&
            !folder.hasOwnProperty('href') &&
            !folder.noToggle &&
            !folder.isActive
          ) {
            handleToggleFolder(path, 'toggled', !isOpen);
          }
        }}
      >
        {folderInner}
      </div>

      {isOpen
        ? <Items
            items={folder.children}
            path={path}
            depth={depth + 1}
            handleToggleFolder={handleToggleFolder}
            ui={ui}
            {...extra}
          />
        : null}
    </div>
  );
};

const Items = ({ items, depth = 0, path, ui, handleToggleFolder, ItemFactory }) => {
  let elem;
  if (!_.isEmpty(items)) {
    elem = _.map(items, (item, i) => {
      if (item.children) {
        return (
          <Folder
            key={i}
            folder={item}
            depth={depth}
            path={path.concat(`d-${i}`)}
            handleToggleFolder={handleToggleFolder}
            ui={ui}
            ItemFactory={ItemFactory}
          />
        );
      }

      return <Item key={i} item={item} ItemFactory={ItemFactory} />;
    });
  } else {
    elem = (
      <div className="TreeList-none">
        Nothing to show here.
      </div>
    );
  }

  return (
    <div className={`TreeList-items depth--${depth}`}>
      {elem}
    </div>
  );
};

class Component extends React.Component {
  handleToggleFolder = (p, state, toggled) => {
    this.props.updateUi('set', p.concat(state), toggled);
  };

  render() {
    const { data = {}, ui = {}, ItemFactory, actions = [], htmlId = 'tree-list' } = this.props;

    return (
      <div className="TreeList" id={htmlId}>
        {!_.isEmpty(actions)
          ? <div className="TreeList-topActions">
              {actions}
            </div>
          : null}

        <Items
          path={[]}
          items={data}
          handleToggleFolder={this.handleToggleFolder}
          ItemFactory={ItemFactory}
          ui={ui}
        />
      </div>
    );
  }
}

export default inject(({ appStore }, { id }) => ({
  ...appStore.injectUi(`sortable-tree-list-${id}`),
}))(observer(Component));



