import React from 'react';
import { inject, observer } from 'mobx-react';
import _ from 'lodash';

import PageHeader from 'common/components/PageHeader';
import PageHeaderHistory from 'common/components/PageHeaderHistory';
import { getEntityData, getEntityTypesWithData } from 'common/utils/entities';
import { checkPermissions } from 'common/utils/acl';

import { Switch, Route } from 'common/router';

import Members from './members';
import Teams from './teams';
import Settings from './settings';
import Entity from './entity';

class Container extends React.Component {
  componentWillMount() {
    const { orgId, orgQuery, routerStore } = this.props;

    if (orgId !== 'new-org') {
      orgQuery.load().catch(err => {
        if (err.code === 404) {
          routerStore.history.replace({
            pathname: `/not-found`,
          });
        }
      });
    }
  }

  render() {
    const { orgId, route, orgQuery } = this.props;

    const crumbs = [
      {
        elem: <PageHeaderHistory key="history" orgId={orgId} />,
      },
    ];

    const org = _.get(orgQuery, 'data', {});
    const newCrumbs = crumbs.concat({
      href: route.url,
      children: org.name || 'New',
    });

    const entityTypesWithData = getEntityTypesWithData();

    const header = ({ activeTab, extraCrumbs, subTitle, left, right, center }) => {
      const entityTabs = _.map(entityTypesWithData, entityData => {
        return {
          name: `${entityData.tabName}`,
          active: activeTab === entityData.type,
          href: `${route.url}/${entityData.type}`,
          icon: entityData.icon,
        };
      });

      const tabs = [
        ...entityTabs,
        {
          name: 'Collaboration',
          active: activeTab === 'Collaboration',
          href: `${route.url}/members`,
          icon: 'user',
          hide: !checkPermissions(org.acl, 'read', 'org', 'collaboration'),
        },
        {
          name: 'Settings',
          active: activeTab === 'Settings',
          href: `${route.url}/settings`,
          icon: 'setting',
          hide: !checkPermissions(org.acl, 'update', 'org'),
        },
      ];

      return (
        <PageHeader
          centered
          subTitle={subTitle || org.summary}
          crumbs={extraCrumbs ? newCrumbs.concat(extraCrumbs) : newCrumbs}
          left={left}
          right={right}
          center={center}
          tabs={tabs}
        />
      );
    };

    const entityRoutes = _.map(entityTypesWithData, entityData => {
      return (
        <Route
          key={`${entityData.type}-list`}
          path={`${route.path}/${entityData.type}`}
          render={matchProps => {
            return (
              <Entity
                route={matchProps}
                crumbs={newCrumbs}
                entityData={entityData}
                header={header({ activeTab: entityData.type })}
                org={org}
              />
            );
          }}
        />
      );
    });

    const collaborationTabs = ({ activeTab }) => {
      return [
        {
          name: 'Members',
          href: `${route.url}/members`,
          active: activeTab === 'members',
        },
        {
          name: 'Teams',
          href: `${route.url}/teams`,
          active: activeTab === 'teams',
        },
      ];
    };

    return (
      <Switch>
        <Route
          path={`${route.path}/members`}
          render={matchProps => {
            return (
              <Members
                route={matchProps}
                crumbs={newCrumbs}
                header={header({ activeTab: 'Collaboration' })}
                tabs={collaborationTabs({ activeTab: 'members' })}
                org={org}
              />
            );
          }}
        />

        <Route
          path={`${route.path}/teams`}
          render={matchProps => {
            return (
              <Teams
                route={matchProps}
                crumbs={newCrumbs}
                header={header({ activeTab: 'Collaboration' })}
                tabs={collaborationTabs({ activeTab: 'teams' })}
                org={org}
              />
            );
          }}
        />

        <Route
          path={`${route.path}/settings`}
          render={matchProps => {
            return (
              <Settings route={matchProps} header={header({ activeTab: 'Settings' })} org={org} />
            );
          }}
        />

        {entityRoutes}

        <Route
          path={route.path}
          render={matchProps => {
            return (
              <Entity
                route={matchProps}
                crumbs={newCrumbs}
                entityData={getEntityData('specs')}
                header={header({ activeTab: 'specs' })}
                org={org}
              />
            );
          }}
        />
      </Switch>
    );
  }
}

export default inject(({ orgService, routerStore }, { route }) => {
  const orgId = _.get(route, 'params.orgId');

  return {
    orgId,
    orgQuery: orgService.getQuery(orgId),
    routerStore,
  };
})(observer(Container));



