import React from 'react';
import _ from 'lodash';
import Alert from 'react-s-alert';
import { Form } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import Modal from 'common/components/Modal';
import ErrorMessage from 'common/components/ErrorMessage';

class InviteMembersModal extends React.Component {
  componentWillMount() {
    const { orgId, orgMembersQuery } = this.props;

    if (orgId) {
      orgMembersQuery.load();
    }
  }

  render() {
    const {
      currentMembers = [],
      currentUser,

      ui,
      updateUi,
      orgMembersQuery,

      error,
      clearError,
      handleSave,
    } = this.props;

    const { invites = [], invite } = ui;
    const orgMembers = _.get(orgMembersQuery, 'result.data', []).slice();

    const currentUserEmail = _.get(currentUser, 'emails.0.address');
    if (currentUserEmail) {
      currentMembers.push({ email: currentUserEmail });
    }

    const availableMembers = _.differenceBy(orgMembers, currentMembers, 'email');

    const options = _.uniqBy(
      _.concat(
        _.map(availableMembers, o => ({
          text: _.isEmpty(o.name) ? o.email : `${o.name} - ${o.email}`,
          value: o.email,
        })),
        _.map(invites, inv => ({ text: inv, value: inv }))
      ),
      'value'
    );

    return (
      <Modal
        title="The more the merrier, invite away!"
        size="small"
        triggerButton={{
          primary: true,
          basic: true,
          content: 'Invite Member',
        }}
        cancelButton={{
          content: 'Cancel',
          disabled: ui.loading,
        }}
        confirmButton={{
          positive: true,
          icon: 'add user',
          content: 'Invite',
          loading: ui.loading,
          disabled: ui.loading || (_.isEmpty(invite) && _.isEmpty(invites)),
          onClick(e, elem) {
            const members = invites.length ? invites : [invite];

            if (_.every(members, _.isEmpty)) {
              return;
            }

            updateUi('set', 'loading', true);
            handleSave(members)
              .then(() => {
                elem.handleClose(e);
                updateUi('set', 'invite', '');
                updateUi('set', 'invites', []);
                updateUi('set', 'loading', false);
                Alert.success('Invited new members.');
              })
              .catch(() => {
                updateUi('set', 'loading', false);
              });
          },
        }}
        contentFactory={() =>
          <Form>
            <ErrorMessage className="mb-2" error={error} onDismiss={clearError} />
            <div className="mb-2">
              Add members by email address or username - hit <code>Enter</code> to add them to list.
              When you're ready, click the "Invite" button.
            </div>

            <Form.Dropdown
              label="Invites"
              name="invites"
              fluid
              multiple
              search
              selection
              allowAdditions
              disabled={ui.loading}
              value={invites.slice()}
              options={options}
              onBlur={() => {
                if (!_.isEmpty(invite)) {
                  updateUi('set', 'invite', '');
                  updateUi('set', 'invites', _.uniq(_.concat(invites, invite)));
                }
              }}
              onSearchChange={(e, value) => {
                updateUi('set', 'invite', value);
              }}
              onChange={(e, { value }) => {
                updateUi('set', 'invites', _.uniq(_.map(value, _.trim)));
              }}
            />
          </Form>}
      />
    );
  }
}

export default inject(({ appStore, orgMemberService, userService }, { orgId, id }) => ({
  ...appStore.injectUi(`invite-members-${id}`),
  currentUser: userService.user,
  orgMembersQuery: orgMemberService.findQuery({
    query: {
      orgId,
    },
  }),
}))(observer(InviteMembersModal));



