import React from 'react';
import { Button, Dropdown, Popup, Form, Label, Input, Message, Icon } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';
import _ from 'lodash';

import Modal from 'common/components/Modal';
import ErrorMessage from 'common/components/ErrorMessage';

let ChangeSourceModal = props => {
  const { orgId, id, sourceUrl, ui, updateUi, clearUi, entityService, entityType } = props;

  return (
    <Modal
      title="Set Source URL"
      triggerFactory={({ handleOpen }) => {
        return (
          <Dropdown.Item
            icon="configure"
            text={`${sourceUrl ? 'Change' : 'Set'} Source URL`}
            onClick={handleOpen}
          />
        );
      }}
      cancelButton={{
        negative: true,
        content: 'Cancel',
      }}
      confirmButton={{
        positive: true,
        content: 'Save Source URL',
        onClick(e, elem) {
          entityService
            .save(
              {
                _id: id,
                sourceUrl: ui.url,
              },
              {
                query: {
                  orgId,
                },
              }
            )
            .then(result => {
              clearUi();
              elem.handleClose();
            });
        },
      }}
      contentFactory={() => (
        <div>
          <Message
            content={`Hook up your ${entityType} to a remote URL for easy one
          click reloading. After setting this value, you can find a 'Reload From Source URL' button
          in the extra options dropdown next to the save button.`}
            compact
            size="small"
            info
          />

          <div className="mt-1">
            <Input
              placeholder="http://petstore.swagger.io/v2/swagger.json"
              value={ui.hasOwnProperty('url') ? ui.url : sourceUrl || ''}
              labelPosition="left"
              label={`URL to ${entityType} data`}
              fluid
              onChange={(e, { value }) => {
                updateUi('set', 'url', value);
              }}
            />
          </div>
        </div>
      )}
    />
  );
};
ChangeSourceModal = inject(({ appStore }, { id }) => ({
  ...appStore.injectUi(`${id}-change-source`),
}))(observer(ChangeSourceModal));

const Header = props => {
  const {
    routerStore,
    appStore,
    primaryActionsFactory,
    secondaryActionsFactory,
    disableLocal,

    orgId,
    editor,
    editorDisabled,

    entity,
    entityType,
    entityService,

    ui,
    updateUi,

    handleRemoveEntity,
    handleSaveEntity,
    handleReloadEntity,
    handleReloadSource,

    fileSystemStore,
    userService,
  } = props;

  const { isLoading, isSaving } = entityService;
  const { isLoadingLocal, isSavingLocal, fileModeActive } = editor;

  const writeFile = fileModeActive;
  const writeFilePath = _.get(editor, 'fileMapping.filePath');
  const actionButton = (
    <Button
      icon={writeFile ? 'save' : 'cloud'}
      content={writeFile ? 'Save File' : 'Save'}
      onClick={() => {
        handleSaveEntity();
      }}
      primary={editor.isDirty}
      disabled={isSavingLocal || isSaving || isLoading || isLoadingLocal}
      loading={isSavingLocal || isSaving || isLoading || isLoadingLocal}
    />
  );

  let ctaElem;
  if (userService.loggedIn) {
    let fileButtons = [];

    if (!disableLocal) {
      if (!window.Electron || writeFilePath) {
        fileButtons.push(
          <Dropdown.Item
            key="file-mode"
            onClick={e => {
              e.stopPropagation();

              if (window.Electron) {
                editor.toggleFileMode();
              } else {
                // TODO: show pretty info modal explaining what this feature is and linking to desktop app downloads
                alert('This feature is only available in the desktop app.');
              }
            }}
          >
            <Popup
              size="mini"
              wide
              hoverable
              position="right center"
              trigger={
                <div>
                  <Icon
                    name="circle"
                    className={fileSystemStore.isCurrentActive ? 'c-positive' : 'c-muted'}
                  />
                  <span className="pl-05">
                    File Mode Is {fileSystemStore.isCurrentActive ? 'ON' : 'OFF'}
                  </span>
                </div>
              }
            >
              This toggle changes how your API
              {' '}
              {entityType}
              {' '}
              is loaded and saved. Save to a file on your computer, or to Stoplight's secure servers.
              <br />
              <a
                className="mt-05 d-b"
                href="https://help.stoplight.io/scenarios/api-specs/versioning-specs"
                target="_blank"
                rel="noopener noreferrer"
              >
                <Icon name="book" /> Learn more about local file mode.
              </a>
            </Popup>
          </Dropdown.Item>
        );

        if (window.Electron) {
          fileButtons.push(
            <Dropdown.Item
              key="save"
              icon="save"
              text="Save File"
              onClick={() => {
                editor.save({ local: true });
              }}
            />
          );

          fileButtons.push(
            <Dropdown.Item
              key="reload"
              icon="refresh"
              text="Reload From File"
              onClick={() => {
                handleReloadEntity({ local: true });
              }}
            />
          );

          fileButtons.push(
            <Popup
              key="open"
              content={writeFilePath}
              size="mini"
              wide="very"
              position="right center"
              trigger={
                <Dropdown.Item
                  icon="external"
                  text="Open File"
                  onClick={() => {
                    const opened = window.Electron.shell.openItem(writeFilePath);
                    if (!opened) {
                      alert(`Whoops, we could not open ${writeFilePath}.`);
                    }
                  }}
                />
              }
            />
          );

          fileButtons.push(
            <Dropdown.Item
              key="change"
              icon="file"
              text="Change File Location"
              disabled={isSaving || isLoading}
              onClick={e => {
                window.Electron.dialog.showOpenDialog(
                  {
                    properties: ['openFile'],
                    filters: [{ name: 'JSON Files', extensions: ['json'] }],
                  },
                  filePaths => {
                    if (filePaths) {
                      const r = confirm(
                        'This will overwrite any unsaved changes. Are you sure you want to change files?'
                      );

                      if (r) {
                        editor.updateFilePath(filePaths[0]);
                      }
                    }
                  }
                );
              }}
            />
          );
        }
      } else {
        fileButtons.push(
          <Modal
            key="save-as"
            title={`Where do you want to save the ${entityType} JSON?`}
            triggerFactory={({ handleOpen }) => (
              <Dropdown.Item
                icon="file outline"
                text="Save As"
                onClick={() => {
                  handleOpen();
                }}
              />
            )}
            cancelButton={{
              negative: true,
              content: 'Cancel',
            }}
            confirmButton={{
              positive: true,
              disabled: _.isEmpty(_.get(ui, 'saveFilePath', writeFilePath)) ||
                _.isEmpty(_.get(ui, 'saveFileName', entity.slug)),
              content: 'Save File',
              onClick(e, elem) {
                updateUi('set', 'fileError', null);
                const filePath = `${_.get(ui, 'saveFilePath', writeFilePath)}/${_.get(ui, 'saveFileName', entity.slug)}.json`;

                try {
                  fileSystemStore.writeFile(filePath, entity.data);
                  editor.updateFilePath(filePath);
                  editor.toggleFileMode({ confirmReload: false });
                  elem.handleClose(e);
                } catch (err) {
                  updateUi('set', 'fileError', err);
                }
              },
            }}
            contentFactory={() => (
              <div>
                <ErrorMessage className="mb-2 f-0" error={ui.fileError} />

                <Button
                  icon="folder"
                  content={_.get(ui, 'saveFilePath', 'Choose File Location')}
                  disabled={isSaving || isLoading}
                  loading={isSaving || isLoading}
                  primary={_.isEmpty(_.get(ui, 'saveFilePath'))}
                  onClick={e => {
                    window.Electron.dialog.showOpenDialog(
                      {
                        buttonLabel: 'Choose Folder',
                        properties: ['openDirectory', 'createDirectory'],
                      },
                      filePaths => {
                        if (filePaths) {
                          updateUi('set', 'saveFilePath', filePaths[0]);
                        }
                      }
                    );
                  }}
                />

                <div className="mt-1 w-50">
                  <Form.Input
                    name="name"
                    placeholder=""
                    value={_.get(ui, 'saveFileName', entity.slug || '')}
                    labelPosition="right"
                    onChange={(e, { value }) => {
                      updateUi('set', 'saveFileName', value.replace(/[^\w-_]/g, ''));
                    }}
                  >
                    <Label basic>File Name</Label>
                    <input />
                    <Label>.json</Label>
                  </Form.Input>
                </div>
              </div>
            )}
          />
        );

        fileButtons.push(
          <Dropdown.Item
            key="open-file"
            icon="file"
            text="Open Existing File"
            disabled={isSaving || isLoading}
            onClick={e => {
              window.Electron.dialog.showOpenDialog(
                {
                  properties: ['openFile'],
                  filters: [{ name: 'JSON Files', extensions: ['json'] }],
                },
                filePaths => {
                  if (filePaths) {
                    editor.toggleFileMode({ confirmReload: false });
                    editor.updateFilePath(filePaths[0]);
                  }
                }
              );
            }}
          />
        );
      }
    }

    ctaElem = (
      <div className="f-0 mr-1">
        <Button.Group size="mini">
          {actionButton}

          {!editorDisabled
            ? <Dropdown
                button
                pointing
                icon="caret down"
                className="EntityEditorSidebarHeader-dropdown DarkerIcon icon"
                disabled={isSaving || isLoading}
                id="tutorial-sidebar-save-options"
              >
                <Dropdown.Menu>
                  <Dropdown.Header content="Stoplight Servers" />

                  <Dropdown.Item
                    icon="cloud"
                    text="Save"
                    onClick={() => {
                      handleSaveEntity({ remote: true });
                    }}
                  />

                  <Dropdown.Item
                    icon="refresh"
                    text="Reload"
                    onClick={() => {
                      handleReloadEntity({ remote: true });
                    }}
                  />

                  {!disableLocal ? <Dropdown.Divider /> : null}
                  {!disableLocal ? <Dropdown.Header content="Local Files" /> : null}

                  {fileButtons}

                  <Dropdown.Divider />

                  {entity.sourceUrl
                    ? <Dropdown.Item
                        icon="refresh"
                        text="Reload From Source URL"
                        onClick={handleReloadSource}
                      />
                    : null}

                  <ChangeSourceModal
                    id={entity._id}
                    orgId={orgId}
                    entityService={entityService}
                    sourceUrl={entity.sourceUrl}
                    entityType={entityType}
                  />

                  <Dropdown.Divider />

                  <Dropdown.Item
                    icon="trash"
                    text={`Delete ${_.capitalize(entityType)}`}
                    onClick={handleRemoveEntity}
                  />
                </Dropdown.Menu>
              </Dropdown>
            : null}
        </Button.Group>
      </div>
    );
  }

  let toggleHeaderButton;
  /* toggleHeaderButton = (
    <Popup
      size="mini"
      trigger={
        <div className="mr-1">
          <Button
            icon={`chevron ${appStore.headerHidden ? 'down' : 'up'}`}
            size="mini"
            onClick={appStore.toggleHeader}
            secondary={appStore.headerHidden}
          />
        </div>
      }
    >
      Toggle fullscreen mode.
    </Popup>
  );*/

  return (
    <div className="EntityEditor-sidebarHeader f ai-c" id="tutorial-sidebar-header">
      <div className="f f-1 ai-c">
        {toggleHeaderButton}

        {editor && primaryActionsFactory
          ? primaryActionsFactory({
              editor,
              isSaving,
              isLoading,
              routerStore,
              editorDisabled,
              writeFile,
            })
          : null}

        {ctaElem}

        {editor && secondaryActionsFactory
          ? secondaryActionsFactory({
              editor,
              isSaving,
              isLoading,
              routerStore,
              editorDisabled,
              writeFile,
            })
          : null}
      </div>
    </div>
  );
};

export default inject(({ routerStore, fileSystemStore, userService, appStore }, { entity }) => ({
  appStore,
  routerStore,
  fileSystemStore,
  userService,
  ...appStore.injectUi(`${entity._id}-header`),
}))(observer(Header));



