import React from 'react';
import _ from 'lodash';
import Alert from 'react-s-alert';
import { Form } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import Modal from 'common/components/Modal';
import ErrorMessage from 'common/components/ErrorMessage';

class InviteEntityModal extends React.Component {
  componentWillMount() {
    this.props.orgEntityQuery.load();
  }

  render() {
    const {
      name,
      handleSave,
      error,

      entityData,

      orgEntityQuery,
      entities = [],

      ui,
      updateUi,
    } = this.props;

    const orgEntities = _.get(orgEntityQuery, 'result.data', []).slice();

    const options = _.map(_.differenceBy(orgEntities, entities, '_id'), p => ({
      text: `${p.name} - ${p.id}`,
      value: p._id,
    }));

    const { selected } = ui;

    return (
      <Modal
        title={`Add ${entityData.name}s to ${name}.`}
        triggerButton={{
          primary: true,
          content: `Add ${entityData.name}s`,
        }}
        cancelButton={{
          negative: true,
          content: 'Cancel',
        }}
        confirmButton={{
          positive: true,
          content: `Add ${entityData.name}s`,
          onClick: (e, elem) => {
            if (_.isEmpty(selected)) {
              return;
            }

            updateUi('set', 'loading', true);

            handleSave(selected).then(() => {
              updateUi('set', 'selected', []);
              updateUi('set', 'loading', false);
              Alert.success(`Added new ${entityData.name}.`);
              elem.handleClose(e);
            });
          },
        }}
        contentFactory={() => (
          <Form>
            <ErrorMessage className="mb-2" error={error} />

            <Form.Dropdown
              label={`${entityData.name}s`}
              name={entityData.type}
              fluid
              multiple
              search
              selection
              value={selected ? selected.slice() : []}
              options={options}
              onChange={(e, { value }) => {
                updateUi('set', 'selected', value);
              }}
            />
          </Form>
        )}
      />
    );
  }
}

export default inject((stores, props) => {
  const { appStore } = stores;
  const { orgId, entityData } = props;

  const entityService = stores[entityData.serviceKey];

  return {
    ...appStore.injectUi(`${entityData.type}-modal-${orgId}`),
    orgEntityQuery: entityService.findQuery({
      query: {
        orgId,
      },
    }),
  };
})(observer(InviteEntityModal));



