import React from 'react';
import { Popup, Icon } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';
import _ from 'lodash';

import { Link } from 'common/router';

const SrnHistory = inject('appStore')(
  observer(({ appStore }) => {
    return (
      <div className="PageHeaderHistory-srns">
        <div className="PageHeaderHistory-title c-muted">
          recently viewed
        </div>

        {_.isEmpty(appStore.srnHistory)
          ? <div className="PageHeaderHistory-none c-muted">
              nothing yet
            </div>
          : null}

        {_.compact(
          appStore.srnHistory.map(s => {
            if (typeof s.srn !== 'string') {
              return null;
            }

            return (
              <Link key={s.srn} to={`/${s.srn}`} className="PageHeaderHistory-srn">
                {s.name ? <b className="mr-1">{s.name}</b> : null}
                <span className="c-muted">{s.srn}</span>
              </Link>
            );
          })
        )}
      </div>
    );
  })
);

const PageHeaderHistory = ({ appStore, orgId }) => {
  return (
    <Popup
      trigger={
        <div className="PageHeader-title-crumb no-hover">
          <Icon name="bars" />
        </div>
      }
      position="bottom left"
      size="small"
      wide="very"
      hoverable
    >
      <div className="PageHeaderHistory-actions f">
        <div className="f-1">
          <Link className="PageHeaderHistory-action" to="/">
            <Icon name="home" />
          </Link>

          <Link className="PageHeaderHistory-action" to={`/new?org=${orgId}&type=specs`}>
            + Spec
          </Link>

          <Link className="PageHeaderHistory-action" to={`/new?org=${orgId}&type=scenarios`}>
            + Scenarios
          </Link>

          <Link className="PageHeaderHistory-action" to={`/new?org=${orgId}&type=instances`}>
            + Instance
          </Link>
        </div>

        <div className="f-0">
          {!window.Electron
            ? <a
                href={`stoplight://open${_.get(window, 'location.pathname', '/')}${_.get(window, 'location.search', '')}`}
              >
                Open in Desktop App
              </a>
            : <a href={window.location.href} target="_blank">Open In Browser</a>}
        </div>
      </div>

      <SrnHistory />
    </Popup>
  );
};

export default PageHeaderHistory;



