import React from 'react';
import { Popup, Button, List, Message } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import Modal from 'common/components/Modal';
import { latestVersion } from 'common/utils/collections/migrations';

const ScenarioMigrateButton = ({
  currentEditor,
  trigger,
  popupProps = {},
  writingFile,
  className,
}) => {
  if (
    _.isEmpty(latestVersion) ||
    latestVersion === _.get(currentEditor, 'parsed.scenarioVersion')
  ) {
    if (currentEditor.migrationNotes) {
      return (
        <Modal
          title="Migration Complete!"
          triggerFactory={() => null}
          forceOpen={currentEditor.migrationNotes ? true : false}
          confirmButton={{
            positive: true,
            content: 'Close',
            onClick(e, elem) {
              currentEditor.removeMigrationNotes();
            },
          }}
          contentFactory={() =>
            <div>
              <Message info>
                Scenarios migrated! Take a look at the notes below to understand the changes.
                <br />
                We recommend that you review the changes and run your collection before saving.
              </Message>

              {_.map(currentEditor.migrationNotes, (notes, migrationKey) => {
                return (
                  <div key={migrationKey}>
                    <h3>{migrationKey} Migration</h3>
                    <List bulleted items={notes} />
                  </div>
                );
              })}
            </div>}
        />
      );
    }

    return null;
  }

  let confirmText;
  if (writingFile) {
    confirmText = `You are currently writing to a local file. We recommend that you back this file up (copy it, check it in to git, etc) first.`;
  } else {
    confirmText = `You will have a chance to review the changes before saving the collection.`;
  }

  return (
    <div className={className}>
      <Popup
        trigger={
          trigger
            ? trigger
            : <Button
                className="cur-p c-muted"
                color="orange"
                size="mini"
                content="Migrate"
                onClick={() => {
                  const r = confirm(
                    `This process will make changes to your Scenario Collection.

${confirmText}`
                  );

                  if (r) {
                    currentEditor.migrateCollection();
                  }
                }}
              />
        }
        content={`Click to migrate your Scenario Collection from ${currentEditor.scenarioVersion ||
          '0.0'} to ${latestVersion}`}
        position="bottom left"
        size="small"
      />
    </div>
  );
};

export default inject(({ collectionEditorStore }, { id }) => ({
  currentEditor: collectionEditorStore.getEditor({ id }),
}))(observer(ScenarioMigrateButton));



