import React from 'react';
import _ from 'lodash';

import { Switch, Route } from 'common/router';

import CollaborationPage from 'common/components/CollaborationPage';
import MainLayout from 'app/layouts/MainLayout';

const Component = ({ route, header, entity, entityData, loading }) => {
  const orgId = _.get(route, 'params.orgId');
  const entityId = _.get(route, 'params.entityId');

  const tabs = ({ activeTab }) => {
    return [
      {
        name: 'Members',
        href: `${route.url}/members`,
        active: activeTab === 'members',
      },
      {
        name: 'Teams',
        href: `${route.url}/teams`,
        active: activeTab === 'teams',
      },
    ];
  };

  return (
    <MainLayout
      className={`${entityData.type}-module`}
      tutorial={`${entityData.type}-overview`}
      header={header({ activeTab: 'collaboration' })}
      loading={loading}
      centeredContent
    >
      <Switch>
        <Route
          path={`${route.path}/members`}
          render={matchProps => (
            <CollaborationPage
              orgId={orgId}
              entityId={entityId}
              entity={entity}
              entityData={entityData}
              tabs={tabs({ activeTab: 'members' })}
              collaborationType={'members'}
              collaborationServiceKey={entityData.memberServiceKey}
            />
          )}
        />

        <Route
          path={`${route.path}/teams`}
          render={matchProps => (
            <CollaborationPage
              orgId={orgId}
              entityId={entityId}
              entity={entity}
              entityData={entityData}
              tabs={tabs({ activeTab: 'teams' })}
              collaborationType={'teams'}
              collaborationServiceKey={entityData.entityTeamServiceKey}
            />
          )}
        />
      </Switch>
    </MainLayout>
  );
};

export default Component;



