import React from 'react';
import _ from 'lodash';
import { inject, observer } from 'mobx-react';
import { Table, Button, Form, Label, Menu } from 'semantic-ui-react';

import { Link } from 'common/router';

import TableList from 'common/components/TableList';
import Modal from 'common/components/Modal';
import ErrorMessage from 'common/components/ErrorMessage';
import { checkPermissions } from 'common/utils/acl';

class ConnectionsPage extends React.Component {
  componentWillMount() {
    this.props.connectionQuery.load();
  }

  canEditConnections = () => {
    const { entity, connectionType } = this.props;

    return (
      connectionType === 'outbound' &&
      checkPermissions(entity.acl, 'update', 'entity', 'connections')
    );
  };

  resetSearch = () => {
    const { updateUi } = this.props;

    updateUi('set', 'loading', false);
    updateUi('set', 'ids', []);
    updateUi('set', 'options', []);
  };

  setOptions = data => {
    const { connectionQuery, entity, updateUi } = this.props;
    const connectedEntities = _.get(connectionQuery, 'result.data', []);

    // Move public records to the end
    const sortedData = data.sort((a, b) => a.public - b.public);
    const entities = _.reject(
      sortedData,
      e => !!_.find(connectedEntities, { _id: e._id }) || e._id === entity._id
    );
    const options = _.map(entities, e => ({
      text: `${e.name} - ${e.id}`,
      value: e._id,
      key: e._id,
    }));

    updateUi('set', 'options', options);
  };

  loadUserEntities = query => {
    const { connectedType, userEntityService, orgId } = this.props;

    return userEntityService
      .findQuery({
        query: {
          orgId,
          entityType: connectedType,
          public: true,
          ...query,
        },
      })
      .load();
  };

  handleSearchChange = (e, value) => {
    const { updateUi } = this.props;

    if (!value.length) {
      return this.resetSearch();
    }

    updateUi('set', 'loading', true);

    setTimeout(() => {
      this.loadUserEntities({ name: { $search: value } }).then(({ data }) => {
        this.setOptions(data);
        updateUi('set', 'loading', false);
      });
    }, 300);
  };

  render = () => {
    const {
      query,
      entity,
      connectedType,

      fileSystemStore,
      connectionService,
      connectionQuery,

      ui,
      updateUi,
      error,
      clearError,

      tabs,
      toptabs,
      routerStore,
    } = this.props;

    const items = _.get(connectionQuery, 'result.data', []);
    const { ids = [], options = [] } = ui;
    const canEditConnections = this.canEditConnections();
    const connectedTypeName = _.capitalize(connectedType);

    return (
      <div className="ConnectionsPage">
        {!_.isEmpty(toptabs)
          ? <Menu pointing secondary widths={2} color="blue">
              {toptabs.map((tab, index) => (
                <Menu.Item
                  key={index}
                  active={tab.active}
                  content={tab.name}
                  onClick={() => {
                    routerStore.history.push({
                      pathname: tab.href,
                    });
                  }}
                />
              ))}
            </Menu>
          : null}

        <TableList
          scope={`${entity.id}-connections`}
          items={items}
          tabs={tabs}
          searchProps={['name']}
          loading={connectionQuery.isLoading}
          noItemsMessage={() =>
            `There are no connected ${connectedTypeName} for ${entity.name}. ${canEditConnections ? `To connect ${connectedTypeName}, click the connection button to the top right.` : ''}`}
          actionButton={() => {
            if (canEditConnections) {
              return (
                <Modal
                  title={`Connect ${connectedTypeName}`}
                  size="small"
                  triggerButton={{
                    primary: true,
                    basic: true,
                    icon: 'linkify',
                    content: `Connect ${connectedTypeName}`,
                  }}
                  cancelButton={{
                    content: 'Cancel',
                    disabled: ui.loading,
                  }}
                  confirmButton={{
                    positive: true,
                    icon: 'fork',
                    content: 'Connect',
                    disabled: ui.loading || !ids.length,
                    loading: ui.loading,
                    onClick: (e, elem) => {
                      updateUi('set', 'loading', true);

                      connectionService.create({ id: ids }, { query }).then(() => {
                        connectionQuery.load();

                        elem.handleClose();
                        updateUi('set', 'ids', []);
                        this.resetSearch();
                      });
                    },
                  }}
                  contentFactory={elem => (
                    <Form>
                      <ErrorMessage className="mb-2" error={error} onDismiss={clearError} />
                      <div className="mb-2">
                        Find
                        {' '}
                        {connectedType}
                        {' '}
                        by name and hit
                        {' '}
                        <code>Enter</code>
                        {' '}
                        to add them to list. When you're ready, click the "Connect" button.
                      </div>

                      <Form.Dropdown
                        label={`${connectedTypeName} to connect`}
                        placeholder={`Enter ${_.trimEnd(connectedType, 's')} name`}
                        fluid
                        multiple
                        search
                        selection
                        selectOnBlur
                        noResultsMessage={`No ${connectedType} found`}
                        loading={ui.loading}
                        value={ids}
                        options={options}
                        onChange={(e, { value }) => {
                          updateUi('set', 'ids', _.uniq(value));
                        }}
                        onSearchChange={this.handleSearchChange}
                      />

                      <small>Hit <code>Enter</code> to add them to the list.</small>
                    </Form>
                  )}
                  onOpen={() => {
                    this.resetSearch();

                    this.loadUserEntities({ $limit: 30 }).then(({ data }) => {
                      this.setOptions(data);
                    });
                  }}
                />
              );
            }
          }}
          cellFactories={[
            (connection, key) => {
              const activeFile = fileSystemStore.findActiveMapping(connectedType, connection._id);

              return (
                <Table.Cell key={key} width="10">
                  <div className="fw-b">
                    <Link to={`/${connection.id}`}>
                      <div className="f">
                        <div className="mr-1">{connection.name}</div>
                        {activeFile
                          ? <Label
                              content="Local File"
                              title={`This ${connectedType} is reading/writing to a file on your computer`}
                              size="mini"
                              basic
                            />
                          : null}
                      </div>
                      <small className="c-muted">{connection.id}</small>
                    </Link>
                  </div>
                </Table.Cell>
              );
            },
            (connection, key) => (
              <Table.Cell key={key} textAlign="right">
                {canEditConnections
                  ? <Button
                      basic
                      negative
                      icon="remove"
                      onClick={() => {
                        const r = confirm(
                          `Are you sure you want to remove the connection to "${connection.name}"?`
                        );

                        if (r) {
                          connectionService
                            .remove(connection._id, { query })
                            .then(() => connectionQuery.load());
                        }
                      }}
                    />
                  : null}
              </Table.Cell>
            ),
          ]}
        />
      </div>
    );
  };
}

export default inject((stores, props) => {
  const {
    orgId,
    entityId,
    entityProp,
    connectedType,
    connectionType,
    connectionServiceKey,
  } = props;

  const query = {
    orgId,
    [entityProp]: entityId,
    entityType: connectedType,
    connectionType,
  };

  const connectionService = stores[connectionServiceKey];
  const userEntityService = stores.userEntityService;

  return {
    query,

    ...stores.appStore.injectUi(`connect-entity-${entityId}`),
    fileSystemStore: stores.fileSystemStore,
    routerStore: stores.routerStore,

    connectionService,
    connectionQuery: connectionService.findQuery({
      query,
    }),

    userEntityService,
  };
})(observer(ConnectionsPage));



