import React from 'react';
import { Dropdown } from 'semantic-ui-react';

import ScenarioMigrateButton from 'common/components/ScenarioMigrateButton';

export const primaryActionsFactory = props => {
  const { editor, editorDisabled, isSaving, isLoading } = props;

  return (
    <div className="f f-0">
      {!editorDisabled
        ? <div className="f-0 mr-1">
            <Dropdown
              button
              pointing
              icon="plus"
              className="icon top left mini primary"
              disabled={isSaving || isLoading}
            >
              <Dropdown.Menu>
                <Dropdown.Item
                  icon="plus"
                  text="Add Before Scenario"
                  onClick={() => {
                    editor.addScenario(undefined, { path: ['before'] });
                  }}
                />

                <Dropdown.Item
                  icon="plus"
                  text="Add Scenario"
                  onClick={() => {
                    editor.addScenario(undefined, { path: ['scenarios'] });
                  }}
                />

                <Dropdown.Item
                  icon="plus"
                  text="Add After Scenario"
                  onClick={() => {
                    editor.addScenario(undefined, { path: ['after'] });
                  }}
                />

                <Dropdown.Item
                  icon="plus"
                  text="Add Utility Scenario"
                  onClick={() => {
                    editor.addScenario(undefined, { path: ['utilities'] });
                  }}
                />

                <Dropdown.Divider />

                <Dropdown.Item
                  icon="undo"
                  text="Undo Last Change"
                  disabled={!editor.canUndo}
                  onClick={() => {
                    editor.undo();
                  }}
                />

                <Dropdown.Item
                  icon={{ name: 'undo', flipped: 'horizontally' }}
                  text="Redo Last Change"
                  disabled={!editor.canRedo}
                  onClick={() => {
                    editor.redo();
                  }}
                />
              </Dropdown.Menu>
            </Dropdown>
          </div>
        : null}
    </div>
  );
};

export const secondaryActionsFactory = props => {
  const { editor, writeFile } = props;

  return (
    <div className="f f-0">
      <ScenarioMigrateButton
        id={editor.id}
        writingFile={writeFile ? true : false}
        className="f-0"
      />
    </div>
  );
};



