import React from 'react';
import _ from 'lodash';
import Alert from 'react-s-alert';
import { Button, Form } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import MainLayout from 'app/layouts/MainLayout';
import ErrorMessage from 'common/components/ErrorMessage';

class Component extends React.Component {
  componentWillMount() {
    const { isNew, location, orgMembersQuery } = this.props;

    if (isNew) {
      orgMembersQuery.load();

      const membersQS = _.get(location, 'query.members', []);

      if (!_.isEmpty(membersQS)) {
        const members = _.split(membersQS, ',');
        this.handleFormInput('set', 'members', members);
      }
    }
  }

  handleFormInput = (t, p, v) => {
    this.props.updateUi(t, `form.${p}`, v);
  };

  handleClearFormInput = e => {
    if (e) {
      e.preventDefault();
    }

    this.props.updateUi('set', 'form', {});
  };

  handleSave = e => {
    e.preventDefault();

    const { teamService, ui, isNew, teamId, orgId, query, routerStore } = this.props;

    const newTeam = ui.form || {};

    teamService
      .save(
        {
          ...newTeam,
          _id: isNew ? null : teamId,
        },
        { query }
      )
      .then(result => {
        this.handleClearFormInput();

        if (isNew) {
          routerStore.history.push({
            pathname: `/${orgId}/teams/${result.slug}`,
          });
          Alert.success('Created a new team.');
        } else {
          Alert.success('Team settings updated.');
        }
      });
  };

  handleRemove = e => {
    e.preventDefault();

    const { teamService, teamId, team, orgId, query, routerStore } = this.props;

    const r = confirm(
      `Are you sure you want to remove ${team.name}?\n
This cannot be reverted and all information will be lost forever.`
    );

    if (r) {
      teamService
        .remove(teamId, {
          query,
        })
        .then(() => {
          routerStore.history.push({ pathname: `/${orgId}/teams` });
          Alert.success('Deleted a team.');
        });
    }
  };

  render() {
    const {
      ui,
      team,
      teamId,
      isNew,
      header,
      currentUser,

      teamService,
      orgMembersQuery,
    } = this.props;

    const newTeam = ui.form || {};

    const currentMembers = _.get(newTeam, 'members', []);
    let orgMembers = _.get(orgMembersQuery, 'result.data', []);

    const currentUserEmail = _.get(currentUser, 'emails.0.address');
    if (currentUserEmail) {
      orgMembers = _.reject(orgMembers, { email: currentUserEmail });
    }

    const memberOptions = _.uniqBy(
      _.concat(
        _.map(orgMembers, m => ({
          text: m.email || m.username || m.name,
          value: m.email,
        })),
        _.map(currentMembers, email => ({ text: email, value: email }))
      ),
      'value'
    );

    return (
      <MainLayout
        centered
        header={header({
          activeTab: 'Settings',
          subTitle: _.get(team, 'summary'),
          hideTabs: isNew,
        })}
      >
        <ErrorMessage error={teamService.error} className="mb-2" />

        <Form>
          <Form.Input
            label="Team Name"
            name="name"
            placeholder="Team Name"
            value={newTeam.hasOwnProperty('name') ? newTeam.name : _.get(team, 'name', '')}
            onChange={(e, { value }) => {
              this.handleFormInput('set', 'name', value);
            }}
          />

          <Form.TextArea
            label="Description"
            name="summary"
            rows="3"
            placeholder="What does this team do?"
            value={newTeam.hasOwnProperty('summary') ? newTeam.summary : _.get(team, 'summary', '')}
            onChange={(e, { value }) => {
              this.handleFormInput('set', 'summary', value);
            }}
          />

          {isNew
            ? <Form.Dropdown
                label="Members"
                name="members"
                fluid
                multiple
                search
                selection
                allowAdditions
                value={currentMembers}
                options={memberOptions}
                onChange={(e, { value }) => {
                  this.handleFormInput('set', 'members', value);
                }}
              />
            : null}

          <Button floated="left" content={isNew ? 'Create' : 'Save'} onClick={this.handleSave} />

          {teamId !== 'new'
            ? <Button basic floated="left" content="Reset" onClick={this.handleClearFormInput} />
            : null}

          {teamId !== 'new'
            ? <Button
                icon="remove"
                content="Remove"
                floated="right"
                basic
                negative
                onClick={this.handleRemove}
              />
            : null}
        </Form>
      </MainLayout>
    );
  }
}

export default inject((stores, props) => {
  const { routerStore, teamService, appStore, orgMemberService, userService } = stores;
  const { orgId, teamId } = _.get(props, 'route.params', {});
  const query = {
    orgId,
  };

  return {
    ...appStore.injectUi(`org-${orgId}-teams-${teamId}-settings`),

    orgId,
    teamId,
    query,

    isNew: !teamId || teamId === 'new',
    currentUser: userService.user,

    routerStore,
    teamService,
    orgMembersQuery: orgMemberService.findQuery({
      query,
    }),
  };
})(observer(Component));



