import React from 'react';
import _ from 'lodash';
import Alert from 'react-s-alert';
import { Form } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import Modal from 'common/components/Modal';
import ErrorMessage from 'common/components/ErrorMessage';

class InviteModalComponent extends React.Component {
  componentWillMount() {
    this.props.orgTeamQuery.load();
  }

  render() {
    const {
      name,
      handleSave,
      error,

      orgTeamQuery,
      entityTeams = [],

      ui,
      updateUi,
    } = this.props;

    const orgTeams = _.get(orgTeamQuery, 'result.data', []).slice();

    const options = _.map(_.differenceBy(orgTeams, entityTeams, '_id'), p => ({
      text: `${p.name} - ${p.id}`,
      value: p._id,
    }));

    const { selected } = ui;

    return (
      <Modal
        title={`Invite a team to ${name}`}
        triggerButton={{
          primary: true,
          content: 'Invite a Team',
        }}
        cancelButton={{
          negative: true,
          content: 'Cancel',
        }}
        confirmButton={{
          positive: true,
          content: 'Invite Teams',
          onClick: (e, elem) => {
            if (_.isEmpty(selected)) {
              return;
            }

            updateUi('set', 'loading', true);

            handleSave(selected).then(() => {
              updateUi('set', 'selected', []);
              updateUi('set', 'loading', false);
              Alert.success('Invite new teams.');
              elem.handleClose(e);
            });
          },
        }}
        contentFactory={() => (
          <Form>
            <ErrorMessage className="mb-2" error={error} />

            <Form.Dropdown
              label="Teams"
              name="teams"
              fluid
              multiple
              search
              selection
              value={selected ? selected.slice() : []}
              options={options}
              onChange={(e, { value }) => {
                updateUi('set', 'selected', value);
              }}
            />
          </Form>
        )}
      />
    );
  }
}

export default inject(({ appStore, teamService }, { orgId }) => ({
  ...appStore.injectUi(`team-modal-${orgId}`),
  orgTeamQuery: teamService.findQuery({
    query: {
      orgId,
    },
  }),
}))(observer(InviteModalComponent));



