import React from 'react';
import _ from 'lodash';

import MainLayout from 'app/layouts/MainLayout';
import SettingsPage from 'common/components/SettingsPage';

const Component = ({ route, header, entity, entityData, loading }) => {
  const orgId = _.get(route, 'params.orgId');
  const entityId = _.get(route, 'params.entityId');

  return (
    <MainLayout
      className={`${entityData.type}-module`}
      tutorial={`${entityData.type}-overview`}
      header={header({ activeTab: 'settings' })}
      loading={loading}
      centeredContent
    >
      <SettingsPage orgId={orgId} entityId={entityId} entity={entity} entityData={entityData} />
    </MainLayout>
  );
};

export default Component;



