import React from 'react';
import { Route, Switch } from 'common/router';
import Team from './team';
import List from './list';

const Component = props => {
  const { route, crumbs = [], header, tabs, org } = props;

  const teamsCrumbs = crumbs.concat({
    href: route.url,
    children: 'Teams',
  });

  return (
    <Switch>
      <Route
        path={`${route.path}/:teamId`}
        render={matchProps => {
          return <Team route={matchProps} crumbs={teamsCrumbs} org={org} />;
        }}
      />

      <Route
        path={route.path}
        render={matchProps => (
          <List route={matchProps} crumbs={teamsCrumbs} header={header} tabs={tabs} org={org} />
        )}
      />
    </Switch>
  );
};

export default Component;



