import React from 'react';
import _ from 'lodash';
import { toJS } from 'mobx';
import { inject, observer } from 'mobx-react';
import { Form, Header, Segment, Message } from 'semantic-ui-react';
import Random from 'meteor-random';
import Alert from 'react-s-alert';

import MainLayout from 'app/layouts/MainLayout';

class Component extends React.Component {
  componentWillMount() {
    const user = _.get(this.props, 'userService.user', {});
    this.props.updateUi('set', 'user', toJS(user));
  }

  hasChanged = (newObj, oldObj, props = []) => {
    return !_.isEqual(_.pick(oldObj, props), _.pick(newObj, props));
  };

  render() {
    const { header, ui, updateUi, userService } = this.props;

    const user = ui.user || {};
    const original = toJS(userService.user) || {};

    return (
      <MainLayout centered header={header}>
        <Segment>
          <Header as="h3" dividing>Basic Info</Header>

          {ui.basicInfoError
            ? <Message
                header="Error"
                content={ui.basicInfoError}
                error
                onDismiss={() => updateUi('set', 'basicInfoError', null)}
              />
            : null}

          <Form loading={ui._savingForm1}>
            <Form.Input
              label="Username"
              name="username"
              placeholder="Username"
              value={user.username || ''}
              onChange={(e, { value }) => {
                updateUi('set', 'user.username', _.trim(value));
              }}
            />
            <Form.Input
              label="Name"
              name="profile.name"
              placeholder="Name"
              value={_.get(user, 'profile.name', '')}
              onChange={(e, { value }) => {
                updateUi('set', 'user.profile.name', value);
              }}
            />
            <Form.Input
              label="Email"
              name="emails[0].address"
              placeholder="Email"
              value={_.get(user, 'emails[0].address', '')}
              onChange={(e, { value }) => {
                updateUi('set', 'user.emails[0].address', _.trim(value));
              }}
            />

            <Form.Group>
              <Form.Button
                content="Reset"
                disabled={
                  !this.hasChanged(user, original, [
                    'username',
                    'emails[0].address',
                    'profile.name',
                  ])
                }
                onClick={e => {
                  e.preventDefault();
                  updateUi('set', 'user', original);
                  updateUi('set', 'basicInfoError', null);
                }}
              />
              <Form.Button
                basic
                content="Save"
                disabled={
                  !this.hasChanged(user, original, [
                    'username',
                    'emails[0].address',
                    'profile.name',
                  ])
                }
                onClick={e => {
                  e.preventDefault();
                  updateUi('set', '_savingForm1', true);
                  updateUi('set', 'basicInfoError', null);

                  const oldObj = _.pick(original, [
                    'username',
                    'emails[0].address',
                    'profile.name',
                  ]);
                  const newObj = _.pick(user, ['username', 'emails[0].address', 'profile.name']);
                  const updateInfo = Object.assign({}, oldObj, newObj);

                  if (
                    !/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(
                      _.get(updateInfo, 'emails[0].address')
                    )
                  ) {
                    updateUi('set', '_savingForm1', false);
                    return updateUi('set', 'basicInfoError', 'Please provide a valid email.');
                  }

                  userService
                    .update(original._id, updateInfo)
                    .then(() => {
                      userService.refreshCurrentUser();
                      updateUi('set', '_savingForm1', false);
                    })
                    .catch(err => {
                      updateUi('set', 'basicInfoError', err.message);
                      updateUi('set', '_savingForm1', false);
                    });
                }}
              />
            </Form.Group>
          </Form>
        </Segment>

        <Segment>
          <Header as="h3" dividing>API Secret</Header>
          <Form loading={ui._savingForm2}>
            <Form.Input
              name="apiSecret"
              type={ui.showSecret ? 'input' : 'password'}
              value={user.apiSecret || ''}
              readOnly
            />

            <Form.Group>
              <Form.Button
                content={ui.showSecret ? 'Hide' : 'Show'}
                onClick={e => {
                  e.preventDefault();
                  updateUi('set', 'showSecret', ui.showSecret ? false : true);
                }}
              />
              <Form.Button
                basic
                content="Generate New Secret"
                onClick={e => {
                  e.preventDefault();

                  const r = confirm(
                    'Are you sure you want to generate a new API Secret? This will invalidate your current API Secret.'
                  );

                  if (!r) return;

                  const apiSecret = Random.id();
                  updateUi('set', 'user.apiSecret', apiSecret);

                  userService.update(original._id, { apiSecret }).then(() => {
                    userService.refreshCurrentUser();
                    updateUi('set', '_savingForm2', false);
                  });
                }}
              />
            </Form.Group>
          </Form>
        </Segment>

        <Segment>
          <Header as="h3" dividing>Change Password</Header>

          {ui.passwordError ? <Message content={ui.passwordError} error /> : null}

          <Form loading={ui._savingForm3}>
            <Form.Input
              label="New Password"
              name="password"
              type="password"
              value={ui.password || ''}
              onChange={(e, { value }) => {
                updateUi('set', 'password', _.trim(value));
              }}
            />
            <Form.Input
              label="Confirm New Password"
              name="confirmPassword"
              type="password"
              value={ui.confirmPassword || ''}
              onChange={(e, { value }) => {
                updateUi('set', 'confirmPassword', _.trim(value));
              }}
            />
            <Form.Button
              content="Change Password"
              disabled={_.isEmpty(ui.password) || _.isEmpty(ui.confirmPassword)}
              onClick={e => {
                e.preventDefault();

                if (_.isEmpty(ui.password)) {
                  updateUi('set', 'passwordError', 'Please enter a password.');
                  return;
                }

                if (!_.isEqual(ui.password, ui.confirmPassword)) {
                  updateUi('set', 'passwordError', 'Passwords do not match.');
                  return;
                }

                updateUi('set', 'passwordError', null);
                userService
                  .update(original._id, { password: ui.password })
                  .then(() => {
                    userService.refreshCurrentUser();
                    updateUi('set', '_savingForm2', false);
                    updateUi('set', 'password', '');
                    updateUi('set', 'confirmPassword', '');
                    Alert.success('Password updated successfully.');
                  })
                  .catch(err => {
                    updateUi('set', 'passwordError', err.message);
                    updateUi('set', '_savingForm1', false);
                  });
              }}
            />
          </Form>
        </Segment>
      </MainLayout>
    );
  }
}

export default inject(({ userService, appStore }) => ({
  ...appStore.injectUi('Account-Settings'),
  userService,
}))(observer(Component));



