import React from 'react';
import _ from 'lodash';
import { Table, Image, Button, Message } from 'semantic-ui-react';
import { inject, observer } from 'mobx-react';

import InviteMembersModal from 'common/components/InviteMembersModal';
import TableList from 'common/components/TableList';
import DropdownMenu from 'common/components/DropdownMenu';

import { identicon } from 'common/utils/images';
import { checkPermissions, hasRole, mappedRoles } from 'common/utils/acl';

import MainLayout from 'app/layouts/MainLayout';

class Component extends React.Component {
  componentWillMount() {
    this.props.teamMembersQuery.load();
  }

  getTeamMembers = () => {
    const { teamMembersQuery } = this.props;
    const teamMembers = _.get(teamMembersQuery, 'result.data', []).slice();

    return _.sortBy(teamMembers, member => {
      switch (_.get(member, 'acl.user.roles.0')) {
        case 'owner':
          return 1;
        case 'admin':
          return 2;
        case 'member':
          return 3;
        default:
          return 4;
      }
    });
  };

  handleInviteMembers = emails => {
    const { teamMemberService, query } = this.props;

    return teamMemberService.create(
      {
        userId: emails,
      },
      { query }
    );
  };

  handleChangeRole = (id, role) => {
    const { teamMemberService, query } = this.props;

    return teamMemberService.update(
      id,
      {
        role,
      },
      { query }
    );
  };

  handleLeaveTeam = () => {
    const r = confirm('Are you sure you want to leave this team?');

    if (r) {
      const { currentUser, query, teamMemberService, routerStore, orgId } = this.props;
      teamMemberService
        .remove(currentUser._id, {
          query,
        })
        .then(() => {
          routerStore.history.push({ pathname: `/${orgId}/teams` });
        });
    }
  };

  render() {
    const {
      query,
      orgId,
      teamId,
      team = {},
      teamMembersQuery,
      teamMemberService,
      header,
      currentUser,
    } = this.props;

    const teamMembers = this.getTeamMembers();
    const owner = _.find(teamMembers, m => {
      return hasRole(m.acl, 'user', 'owner');
    });
    const isTeamOwner = _.get(owner, '_id') === currentUser._id;
    const canAddMembers = checkPermissions(team.acl, 'update', 'team', 'collaboration');

    let listElem;

    if (!teamMembersQuery.isLoading && _.isEmpty(teamMembers)) {
      let message = 'There are no Members in this Team.';

      listElem = <Message content={message} info />;
    }

    if (!listElem) {
      listElem = (
        <TableList
          scope={`${orgId}-${teamId}-members`}
          items={teamMembers}
          loading={teamMembersQuery.isLoading}
          actionButton={() => {
            if (canAddMembers) {
              return (
                <InviteMembersModal
                  id={teamId}
                  orgId={orgId}
                  entity="team"
                  currentMembers={teamMembers}
                  name={team.name}
                  error={teamMemberService.error}
                  handleSave={this.handleInviteMembers}
                />
              );
            }
          }}
          cellFactories={[
            (member, key) => (
              <Table.Cell key={key} width="14">
                <div className="f">
                  <div className="f ai-c">
                    <Image src={identicon(member.email)} size="mini" inline />
                    <div className="ml-1">
                      <div>{member.name}</div>
                      <div>{member.username} - {member.email}</div>
                    </div>
                  </div>
                </div>
              </Table.Cell>
            ),

            (member, key) => {
              let roleElem;
              const role = _.get(member, 'acl.user.roles[0]');
              const roles = mappedRoles(member.acl);

              if (canAddMembers) {
                let options = [];

                const hasOwnerRole = hasRole(member.acl, 'user', 'owner');

                const disabled = currentUser._id === member._id || hasOwnerRole;

                if (isTeamOwner || hasOwnerRole) {
                  options.push({
                    title: 'Owner',
                    value: 'owner',
                    disabled: disabled,
                    onClick: value => {
                      const r = confirm(
                        `Are you sure you want to transfer ownership of the ${team.name} to ${member.name || member.username}?`
                      );

                      if (r) {
                        this.handleChangeRole(member._id, value).then(() => {
                          teamMembersQuery.load();
                        });
                      }
                    },
                    description: (
                      <div>
                        <div>
                          Owners can
                          {' '}
                          <span className="fd-u">update</span>
                          {' '}
                          the
                          {' '}
                          team, its connections, and its members.
                        </div>
                        <div>
                          There can only be
                          {' '}
                          <span className="fd-u">one</span>
                          {' '}
                          owner of a team.
                        </div>
                      </div>
                    ),
                  });
                }

                options = options.concat([
                  {
                    title: 'Admin',
                    value: 'admin',
                    disabled: disabled,
                    description: (
                      <span>
                        Admins can
                        {' '}
                        <span className="fd-u">update</span>
                        {' '}
                        the team, its entities, and its members.
                      </span>
                    ),
                  },
                  {
                    title: 'Member',
                    value: 'member',
                    disabled: disabled,
                    description: (
                      <span>
                        Members can
                        {' '}
                        <span className="fd-u">add</span>
                        {' '}
                        entities and
                        {' '}
                        <span className="fd-u">view</span>
                        {' '}
                        other members.
                      </span>
                    ),
                  },
                  {
                    title: 'Guest',
                    value: 'guest',
                    disabled: disabled,
                    description: (
                      <span>
                        Guests can <span className="fd-u">view</span> entities.
                      </span>
                    ),
                  },
                ]);

                if (hasOwnerRole && isTeamOwner) {
                  options.push({
                    divider: true,
                  });

                  const description = `Before leaving a team, you must give another member the owner role.`;

                  options.push({
                    title: `Leave team`,
                    description,
                    icon: 'remove',
                    onClick: () => {
                      alert(description);
                    },
                  });
                } else if (!hasOwnerRole) {
                  options.push({
                    divider: true,
                  });

                  let confirmText = 'Are you sure you want to';

                  if (member._id === currentUser._id) {
                    confirmText = `${confirmText} leave this team?`;
                  } else {
                    confirmText = `${confirmText} remove ${member.name || member.username} from this team?`;
                  }

                  options.push({
                    title: member._id === currentUser._id ? 'Leave team' : `Remove member`,
                    description: confirmText,
                    icon: 'remove',
                    onClick: () => {
                      if (member._id === currentUser._id) {
                        this.handleLeaveTeam();
                      } else {
                        const r = confirm(confirmText);

                        if (r) {
                          teamMemberService.remove(member._id, { query });
                        }
                      }
                    },
                  });
                }

                roleElem = (
                  <DropdownMenu
                    className="icon w-100"
                    floatLeft={true}
                    value={role}
                    onChange={value => this.handleChangeRole(member._id, value)}
                    options={options}
                    dropdownOptions={{
                      button: true,
                    }}
                  />
                );
              } else {
                roleElem = (
                  <div className="f ai-c">
                    <small className="c-muted mr-1">{roles}</small>
                    {member._id === currentUser._id
                      ? <Button
                          basic
                          negative
                          title="Leave Team"
                          icon="sign out"
                          onClick={this.handleLeaveTeam}
                        />
                      : <Button basic className="v-h" />}
                  </div>
                );
              }

              return <Table.Cell key={key} textAlign="center">{roleElem}</Table.Cell>;
            },
          ]}
        />
      );
    }

    return (
      <MainLayout
        centered
        header={header({
          activeTab: 'Members',
          subTitle: _.get(team, 'summary'),
        })}
        showDownloads
      >
        {listElem}
      </MainLayout>
    );
  }
}

export default inject((stores, props) => {
  const { teamMemberService, userService, routerStore } = stores;
  const orgId = _.get(props, 'route.params.orgId');
  const teamId = _.get(props, 'route.params.teamId');

  const query = { orgId, teamId };

  return {
    query,
    orgId,
    teamId,
    routerStore,
    currentUser: userService.user,

    teamMemberService,
    teamMembersQuery: teamMemberService.findQuery({
      query: {
        ...query,
        $sort: { name: 1 },
      },
    }),
  };
})(observer(Component));



