import React from 'react';
import { Message, Icon } from 'semantic-ui-react';
import DropdownMenu from 'common/components/DropdownMenu';

const OrgCollabSwitcher = ({ orgName, role, onChange, typeName, canEdit = true, basic }) => {
  const contentElem = (
    <div>
      <span className="pr-05">
        <Icon name="exclamation circle" />
      </span>

      By default, all members in the <strong>{orgName}</strong> org will be given

      {canEdit
        ? <DropdownMenu
            className="OrgCollabSwitcher-permissions"
            value={role || 'no'}
            onChange={onChange}
            options={[
              {
                title: 'no',
                value: 'no',
                description: (
                  <span>
                    This
                    {' '}
                    {typeName}
                    {' '}
                    will be
                    {' '}
                    <span className="fd-u">hidden</span>
                    {' '}
                    from anyone not listed as a collaborator.
                  </span>
                ),
              },
              {
                title: 'guest',
                value: 'guest',
                description: (
                  <span>
                    Guests can
                    {' '}
                    <span className="fd-u">view</span>
                    {' '}
                    the
                    {' '}
                    {typeName}
                    {' '}
                    and its connections. They
                    {' '}
                    <span className="fd-u">cannot view</span>
                    {' '}
                    its collaborators.
                  </span>
                ),
              },
              {
                title: 'member',
                value: 'member',
                description: (
                  <span>
                    Members can
                    {' '}
                    <span className="fd-u">update</span>
                    {' '}
                    the
                    {' '}
                    {typeName}
                    {' '}
                    and its connections. They can
                    {' '}
                    <span className="fd-u">view</span>
                    {' '}
                    its collaborators.
                  </span>
                ),
              },
            ]}
          />
        : <strong> {role || 'no'} </strong>}

      access to this {typeName}.
    </div>
  );

  if (basic) {
    return (
      <div className="OrgCollabSwitcher is-basic">
        {contentElem}
      </div>
    );
  }

  return (
    <Message className="OrgCollabSwitcher">
      {contentElem}
    </Message>
  );
};

export default OrgCollabSwitcher;



